//This handles retrieving data and is used by controllers. 3 options (server, factory, provider) with
//each doing the same thing just structuring the functions/data differently.
app.service('viewService', ['$q', '$http', 'dataService', function ($q, $http, dataService) {
    var propertyKeyPrefix = 'views4';
    var propertyKey = function(loggedInUser) {
        return propertyKeyPrefix + loggedInUser.accountId;
    };

    // DO data in services: http://blog.nebithi.com/angularjs-dos-and-donts/
    this.data = {
        allViews: [],
        currentView : {
        }
    };

    this.updateCurrentView = function(viewId) {
        if (this.data.currentView.viewId != viewId) {
            var view = this.findById(viewId, this.data.allViews);
            if (view) {
                angular.copy(view, this.data.currentView);
                //this.data.currentView._origColumns = view.columns;
            }
        }
    };

    this.getViews = function(viewId, loggedInUser) {
        var deferred = $q.defer();
        var viewService = this;
        if (this.data.allViews.length == 0) {
            dataService.getProperty(propertyKeyPrefix, loggedInUser, []).then(function(data) {
                [].push.apply(viewService.data.allViews, data);
                viewService.updateCurrentView(viewId);
                deferred.resolve(viewService.data.allViews);
            });
        } else {
            viewService.updateCurrentView(viewId);
            deferred.resolve(viewService.data.allViews);
        }
        return deferred.promise;
    };

    this.addView = function(view, loggedInUser) {
        var deferred = $q.defer();
        var viewService = this;
        dataService.getProperty(propertyKeyPrefix, loggedInUser, []).then(function(views) {
            var last = views.length > 0 ? views[views.length - 1] : {viewId: 0};
            var newView = angular.copy(view);
            newView.viewId = last.viewId + 1;
            views.push(newView);
            dataService.setProperty(propertyKey(loggedInUser), views).then(function() {
                viewService.data.allViews.push(newView);
                angular.copy(newView, viewService.data.currentView);
                //viewService.data.currentView._origColumns = newView.columns;
                deferred.resolve(viewService.data.currentView);
            });
        });
        return deferred.promise;
    };

    this.updateView = function(view, loggedInUser) {
        var deferred = $q.defer();
        var viewService = this;
        dataService.getProperty(propertyKeyPrefix, loggedInUser, []).then(function(views) {
            var i = viewService.indexById(view.viewId, views);
            var updatedView = angular.copy(view);
            views[i] = updatedView;
            dataService.setProperty(propertyKey(loggedInUser), views).then(function() {
                // update all views
                var index = viewService.indexById(view.viewId, viewService.data.allViews);
                viewService.data.allViews.splice(index, 1, updatedView); // apply changes to view
                // update view
                if (!angular.equals(view, viewService.data.currentView)) {
                    angular.copy(view, viewService.data.currentView);
                }
                deferred.resolve(viewService.data.currentView);
            });
        });
        return deferred.promise;
    };

    this.removeView = function(view, loggedInUser) {
        var deferred = $q.defer();
        var viewService = this;
        var viewId = view.viewId;
        dataService.getProperty(propertyKeyPrefix, loggedInUser, []).then(function(views) {
            var i = viewService.indexById(viewId, views);
            views.splice(i, 1);
            dataService.setProperty(propertyKey(loggedInUser), views).then(function() {
                var index = viewService.indexById(viewId, viewService.data.allViews);
                viewService.data.allViews.splice(index, 1);
                angular.copy({}, viewService.data.currentView);
                deferred.resolve(viewService.data.allViews);
            });
        });
        return deferred.promise;
    };

    this.indexById = function(id, array) {
        return TimesheetUtils.indexByProperty('viewId', id, array);
    };

    this.findById = function(id, array) {
      return TimesheetUtils.findByProperty('viewId', id, array);
    };

    this.resetState = function() {
        this.data.allViews.splice(0, this.data.allViews.length); // reset
    };
}]);
