if (typeof module === 'object' && module.exports) {
    var CsvView = require('./CsvView');
    var ExcelView = require('./ExcelView');
}

function HtmlView(allIssues) {
    this.allIssues = allIssues;
}

var htmlUserFieldFormatter = function htmlUserFieldFormatter(field, options) {
    var hostBaseUrl = options.hostBaseUrl || '';
    var name = field.displayName || field.name || field.accountId;
    return "<a href='" + hostBaseUrl + "/people/" + field.accountId + "' title='" + field.emailAddress + "'>" + name + "</a>";
};

var htmlKeyFieldFormatter = function htmlKeyFieldFormatter(field, options) {
    var hostBaseUrl = options.hostBaseUrl || '';
    var name = field.hasOwnProperty('fields') && field.fields.hasOwnProperty('summary') ? field.fields.summary : '';
    name = name.replace(/'/, '&#39;');
    return "<a href='" + hostBaseUrl + "/browse/" + field.key + "' title='" + name + "'>" + field.key + (name ? " " + name : "") + "</a>";
};

var getFormattedTime = function (value, config) {
    return value != 0 ? DrawHelper.getFormattedTime(value, config || {}) : "";
};

HtmlView.prototype.fieldValue = CsvView.fieldValueFn(htmlUserFieldFormatter, htmlKeyFieldFormatter, function htmlHoursFormatter(value, options) {
    return getFormattedTime(value, options.jiraConfig);
});

HtmlView.prototype.generate = function(options) {
    var hostBaseUrl = options.hostBaseUrl || '';
    var maxFractionDigits = options.jiraConfig.maxFractionDigits;
    var prepareLink = function(url, text) {
        return "<a href='" + hostBaseUrl + url + "'>" + text + "</a>";
    };
    var getColumnTitle = function (columnKey) {
        if (columnKey.user) {
            var name = columnKey.user.displayName || columnKey.user.name || columnKey.user.accountId;
            return "<a href='" + hostBaseUrl + "/people/" + columnKey.user.accountId + "' title='" + columnKey.user.emailAddress + "'>" + name + "</a>";
        } else if (columnKey.date) {
            return moment(columnKey.date).format('DD/MMM');
        } else if (columnKey.weekNumber) {
            return columnKey.weekNumber;
        } else if (columnKey.monthKey) {
            return moment(columnKey.keyValue).format('MMM');
        } else {
            if (['TimeTracking', 'TimeEntry', 'IssueTime', 'Timespent', 'TimeBalance'].indexOf(options.pivotTableType) >= 0) {
                return options.translations[columnKey.keyValue];
            } else {
                return columnKey.keyValue;
            }
        }
    };
    var pivotTable;
    if (options.pivotTable) {
        pivotTable = options.pivotTable;
        if (options.pivotTableType != 'TimeEntry') {
            options.groupByField = options._groupByField;
            options.categorizeByField = options._categorizeByField;
        }
    } else {
        pivotTable = PivotTableFactory.createPivotTable(options);
        for (var i in this.allIssues) {
            pivotTable.add(this.allIssues[i]);
        }
    }
    pivotTable.showOnce = [];
    var columns = pivotTable.sortedColumns();
    var rows = pivotTable.sortedRows();
    var excel = '<style>\n';
    excel += '<!--br\n';
    excel += '    {mso-data-placement:same-cell;}\n';
    excel += '<!--table\n';
    var separators = (1234.5).toLocaleString().match(/\d+(\D)?\d+(\D)\d+/);
    excel += '    {mso-displayed-decimal-separator:"\\' + separators[2] + '";';
    excel += (separators[1] ? '\n    mso-displayed-thousand-separator:"\\' + separators[1] + '";' : '') + '}\n';
    excel += '-->\n';
    excel += '</style>\n';
    excel += "<table>\n";
    excel += "  <tr class='rowFooter'>\n";
    if (options.categorizeByField && options.groupByField) {
        excel += "    <td class='nav border total'>" + (options.categorizeByFieldOption ? options.categorizeByFieldOption.label : options.categorizeByField) + "</td>\n";
    }
    if (options.groupByField) {
        excel += "    <td class='nav border total'>" + (options.groupByFieldOption ? options.groupByFieldOption.label : options.groupByField) + "</td>\n";
    }
    var hideCommentColumn = options.compressed || options.pivotTableType == 'TimeTracking';
    if (!options.groupByField || (options.groupByField && !options.compressed) || options.groupByField == 'issue') {
        excel += "    <td class='nav border total'>" + options.translations["Issue Type"] + "</td>\n";
        excel += "    <td class='nav border total'>" + options.translations["Parent"] + "</td>\n";
        excel += "    <td class='nav border total'>" + options.translations["Key"] + "</td>\n";
        excel += "    <td class='nav border total'>" + options.translations["Summary"] + "</td>\n";
        excel += "    <td class='nav border total'>" + options.translations["Priority"] + "</td>\n";
    }
    var option;
    for (var i in pivotTable.moreFields) {
        var moreField = pivotTable.moreFields[i];
        option = options.moreFieldsOptions.findOption(moreField);
        excel += "    <td class='nav border total'>" + (option ? option.label : moreField) + "</td>\n";
    }
    if (!hideCommentColumn) {
        excel += "    <td class='nav border total'>" + options.translations["Work Description"] + "</td>\n";
    }
    for (var i in columns) {
        excel += "    <td class='nav border total'>" + getColumnTitle(columns[i].columnKey) + "</td>\n";
    }
    var hideTotalColumn = ["TimeTracking", "TimeBalance", "Timespent", "TimeEntry"].indexOf(options.pivotTableType) >=0;
    if (!hideTotalColumn) {
        excel += "    <td class='nav border total'>" + options.translations["Total"] + "</td>\n";
    }
    excel += "  </tr>\n";
    for (var i in rows) {
        excel += "  <tr>\n";
        var row = rows[i];
        var issue = row.rowKey._issue || row.rowKey.issue || /* groupBy: issue */ row.rowKey.field;
        if (options.categorizeByField && options.groupByField) {
            var categorizeByFieldValue;
            if (options.categorizeByField == 'workeduser') {
                categorizeByFieldValue = htmlUserFieldFormatter(row.rowKey.project, options);
            } else if (['issue', 'project'].indexOf(options.categorizeByField) >= 0) {
                categorizeByFieldValue = htmlKeyFieldFormatter(row.rowKey.project, options);
            } else if (['workeddate', 'workcreated'].indexOf(options.categorizeByField) >= 0) {
                categorizeByFieldValue = moment(row.rowKey.project).format(TimesheetUtils.dateTimeFormat);
            } else if (['commentfirstword', 'worklabel', 'workedusergroup'].indexOf(options.categorizeByField) >= 0) {
                categorizeByFieldValue = row.rowKey._project;
            } else if (['workedweek'].indexOf(options.categorizeByField) >= 0) {
                categorizeByFieldValue = options.translations["Week"] + " " + row.rowKey.field.weekNumber;
            } else {
                categorizeByFieldValue = this.fieldValue(issue, options.categorizeByField, options);
            }
            excel += "    <td class='nav border'>" + categorizeByFieldValue + "</td>\n";
        }
        if (options.groupByField) {
            var groupByFieldValue;
            if (options.groupByField == 'workeduser') {
                groupByFieldValue = htmlUserFieldFormatter(row.rowKey.field, options);
            } else if (['issue', 'parent', 'project'].indexOf(options.groupByField) >= 0) {
                groupByFieldValue = htmlKeyFieldFormatter(row.rowKey.field, options);
            } else if (['workeddate', 'workcreated'].indexOf(options.groupByField) >= 0) {
                groupByFieldValue =  moment(row.rowKey.field).format(TimesheetUtils.dateTimeFormat);
            } else if (['commentfirstword', 'worklabel', 'workedusergroup'].indexOf(options.groupByField) >= 0) {
                groupByFieldValue = row.rowKey._field;
            } else if (['workedweek'].indexOf(options.groupByField) >= 0) {
                groupByFieldValue = options.translations["Week"] + " " + row.rowKey.field.weekNumber;
            } else {
                groupByFieldValue = this.fieldValue(issue, options.groupByField, options);
            }
            excel += "    <td class='nav border'>" + groupByFieldValue + " (" + row.num + " " + (row.num != 1 ? 'issues' : 'issue') + ")</td>\n";
        }
        if (row.rowKey.issue || options.groupByField == 'issue') {
            excel += "    <td class='nav border'>" + issue.fields.issuetype.name + "</td>\n";
            excel += "    <td class='nav border'>" + (issue.fields.parent ? prepareLink('/browse/' + issue.fields.parent.key, issue.fields.parent.key ) : "") + "</td>\n";
            excel += "    <td class='nav border'>" + prepareLink('/browse/' + issue.key, issue.key) + "</td>\n";
            excel += "    <td class='nav border'>" + prepareLink('/browse/' + issue.key, issue.fields.summary) + "</td>\n";
            excel += "    <td class='nav border'>" + this.fieldValue(issue, 'priority', options) + "</td>\n";
        } else if (!options.compressed) { // groupByField
            excel += TimesheetUtils.repeat("    <td class='nav border'>&nbsp;</td>\n", 5);
        }
        for (var i in row.moreFields) {
            // FIXME: formatDecimal for time fields
            excel += "    <td class='nav border'>" + this.fieldValue(issue, row.moreFields[i], options) + "</td>\n";
        }
        if (!hideCommentColumn) {
            excel += "    <td class='nav border'>&nbsp</td>\n";
        }
        for (var i in columns) {
            var columnName = columns[i].columnKey.keyValue;
            var entry = row.columns[columnName];
            if (!entry) {
                excel += "    <td class='nav border'></td>\n";
            } else if (columnName == '6progress') {
                var value = entry.value;
                if (value.timespent + value.estimate == 0) {
                    excel += "    <td class='nav border'></td>\n";
                } else {
                    var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent));
                    excel += "    <td class='nav border'>" + DrawHelper.formatDecimal(percents, maxFractionDigits) + "%</td>\n";
                }
            } else if (columnName == '5originalestimateremaining') {
                excel += "    <td class='nav border'>" + getFormattedTime(entry.value.value, options.jiraConfig) + "</td>\n";
            } else if (["1started", "3action"].indexOf(columnName) >= 0) {
                excel += "    <td class='nav border'></td>\n";
            } else {
                excel += "    <td class='nav border'>" + getFormattedTime(entry.sum, options.jiraConfig) +
                    (options.showDetailsCount ? " " + row.columns[columnName].detailsNum : "") + "</td>\n";
            }
        }
        if (!hideTotalColumn) {
            excel += "    <td class='nav border total'>" + getFormattedTime(row.sum, options.jiraConfig) +
                (options.showDetailsCount ? " " + row.detailsNum : "") + "</td>\n";
        }
        excel += "  </tr>\n";
        if (!options.compressed && (options.pivotTableType != 'TimeTracking' || options.groupByField)) {
            for (var j in row.data) {
                excel += "  <tr>\n";
                if (options.categorizeByField  && options.groupByField) {
                    excel += "    <td class='nav border'>&nbsp;</td>\n";
                }
                if (options.groupByField) {
                    excel += "    <td class='nav border'>&nbsp;</td>\n";
                }
                var detailedRow = row.data[j];
                var detailedIssue = detailedRow.rowKey._issue;
                if (detailedIssue) {
                    excel += "    <td class='nav border'>" + detailedIssue.fields.issuetype.name + "</td>\n";
                    excel += "    <td class='nav border'>" + (detailedIssue.fields.parent ? prepareLink('/browse/' + detailedIssue.fields.parent.key, detailedIssue.fields.parent.key ) : "") + "</td>\n";
                    excel += "    <td class='nav border'>" + prepareLink('/browse/' + detailedIssue.key, detailedIssue.key) + "</td>\n";
                    excel += "    <td class='nav border'>" + prepareLink('/browse/' + detailedIssue.key, detailedIssue.fields.summary) + "</td>\n";
                    excel += "    <td class='nav border'>" + this.fieldValue(detailedIssue, 'priority', options) + "</td>\n";
                } else {
                    excel += TimesheetUtils.repeat("    <td class='nav border'>&nbsp;</td>\n", 5);
                }
                for (var i in row.moreFields) {
                    if (row.data[j].rowKey._issue) {
                        var key = row.data[j].rowKey._issue.key + '/' + i;
                        if (pivotTable.showOnce.indexOf(key) < 0) {
                            var value = row.data[j].rowKey._issue.fields[row.moreFields[i]];
                            if (typeof value == 'number') {
                                pivotTable.showOnce.push(key);
                            }
                            // FIXME: formatDecimal for time fields
                            excel += "    <td class='nav border'>" + this.fieldValue(row.data[j].rowKey._issue, row.moreFields[i], options) + "</td>\n";
                        } else {
                            excel += "    <td class='nav border'>&nbsp;</td>\n";
                        }
                    } else {
                        excel += "    <td class='nav border'>&nbsp;</td>\n";
                    }
                }
                if (!hideCommentColumn) {
                  excel += "    <td class='nav border'>" + (detailedRow.worklog && detailedRow.worklog.comment ? detailedRow.worklog.comment.replace(/<([^>]+)>/g, '&lt;$1&gt;') : "") + "</td>\n";
                }
                for (var i in columns) {
                    var columnName = columns[i].columnKey.keyValue;
                    var value = detailedRow.values[columnName];
                    if (columnName == '6progress') {
                        if (value.timespent + value.estimate == 0) {
                            excel += "    <td class='nav border'></td>\n";
                        } else {
                            var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent));
                            excel += "    <td class='nav border'>" + DrawHelper.formatDecimal(percents, maxFractionDigits) + "%</td>\n";
                        }
                    } else if (columnName == '5originalestimateremaining') {
                        excel += "    <td class='nav border'>" + getFormattedTime(value.value, options.jiraConfig) + "</td>\n";
                    } else if (["1timeoriginalestimate", "2esttimeremaining", "3timespent", "4diff", "2timespent"].indexOf(columnName) >= 0) {
                        excel += "    <td class='nav border'>" + getFormattedTime(value, options.jiraConfig) + "</td>\n";
                    } else if (columnName == '1started') {
                        excel += "    <td class='nav border'>" + moment(value).format('LT') + "</td>\n";
                    } else if (detailedRow.columnKey.keyValue == columnName) {
                        excel += "    <td class='nav border'>" + getFormattedTime(value, options.jiraConfig) + "</td>\n";
                    } else {
                        excel += "    <td class='nav border'>&nbsp;</td>\n";
                    }
                }
                if (!hideTotalColumn) {
                    excel += "    <td class='nav border'>&nbsp;</td>\n";
                }
                excel += "  </tr>\n";
            }
        }
    }
    excel += "  <tr class='rowFooter'>\n";
    excel += "    <td class='nav border total'>" + options.translations["Total"] + " (" + pivotTable.num + " " + options.translations['issue(s)'] + ")</td>\n";
    var colspan = 0;
    if (options.categorizeByField && options.groupByField) {
        colspan++;
    }
    if (options.groupByField) {
        colspan++;
    }
    if (!options.groupByField || !options.compressed || options.groupByField == 'issue') {
        colspan += 5;
    }
    excel += TimesheetUtils.repeat("    <td class='nav border total'>&nbsp;</td>\n", colspan - 1 /* Total */);
    for (var i in pivotTable.moreFields) {
        // FIXME: formatDecimal for time fields
        excel += "    <td class='nav border total'>" + this.fieldValue({fields: pivotTable.fieldTotals}, pivotTable.moreFields[i], options) + "</td>\n";
    }
    if (!hideCommentColumn) {
        excel += "    <td class='nav border total'>&nbsp;</td>\n";
    }
    for (var i in columns) {
        var columnName = columns[i].columnKey.keyValue;
        var entry = pivotTable.totals[columnName];
        if (columnName == '6progress') {
            var value = entry.value;
            if (value.timespent + value.estimate == 0) {
                excel += "    <td class='nav border total'></td>\n";
            } else {
                var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent));
                excel += "    <td class='nav border total'>" + DrawHelper.formatDecimal(percents, maxFractionDigits) + "%</td>\n";
            }
        } else if (columnName == '5originalestimateremaining') {
            excel += "    <td class='nav border total'>" + getFormattedTime(entry.value.value, options.jiraConfig) + "</td>\n";
        } else if (["1started", "3action"].indexOf(columnName) >= 0) {
            excel += "    <td class='nav border total'></td>\n";
        } else {
            excel += "    <td class='nav border total'>" + getFormattedTime(entry.sum, options.jiraConfig) +
                (options.showDetailsCount ? " " + entry.detailsNum : "") + "</td>\n";
        }
    }
    if (!hideTotalColumn) {
        excel += "    <td class='nav border total'>" + getFormattedTime(pivotTable.sum, options.jiraConfig) +
            (options.showDetailsCount ? " " + pivotTable.detailsNum : "") + "</td>\n";
    }
    excel += "  </tr>\n";
    var restrictedProjects = options.restrictedProjects && options.restrictedProjects.join(',');
    if (restrictedProjects) {
        excel += "<tr><td class='nav border total' colspan=" + colspan + ">" + options.translations["Data is limited by Auditors Roles"] + " " + restrictedProjects + "</td></tr>";
    }
    var restrictedGroups = options.restrictedGroups && options.restrictedGroups.join(',');
    if (restrictedGroups) {
        excel += "<tr><td class='nav border total' colspan=" + colspan + ">" + options.translations["Data is limited to Restricted Groups"] + " " + restrictedGroups + "</td></tr>";
    }
    excel += "</table>\n";
    return excel;
};

HtmlView.prototype.download = ExcelView.prototype.download;

if (typeof module === 'object' && module.exports) {
    module.exports = HtmlView;
    var PivotTableFactory = require('./PivotTable');
    var TimesheetUtils = require('../../timesheetUtils');
    var DrawHelper = require('../../app/directives/DrawHelper');
    var moment = require('moment-timezone');
}
