app.controller('WelcomeController', ['$scope', '$window', 'translations',
        function ($scope, $window, translations) {

    init();

    $scope.isTimesheet = $window.isTimesheet;
    $scope.appNameTranslated = translations[$scope.appName];

    function init() {
        $scope.contentTemplate = function() {
            return $scope.templates['welcome']; // furl
        };
    }
}]);
