//This handles retrieving data and is used by controllers. 3 options (server, factory, provider) with
//each doing the same thing just structuring the functions/data differently.

    app.factory('flightRecorder', ['$http', '$q', 'configurationService', 'loggedInUser', 'projectKey', 'applicationLoggingService', 'pivottableService',
        function ($http, $q, configurationService, loggedInUser, projectKey, logger, pivottableService) {

        var generateFileName = function() {
            /* global moment */
            return moment(new Date()).format('YYYY-MMM-DD_HH-mm-ss') + ".txt";
        };

        this.enabled = false;
        // this.supportData = {'fileName': fileData}, where fileData = {...}
        this.supportData = {};
        this.fileName = null;

        var deferred = $q.defer();
        var flightRecorder = this;
        loggedInUser.then(function(loggedInUser) {
            flightRecorder.loggedInUser = loggedInUser;
            $q.all([configurationService.getUserPreferences(loggedInUser),
                    configurationService.getConfiguration()]).then(function(configs) {
                flightRecorder.userPreferences = configs[0];
                flightRecorder.config = configs[1];
                deferred.resolve(flightRecorder);
            });
        });
        projectKey.then(function(projectKey) {
            flightRecorder.projectKey = projectKey;
        });

        var AP = window.RA || window.AP;
        var originRequestFunc = AP.request;
        this.setEnabled = function(enabled) {
            this.enabled = enabled;
            if (enabled) {
                flightRecorder.startFile();
                AP.request = function(request) {
                    originRequestFunc(flightRecorder.wrapRequest(request)); // FIXME: arguments?
                };
            } else {
                AP.request = originRequestFunc;
                flightRecorder.supportData = {};
            }
        };

        this.startFile = function() {
            this.fileName = generateFileName();
            while (this.supportData.hasOwnProperty(this.fileName)) {
                this.fileName = generateFileName();
            }
            this.supportData[this.fileName] = [];
        };

        this.cookie = function(data) {
            if (this.enabled) {
                /* global angular */
                this.cookies = angular.copy(data);
            }
        };

        this.wrapRequest = function(request) {
            var item = {
                action: 'rest',
                request: {
                    url: request.url,
                    data: request.data
                }
            };

            var originSuccess = request.success;
            var originError = request.error;
            request.success = function (data) {
                item.response = data;
                flightRecorder.supportData[flightRecorder.fileName].push(item);
                originSuccess(data);
            };
            request.error = function(jqXHR) {
                item.error = jqXHR;
                flightRecorder.supportData[flightRecorder.fileName].push(item);
                originError(jqXHR, /* type */ null, /* error */ null);
            };
            return request;
        };

        this.stringify = function(value) {
            return typeof value == 'object' ? JSON.stringify(value) : "'" + (value || '').replace(/\\/g, '\\\\').replace(/'/g, "\\'") + "'";
        };

        this.generateJs = function() {
            var js = '//' + window.deployedDate + '\n';
            js += 'AJS = window.AJS || {$: window.$};\n';
            if (this.config.cacheWorklog && this.config.cacheWorklog.val
                && pivottableService.allIssues) {  // loaded
                var updatedSince = window.localStorage.getItem('updatedSince');
                if (updatedSince) {
                    js += 'window.localStorage.setItem("updatedSince", ' + updatedSince + ');\n';
                }
                var deletedSince = window.localStorage.getItem('deletedSince');
                if (deletedSince) {
                    js += 'window.localStorage.setItem("deletedSince", ' + deletedSince + ');\n';
                }
                var recordToFile = function (key, value) {
                    if (value) {
                        js += 'window.localStorage.setItem("' + key + '", LZString.compressToUTF16(' + flightRecorder.stringify(LZString.decompressFromUTF16(value)) + '));\n';
                    }
                };
                pivottableService.allIssues.forEach(function (issue) {
                    var key = 'i' + issue.id;
                    var value = window.localStorage.getItem(key);
                    recordToFile(key, value);
                    if (issue._subTaskIds) {
                        for (var i = 0; i < issue._subTaskIds.length; i++) {
                            key = 'i' + issue._subTaskIds[i];
                            value = window.localStorage.getItem(key);
                            recordToFile(key, value);
                        }
                    }

                });
            }
            js += 'AP = {\n';
            js += '  flag: {\n';
            js += '      create: function() {\n';
            js += '          debugger;\n';
            js += '      }\n';
            js += '  },\n';
            js += '  events: {onPublic: function() {}},\n';
            js += '  getLocation: function(callback) {\n';
            js += '    setTimeout(function() {\n';
            js += "      callback('" + logger.baseUrl + (this.projectKey ? '/?project.key=' + this.projectKey : '') + '#!' + $.param(this.routeParams).replace(/'/g, "\\'") + "');\n";
            js += '    });\n';
            js += '  },\n';
            js += '  context: {\n';
            js += '    getContext: function(callback) {\n';
            js += '      setTimeout(function() {\n';
            js += '        callback({license: {active: true}});\n';
            js += '      });\n';
            js += '    }\n';
            js += '  },\n';
            js += '  request: function(options) {\n';
            js += '    setTimeout(function() {\n';
            js += '      if (options.url == "/rest/api/2/myself?expand=groups") {\n';
            js += "        options.success(" + JSON.stringify(this.loggedInUser) + ");\n";
            js += '      } else if (options.url == "/rest/atlassian-connect/1/addons/timereports") {\n';
            js += "        options.success(" + JSON.stringify(logger.license) + ");\n";
            js += '      } else if (options.url == "/rest/atlassian-connect/1/addons/timereports/properties/") {\n';
            js += "        options.success({keys: [{key: 'configuration'}]});\n";
            js += '      } else if (options.url == "/rest/atlassian-connect/1/addons/timereports/properties/configuration") {\n';
            var configuration = {};
            for (var key in this.config) {
                configuration[key] = this.config[key].val;
            }
            js += "        options.success({value: " + JSON.stringify(configuration) + "});\n";
            js += '      } else if (options.url == "/rest/atlassian-connect/1/addons/timereports/properties/preferences4' + this.loggedInUser.accountId + '") {\n';
            var userPreferences = {};
            for (var key in this.userPreferences) {
                userPreferences[key] = this.userPreferences[key].val;
            }
            js += "        options.success({value: " + JSON.stringify(userPreferences) + "});\n";
            var flightRecorderData = flightRecorder.supportData[Object.keys(flightRecorder.supportData)[0]];
            for (var i = 0; i < flightRecorderData.length; i++) {
                var item = flightRecorderData[i];
                if (item.action == 'rest') {
                    js += '      } else if (options.url == "' + item.request.url + '") {\n';
                    if (item.error) {
                        js += "        options.error(" + this.stringify(item.error) + ");\n";
                    } else {
                        js += "        options.success(" + this.stringify(item.response) + ");\n";
                    }
                }
            }
            // fake response for all unrecorded updates
            js += '      } else if (options.url.match(/rest\\/api\\/2\\/worklog\\/.+?since=/)) {\n';
            js += "        options.success('{\"values\": [], \"until\": 9000000000000,\"lastPage\": true}');\n";
            js += '      } else {\n';
            js += '        throw new Error("Not recorded: " + options.url);\n';
            js += '      }\n'; // if options.url
            js += '    });\n';
            js += '  },\n'; // request
            js += '  resize: function() {\n';
            js += '  },\n';
            js += '  messages: {\n';
            js += '    error: function() {\n';
            js += '      console.log.apply(console, arguments);\n';
            js += '    }\n';
            js += '  },\n';
            js += '  cookie: {\n';
            js += '    read: function(name, callback) {\n';
            js += '      callback(' + JSON.stringify(this.cookies) + ');\n';
            js += '    }\n';
            js += '  },\n';
            js += '  history: {\n';
            js += '    pushState: function(state) {\n';
            js += "      console.log('history state', state);\n";
            js += '    }\n';
            js += '  },\n';
            js += '  inlineDialog: {\n';
            js += '    hide: function(state) {\n';
            js += '    }\n';
            js += '  },\n';
            js += '  require: function(what, callback) {\n';
            js += '    throw new Error("Not implemented: " + what);\n';
            js += '  }\n';
            js += '}';
            return js.replace(/jira-timesheet-plugin/g, 'timereports');
        };

        this.zipBlob = function () {
            var zip = new JSZip();
            var data = this.generateJs();
            zip.file(this.fileName, data);
            return zip.generateAsync({type:"blob"});
        };

        this.sendEmail = function (includeSupportData, replyTo, title, content, screenshot, error) {
            var $blob = includeSupportData ? flightRecorder.zipBlob() : $q.resolve();
            return $blob.then(function (blob) {
                var fd = new FormData();
                if (blob) {
                    fd.append('supportData', blob, "supportData.zi_");
                }
                fd.append('replyTo', replyTo);
                fd.append('title', title);
                var text = content || '';
                if (error) {
                    text += '\n--\n' + JSON.stringify(error).replace(/\\n/g, '\n');
                }
                fd.append('content', text);
                if (screenshot) {
                    fd.append('screenshot', screenshot, "screen.png");
                }
                return $http.post(window.mailerUrl, fd, {transformRequest: angular.identity, headers: {'Content-Type': undefined}});
            });
        };

        return deferred.promise;

    }]);
