//This handles retrieving data and is used by controllers. 3 options (server, factory, provider) with
//each doing the same thing just structuring the functions/data differently.
app.service('pivottableService', ['$q', '$timeout', '$interval', '$http', 'applicationLoggingService', 'configurationService', '$log',
        function ($q, $timeout, $interval, $http, logger, configurationService, $log) {
    var AP = window.RA || window.AP;

    var DASHBOARD_PROPERTY_GADGET_CONFIG = 'gadgetConfig';

    const MAX_BULK_LENGTH = 10;
    const RETRY_COUNT = 4;

    this.permissions = {};
    // filterWorklogs()
    this.userGroups = {};
    this.userInfos = {};
    this.worklogAuthors = [];
    this.changelogAuthors = [];
    // isEpicField()
    this.epicFields = {};
    this.workLabels = {};
    this.issueWorklogs = [];
    this.issueChangelogs = [];
    this.findIssueByKeyPromises = [];
    this.getPermissionsPromises = [];
    this.onAllIssuesPromises = [];
    this.createWorklogPromise = $q.resolve();
    this.getUserInfoPromises = [];
    this.$restrictedProjects = {};
    this.restrictedProjects = [];
    logger._ = Date.now(); // session tracking id

    this.getPivotTable = function(loggedInUser, options) {

        var postProcessPivotTable = function () {
            var addEmptyRows = function () {
                if (options.pivotTableType == 'Calendar') {
                    TimesheetUtils.getWeeksForMonth(options).map(function (row) {
                        TimesheetUtils.getOrCreate(pivotTable.rows, {
                            keyValue: moment(row.date).format('YYYYMMDD'),
                            issue: {},
                            field: {startDate: row.date, weekNumber: TimesheetUtils.getNormalizedWeekNumber(row.date)}
                        }, PivotRow, function () {
                            this.columns = pivotTable.pivotStrategy.getColumnsFunction(row.date);
                        });
                    })
                }
            };
            var highlightHours = function () {
                var setColor = function (obj, threshold) {
                    if (obj.sum > threshold) {
                        obj.color = options.configOptions.workOverLimitColor;
                        obj.title = '+' + DrawHelper.getFormattedTime(obj.sum - threshold, options.jiraConfig);
                    } else if (obj.sum < threshold) {
                        obj.color = options.configOptions.workUnderLimitColor;
                        obj.title = '-' + DrawHelper.getFormattedTime(threshold - obj.sum, options.jiraConfig);
                    }
                 };

                if (options.pivotTableType === 'Timesheet' &&
                    (options.user || (options.groupByField === 'workeduser' && !options.categorizeByField)) &&
                    (options.configOptions.workUnderLimitColor || options.configOptions.workOverLimitColor)) {
                    var workingSecondsPerDay = options.jiraConfig.workingHoursPerDay * 60 * 60;
                    var rows = Object.keys(pivotTable.rows);
                    var totals = Object.keys(pivotTable.totals);
                    var workingDaysTotal = 0;
                    var workedUsers = options.groupByField === 'workeduser' ? rows.length : 1;
                    for (var totalKey in totals) {
                        var total = pivotTable.totals[totals[totalKey]];
                        workingDaysTotal += total.columnKey.workingDays;
                        setColor(total, workingSecondsPerDay * total.columnKey.workingDays * workedUsers);
                    }
                    if (options.groupByField === 'workeduser') {
                        for (var rowKey in rows) {
                            var row = pivotTable.rows[rows[rowKey]];
                            var columns = Object.keys(row.columns);
                            setColor(row, workingSecondsPerDay * workingDaysTotal);
                            for (var columnKey in columns) {
                                var column = row.columns[columns[columnKey]];
                                setColor(column, workingSecondsPerDay * column.columnKey.workingDays);
                            }
                        }
                    }
                    setColor(pivotTable, workingSecondsPerDay * workingDaysTotal * workedUsers);
                }
            };

            addEmptyRows();
            highlightHours();
        };

        // DO data in services: http://blog.nebithi.com/angularjs-dos-and-donts/
        this.loggedInUser = loggedInUser;
        this.needUserInfos = [options.groupByField, options.categorizeByField].indexOf('workedusergroup') >= 0;
        if (options.configOptions.restrictedGroups) {
            var loggedInUserInOptionGroups = !options.groups || options.groups.filter(function (group) {
                return TimesheetUtils.findByProperty('name', group, loggedInUser.groups.items);
            }).length > 0;
            options.groups = options.restrictedGroups = options.configOptions.restrictedGroups.filter(function (group) {
                return (!options.groups || options.groups.indexOf(group) >= 0) &&
                    TimesheetUtils.findByProperty('name', group, loggedInUser.groups.items);
            });
            if (!options.groups.length) {
                options.groups = null;
                if (options.username == loggedInUser.accountId) { // TODO: groups unchanged
                    options.restrictedGroups = null; // avoid warning
                }
                options.username = options.user = !options.username && loggedInUserInOptionGroups || options.username == loggedInUser.accountId ? loggedInUser.accountId : 'noSuchUser';
            }
        }
        if (options.groups || options.excludeGroups) {
            options.worklogAuthors = this.worklogAuthors;
            options.changelogAuthors = this.changelogAuthors;
        }
        if (options.configOptions.auditorsRoles) {
            options.restrictedProjects = this.restrictedProjects;
            options.loggedInUser = loggedInUser;
        }
        options.userInfos = this.userInfos;

        var service = this;
        var pivotTable = PivotTableFactory.createPivotTable(options, logger);
        var deferred = $q.defer();
        var isIssueReport = ['IssueTime', 'IssueTimeBalance'].indexOf(options.pivotTableType) >= 0;
        if (isIssueReport && !this.allIssues) {
            this.allIssues = [];
            if (options.pivotTableType == 'IssueTime') {
                this.getIssueWorklog(options.issueKey, options.issueId).then(function (worklog) {
                    service.addIssue(pivotTable, {key: options.issueKey, id: options.issueId, fields: {worklog: worklog}});
                    deferred.resolve(pivotTable);
                });
            } else {
                options._fields = "created," + this.timeFields.join(',');
                this.findIssueByKey(options.issueKey, options).then(function (issue) {
                    service.loadAllChangelogs(issue, options).promise.then(function (issue) {
                        service.addIssue(pivotTable, issue);
                        deferred.resolve(pivotTable);
                    });
                })
            }
        } else if (!isIssueReport && this.checkAndUpdateOptions(options)) {
            pivotTable.queue = {};
            pivotTable.queueToAdd = [];
            pivotTable.matches = {};
            if (this.groups || this.excludeGroups || this.needUserInfos) {
                this.userGroups[loggedInUser.accountId] = $q.resolve(loggedInUser);
                if (this.needUserInfos) {
                    this.userInfos[loggedInUser.accountId] = loggedInUser;
                }
                if (this.groups || this.excludeGroups) {
                    this.initWorklogAuthors(angular.copy(loggedInUser));
                    if (options.pivotTableType == 'TimeBalance') {
                        this.initChangelogAuthors(angular.copy(loggedInUser));
                    }
                }
            }

            var addInProgressIssues = function () {
                if (hasInProgressIssues(options)) {
                    var query = 'jql=' + encodeURIComponent(options.configOptions.inProgressIssuesJql) + '&fields=' + options._fields;
                    return service.onAllIssues(query, function (data) {
                        var addIssue = function (issue) {
                            if (!pivotTable.rows.hasOwnProperty(issue.key)) {
                                issue.fields.worklog = {total: 0, maxResults: 0, worklogs: []};
                                issue._inProgressIssue = true;
                                service.addIssue(pivotTable, issue);
                            }
                        };
                        return $q.all(data.issues.map(function (issue) {
                            if (options.sumSubTasks) {
                                var parent = service.getParentIssue(pivotTable, issue, options);
                                if (parent.hasOwnProperty('promise')) {
                                    return service.onParentIssue(pivotTable, issue, options, parent, [], addIssue);
                                } else {
                                    addIssue(issue);
                                }
                            } else {
                                addIssue(issue);
                            }
                        }));
                    }).catch(function () {
                        // ignore
                    });
                } else {
                    return $q.resolve();
                }
            };

            //var performance = TimesheetUtils.getPerformance();
            //var t0 = performance.now();
            var m = 0, n = 0, notify;
            var $notify = $interval(function() {
                // 6250 ms till 10% for 10K issues
                if (!notify) {
                    deferred.notify(++n/250);
                } else {
                    $interval.cancel($notify);
                }
            }, 250);
            this.onAllIssues(this.getQuery(pivotTable, options), function(data) {
                //var t1 = performance.now();
                if (!notify) {
                    n = data.total || 1;
                    notify = function() {
                        deferred.notify(++m/n);
                    };
                }
                var result = service.processIssues(pivotTable, options, data, notify);
                //var t2 = performance.now();
                //logger.debug('processIssues performance', {_: logger._, time: t2 - t1, t1: t1 - t0});
                return result;
            }).then(function(result) {
                if (service.cancel(deferred)) {
                    return;
                }
                var promises = result.reduce(function(a, b) { // flatten
                  return a.concat(b);
                });
                //var t2 = performance.now();
                // objects may have 2^32 - 1 properties: http://stackoverflow.com/questions/9282869/javascript-limits-to-number-of-object-properties
                $q.all(promises).then(function() {
                    if (service.cancel(deferred)) {
                        return;
                    }
                    angular.forEach(pivotTable.queueToAdd, function(issue) {
                        service.addIssue(pivotTable, issue);
                    });
                    addInProgressIssues().then(function() {
                        postProcessPivotTable();
                        deferred.resolve(pivotTable);
                    });
                    //var t3 = performance.now();
                    //service.allIssues.length > 100 && logger.debug('getPivotTable performance', {/*_: logger._  , sumSubTasks: options.sumSubTasks, groups: options.groups, allIssues: service.allIssues.length, promises: promises.length,*/ time: t3 - t0, t2: t2 - t0, t3: t3 - t2});
                }, deferred.reject);
            }, function(error) {
                $interval.cancel($notify);
                deferred.reject(error);
            });
        } else {
            if (options.queue) {
                pivotTable.queue = options.queue;
            }
            if (options.queueToAdd) {
                pivotTable.queueToAdd = options.queueToAdd;
            }
            if (options.matches) {
                pivotTable.matches = options.matches;
            }
            for (var i in this.allIssues) {
                /*var pivotEntries =*/ pivotTable.add(this.allIssues[i]);
            }
            postProcessPivotTable();
            if (options.pivotTableType == 'PieChart') {
                $timeout(function () {
                    deferred.resolve(pivotTable);
                });
            } else { // for IssueTime
                deferred.resolve(pivotTable);
            }
        }
        return deferred.promise;
    };

    this.cancel = function(deferred) {
        if (this._cancel && deferred) {
            deferred.reject('cancelled');
            this._cancel = false;
            return true;
        } else if (!deferred) {
            this._cancel = true;
        }
        return false;
    };

    var hasInProgressIssues = function (options) {
        return ['TimeEntry', 'Timesheet'].indexOf(options.pivotTableType) >= 0 &&
              TimesheetUtils.isCurrentWeek(options.startDate, options.endDate) &&
              !options.groupByField && options.configOptions.inProgressIssuesJql;
    };

    this.getAndProcessIssues = function(pivotTable, options) {
        var service = this;
        var deferred = $q.defer();
        var queryBuilder = new TimesheetUtils.queryBuilder();
        if (options.startDate != null && options.startDate != "") {
            queryBuilder.addQuery('start', service.addDays(options.startDate, -1)); // -1 Day to startDate to cover all timezones
        }
        if (options.endDate != null && options.endDate != "") {
            queryBuilder.addQuery('end', service.addDays(options.endDate, 2)); // +1 Day to endDate to cover all timezones
        }
        if (options.username) {
            queryBuilder.addQuery('author', options.username); // filter by user
        }
        if (options.groups) {
          queryBuilder.addQuery('groups', options.groups); // filter by groups
        }
        if (options.jql != null) {
            queryBuilder.addQuery('jql', options.jql); // empty JQL for all issues
        } else if (options.filterOrProjectId) {
            queryBuilder.addQuery('filterOrProjectId', options.filterOrProjectId);
        }
        if (options.projectKey) {
            queryBuilder.addQuery('projectKey', options.projectKey);
        }
        if (options.sumSubTasks) {
            queryBuilder.addQuery('sumSubTasks', options.sumSubTasks);
        }
        var moreFields = this.getMoreFields(options);
        if (moreFields.length > 0) {
            queryBuilder.addQuery('moreFields', moreFields);
        }

        var fields = 'project,issuetype,resolution,summary,priority,status,parent,issuelinks';
        this.initEpicLinkField();
        if (this.epicLinkField) {
            fields += ',' + this.epicLinkField.id;
        }
        if (this.parentLinkField) {
            fields += ',' + this.parentLinkField.id;
        }
        if (moreFields.length > 0) {
            fields += ',' + moreFields.join(',');
        }
        if (this.isProcessChildren(options)) {
            fields += ',subtasks'; // for processChildren
        }
        if (['TimeBalance', 'IssuePassedTimeByStatus', 'IssuePassedTimeByResolution'].indexOf(options.pivotTableType) >= 0) {
            fields += ',created';
        }
        options._fields = fields; // for findIssueByKey

        queryBuilder.addQuery('_', Date.now());
        var query = queryBuilder.build();

        var performance = TimesheetUtils.getPerformance();
        var t0 = performance.now();
        var m, n = 0, $notify = $interval(function() {
            // 31 seconds till 50%
            deferred.notify(++n/250);
        }, 250);
        var url = "/api/worklog?" + query;
        $http.get(url).then(function (response) {
            var worklog = response.data;
            $interval.cancel($notify);
            if (service.cancel(deferred)) {
                return;
            }
            var t1 = performance.now();
            m = n * worklog.length / 250; // continue
            n = (worklog.length - m) / worklog.length; // increment, may be NaN = 0/0
            var promises = service.processIssues(pivotTable, options, {issues: worklog}, function() {
                m += n;
                deferred.notify(m / worklog.length);
            });
            $q.all(promises).then(deferred.resolve);
            var t2 = performance.now();
            logger.debug('getAndProcessIssues', {_: logger._, t2: t2 - t1, t1: t1 - t0, t: t2 - t0});
        }).catch(function (/* response or error */) {
            var args = Array.prototype.slice.call(arguments);
            args.splice(args.length, 0, /* type */ null, /* error */ args[0].message, url, deferred);
            service.onError.apply(service, args);
        });
        return deferred.promise;
    };

    this.onAllIssues = function(query, callback) {
        var promises = this.onAllIssuesPromises;
        var previous = promises.length < MAX_BULK_LENGTH ? 0 : promises[promises.length - MAX_BULK_LENGTH];
        var service = this;
        var promise = $q.when(previous).then(function () { // not more than MAX_BULK_LENGTH concurrent requests
            return service.onAllIssuesDelayed(query, callback);
        });
        promises.push(promise);
        return promise;
    };

    this.onAllIssuesDelayed = function(query, callback) {
        var startAt = (query ? '&' : '') + 'startAt=';
        var deferred = $q.defer();
        var promises = [];
        var service = this;
        this.getIssues(query + startAt + '0').then(function(data) {
            promises.push(callback(data)); // firstDeferred
            var n = Math.ceil(data.total / data.maxResults) || 1;
            for (let i = 1; i < n; i++) { // closure!
                var previous = promises.length > MAX_BULK_LENGTH ? promises[promises.length - MAX_BULK_LENGTH] : $q.resolve(); // promises[1 .. ]
                var promise = previous.then(function() { // not more than MAX_BULK_LENGTH concurrent requests
                    if (service.cancel(deferred)) {
                        return;
                    }
                    return service.getIssues(query + startAt + data.maxResults * i)
                        .then(callback);
                });
                promises.push(promise);
            }
            $q.all(promises).then(deferred.resolve, deferred.reject);
        }, deferred.reject);
        return deferred.promise;
    };

    this.getNumericFields = function() {
        return this.allFields.filter(function (field) {
            return field.schema && field.schema.type == 'number';
        }).map(function (field) {
            return field.id;
        });
    };

    this.getFields = function() {
        var pivottableService = this;
        var deferred = $q.defer();

        if (pivottableService.allFields == null) {
            var url = '/rest/api/2/field';
            AP.request({
                url: url,
                dataType: 'json',
                success: function(data) {
                    data = TimesheetUtils.getJson(data);
                    pivottableService.allFields = data;
                    pivottableService.allNumericFields = pivottableService.getNumericFields();
                    deferred.resolve(data);
                },
                error: function () {
                    var args = Array.prototype.slice.call(arguments);
                    args.push(url, deferred);
                    pivottableService.onError.apply(pivottableService, args);
                }
            });
        } else {
            deferred.resolve(pivottableService.allFields);
        }

        return deferred.promise;
    };

    this.getFilters = function() {
        var pivottableService = this;
        var deferred = $q.defer();

        if (pivottableService.allFilters == null) {
            var url = '/rest/api/2/filter/favourite';
            AP.request({
                url: url,
                dataType: 'json',
                success: function(data) {
                    data = TimesheetUtils.getJson(data);
                    pivottableService.allFilters = data;
                    deferred.resolve(data);
                },
                error: function () {
                    var args = Array.prototype.slice.call(arguments);
                    args.push(url, deferred);
                    pivottableService.onError.apply(pivottableService, args);
                }
            });
        } else {
            deferred.resolve(pivottableService.allFilters);
        }
        return deferred.promise;
    };

    this.getJiraConfiguration = function() {
        if (!this.jiraConfiguration) {
            var deferred = $q.defer();
            this.jiraConfiguration = deferred.promise;
            var service = this;
            var url = '/rest/api/2/configuration';
            AP.request({
                url: url,
                dataType: 'json',
                success: function (data) {
                    var config = TimesheetUtils.getJson(data);
                    config.workingHoursPerDay = 8;
                    config.workingDaysPerWeek = 5;
                    config.defaultUnit = 'minute';
                    config.timeFormat = '';
                    if (config.timeTrackingConfiguration) {
                        config.workingHoursPerDay = config.timeTrackingConfiguration.workingHoursPerDay || config.workingHoursPerDay;
                        config.workingDaysPerWeek = config.timeTrackingConfiguration.workingDaysPerWeek || config.workingDaysPerWeek;
                        config.defaultUnit = config.timeTrackingConfiguration.defaultUnit || config.defaultUnit;
                        config.timeFormat = config.timeTrackingConfiguration.timeFormat || config.timeFormat;
                    }
                    deferred.resolve(config);
                },
                error: function () {
                    var args = Array.prototype.slice.call(arguments);
                    args.push(url, deferred);
                    service.onError.apply(service, args);
                }
            });
          }
        return this.jiraConfiguration.catch(function () {
            return {};
        });
    };

    this.getUserInfo = function(accountId, by) {
        var deferred = $q.defer();
        if (!accountId) {
            deferred.resolve({accountId: '', groups: {items: []}});
        } else {
            var promises = this.getUserInfoPromises;
            var previous = promises.length < MAX_BULK_LENGTH ? 0 : promises[promises.length - MAX_BULK_LENGTH];
            promises.push(deferred.promise);
            var service = this;
            $q.when(previous).then(function() { // not more than MAX_BULK_LENGTH concurrent requests
                var url = '/rest/api/2/user?expand=groups&' + (by || 'accountId') + '=' + encodeURIComponent(accountId);
                AP.request({
                    url: url,
                    dataType: 'json',
                    success: function (data) {
                        deferred.resolve(TimesheetUtils.getJson(data));
                    },
                    error: function (jqXHR) {
                        if (jqXHR && jqXHR.status == 404) {
                            deferred.resolve({accountId: accountId, groups: {items: []}});
                        } else {
                            var args = Array.prototype.slice.call(arguments);
                            args.push(url, deferred);
                            service.onError.apply(service, args);
                        }
                    }
                });
            })
        }
        return deferred.promise;
    };

    this.getMyself = function () {
        var deferred = $q.defer();
        var service = this;
        var url = '/rest/api/2/myself?expand=groups';
        AP.request({
            url: url,
            dataType: 'json',
            success: function (data) {
                deferred.resolve(TimesheetUtils.getJson(data));
            },
            error: function () {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.getIssues = function(query) {
        var pivottableService = this;
        var deferred = $q.defer();
        var url = '/rest/api/2/search?' + query;
        AP.request({
            retryCount: RETRY_COUNT,
            url: url,
            dataType: 'json', // does not work!
            success: function(data) {
                data = TimesheetUtils.getJson(data);
                deferred.resolve(data);
            },
            error: function() {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                pivottableService.onError.apply(pivottableService, args);
            }
        });
        return deferred.promise;
    };

    var quote = function(value) {
        return '"' + value + '"';
    };

    this.getQuery = function(pivotTable, options) {
        var pivottableService = this;
        // https://ecosystem.atlassian.net/browse/ADDON-230
        var maxResults = 1000; // https://confluence.atlassian.com/display/JIRAKB/Changing+maxResults+Parameter+for+JIRA+REST+API

        var isIssuePassedTimeByStatus = options.pivotTableType == 'IssuePassedTimeByStatus';
        var isTimeTracking = options.pivotTableType == 'TimeTracking';
        var isTimeBalance = options.pivotTableType == 'TimeBalance';
        var isIssuePassedTimeByResolution = options.pivotTableType == 'IssuePassedTimeByResolution';

        var prepareJqlBuilder = function(options) {
            var addDateFields = function (value, delimiter) {
                options.dateFields.forEach(function (field) {
                    qb.addQuery(field, value, delimiter);
                });
                jqlQueryBuilder.addQueryBuilder(qb);
            };
            var jqlQueryBuilder;
            if (options.sumSubTasks && !isTimeTracking) {
                pivottableService.initMatches(options);
                jqlQueryBuilder = new TimesheetUtils.queryBuilder(" and ");
            } else {
                jqlQueryBuilder = TimesheetUtils.getSearchQueryBuilder(options);
            }
            if (!options.includeEmpty && options.startDate != null && options.startDate != "") {
                var dateClause = isIssuePassedTimeByResolution ? 'resolution changed' : isIssuePassedTimeByStatus ? 'status changed' : isTimeTracking || isTimeBalance ? 'updated' : 'worklogDate';
                var dateCondition = isIssuePassedTimeByStatus || isIssuePassedTimeByResolution ? ' after ' : ' >= ';
                var qb = isIssuePassedTimeByStatus || isTimeTracking || isTimeBalance || isIssuePassedTimeByResolution ? new TimesheetUtils.queryBuilder(" or ") : jqlQueryBuilder;
                var startDate = pivottableService.addDays(options.startDate, isIssuePassedTimeByStatus || isTimeTracking || isTimeBalance || isIssuePassedTimeByResolution ? 0 : -1);
                if (isTimeTracking || isTimeBalance) {
                    addDateFields(quote(startDate), ' >= ');
                } else {
                    qb.addQuery(dateClause, quote(startDate), dateCondition); // -1 Day to startDate to cover all timezones for worklogs
                }
                if (isIssuePassedTimeByStatus || isIssuePassedTimeByResolution) {
                    qb.addQuery(dateClause, quote(startDate), ' on '); // include report date
                    qb.addQuery('created', quote(startDate), ' >= ');
                    jqlQueryBuilder.addQueryBuilder(qb);
                }
            }
            if (!options.includeEmpty && options.endDate != null && options.endDate != "") {
                var dateClause = isIssuePassedTimeByResolution ? 'resolution changed' : isIssuePassedTimeByStatus ? 'status changed' : isTimeTracking || isTimeBalance ? 'updated' : 'worklogDate';
                var dateCondition = isIssuePassedTimeByStatus || isIssuePassedTimeByResolution ? ' before ' : ' < ';
                var qb = isIssuePassedTimeByStatus || isTimeTracking || isTimeBalance || isIssuePassedTimeByResolution ? new TimesheetUtils.queryBuilder(" or ") : jqlQueryBuilder;
                var endDate = pivottableService.addDays(options.endDate, 1);
                if (isTimeTracking || isTimeBalance) {
                    addDateFields(quote(endDate), ' < ');
                } else {
                    qb.addQuery(dateClause, quote(endDate), dateCondition); // +1 Day to endDate to cover all timezones for worklogs, or include report day for other reports
                }
                if (isIssuePassedTimeByStatus || isIssuePassedTimeByResolution) {
                    qb.addQuery('created', quote(endDate), ' < ');
                    jqlQueryBuilder.addQueryBuilder(qb);
                }
            }
            var jqlEscape = function(value) {
                return value
                    .replace('"', '\\u0022')
                    .replace('#', '\\u0023')
                    .replace('$', '\\u0024')
                    .replace('%', '\\u0025')
                    .replace('*', '\\u002a')
                    .replace('@', '\\u0040')
                    .replace('^', '\\u005e');
                // <>& unsupported by user management
            };
            var authorClause = isIssuePassedTimeByResolution ? 'resolution changed' : isIssuePassedTimeByStatus ? 'status changed' : isTimeTracking ? 'assignee' : 'worklogAuthor';
            var authorCondition = isIssuePassedTimeByStatus || isIssuePassedTimeByResolution ? ' by ' : '=';
            if (!options.includeEmpty && options.username && !isTimeBalance) { // ACJIRA-300
                var username = jqlEscape(options.username);
                var qb = isIssuePassedTimeByStatus || isTimeTracking || isIssuePassedTimeByResolution ? new TimesheetUtils.queryBuilder(" or ") : jqlQueryBuilder;
                qb.addQuery(authorClause, quote(username), authorCondition);
                if (isIssuePassedTimeByStatus || isTimeTracking || isIssuePassedTimeByResolution) {
                    qb.addQuery('reporter', quote(username));
                    jqlQueryBuilder.addQueryBuilder(qb);
                }
            }
            if (!options.includeEmpty && options.groups && !isTimeBalance) {
                var memberOfQB = new TimesheetUtils.queryBuilder(" or ");
                for (var i = 0; i < options.groups.length; i++) {
                    var authorCondition = isIssuePassedTimeByStatus || isIssuePassedTimeByResolution ? ' by ' : ' in ';
                    memberOfQB.addQueryWithFunc(authorClause, quote(jqlEscape(options.groups[i])), authorCondition, "membersOf");
                    if (isIssuePassedTimeByStatus || isTimeTracking || isIssuePassedTimeByResolution) {
                        memberOfQB.addQueryWithFunc('reporter', quote(jqlEscape(options.groups[i])), ' in ', "membersOf");
                    }
                }
                jqlQueryBuilder.addQueryBuilder(memberOfQB);
            }
            return jqlQueryBuilder;
        };

        var fields = 'project,issuetype,resolution,summary,priority,status,parent,issuelinks';
        if (!(isTimeTracking || isIssuePassedTimeByStatus || isTimeBalance || isIssuePassedTimeByResolution)) {
            fields += ',worklog';
            if (options.includeEmpty) {
                maxResults /= 2;
            }
        }
        fields += this.timeFieldsToAdd(options);
        this.initEpicLinkField();
        if (this.epicLinkField) {
            fields += ',' + this.epicLinkField.id;
        }
        if (this.parentLinkField) {
            fields += ',' + this.parentLinkField.id;
        }
        if (isIssuePassedTimeByStatus || isTimeBalance || isIssuePassedTimeByResolution) {
            fields += ',created,creator';
        }
        var moreFields = this.getMoreFields(options);
        if (moreFields.length > 0) {
            fields += ',' + moreFields.join(',');
        }
        if (this.isProcessChildren(options)) {
            fields += ',subtasks'; // for processChildren
        }
        options._fields = fields; // for findIssueByKey

        var queryBuilder = new TimesheetUtils.queryBuilder()
            // fields=worklog or expand=worklog
            .addQuery('fields', fields)
            // maxResults <= jira.search.views.default.max = 1000
            .addQuery('maxResults', maxResults);
        if (options.pivotTableType == 'IssueWorkedTimeByStatus' || isIssuePassedTimeByStatus || isTimeBalance || isIssuePassedTimeByResolution) {
            queryBuilder.addQuery('expand', 'changelog');
        }

        var jqlQueryBuilder = prepareJqlBuilder(options);

        if (!jqlQueryBuilder.isEmpty()) {
            var jqlQuery = jqlQueryBuilder.build();
            queryBuilder.addQuery('jql', encodeURIComponent(jqlQuery));
            var issuePickerJqlQueryBuilder = TimesheetUtils.getSearchQueryBuilder(options);
            options.jqlQuery = issuePickerJqlQueryBuilder.build();
        }
        var query = queryBuilder.build();
        return query;
    };

    this.timeFields = ['timeoriginalestimate', 'timeestimate', 'timespent'];

    this.isProcessChildren = function(options) {
        var moreFields = this.getMoreFields(options);
        return options.sumSubTasks &&
            (['TimeTracking', 'TimeBalance'].indexOf(options.pivotTableType) >=0 || TimesheetUtils.findOne(moreFields, this.allNumericFields) || options.includeEmpty)
            && (!options.groupByField
                || (!this.isEpicField(options.groupByField) && (!this.epicLinkField || this.epicLinkField.id != options.groupByField)
                    && (!this.parentLinkField || this.parentLinkField.id != options.groupByField)));
    };

    this.processIssues = function(pivotTable, options, data, notify) {
        var promises = [];
        for (var i in data.issues) {
            var issue = data.issues[i];
            if (['TimeTracking', 'IssuePassedTimeByStatus', 'TimeBalance', 'IssuePassedTimeByResolution'].indexOf(options.pivotTableType) >= 0) {
                 issue.fields.worklog = {total: 0, maxResults: 0, worklogs: []};
            }
            var promise = this.processIssue(pivotTable, options, issue);
            promise.then(notify);
            promises.push(promise);
        }
        return promises;
    };

    this.processIssue = function(pivotTable, options, issue) {
        var deferred = $q.defer();
        var pivottableService = this;
        if (!options.sumSubTasks) {
            var promises = [];
            if (options.groupByField == 'parent') {
                var parent = this.getParentIssue(pivotTable, issue, options);
                if (parent.hasOwnProperty('promise')) {
                    var parentDeferred = $q.defer();
                    promises.push(parentDeferred.promise);
                    pivottableService.resolveParent(pivotTable, issue, parentDeferred, options, parent);
                } else {
                    issue.fields.parent = issue; // self
                }
            } else {
                this.getEpicFields(pivotTable, issue, options, promises);
            }
            var result = this.loadAllWorklogs(issue, null, options); // issue or deferred
            if (result.hasOwnProperty('promise')) {
                promises.push(result.promise); // wait for filterWorklogs to collect worklogAuthors
            }
            var changelogs = this.loadAllChangelogs(issue, options); // issue or deferred
            if (changelogs.hasOwnProperty('promise')) {
                promises.push(changelogs.promise);
            }
            $q.all(promises).then(function () {
                // with all worklogs and resolved parent
                pivottableService.addIssue(pivotTable, issue);
                deferred.resolve(issue);
            });
        } else {

            var checkInQueue = function (def) {
                if (pivotTable.queue.hasOwnProperty(issue.key)) {
                    var element = pivotTable.queue[issue.key];
                    if (!element.promise.$$state || element.promise.$$state.status == 0) { // pending
                        element.resolve(issue); // don't wait for findIssueByKey
                    } // else findIssueByKey is already done
                    return element.promise;
                } else {
                    if (def) {
                        pivotTable.queue[issue.key] = def;
                    } // else put deferred resolved with issue?
                    return issue;
                }
            };

            var result = this.getParentIssue(pivotTable, issue, options);
            if (result.hasOwnProperty('promise')) {
                var issueFromQueue = checkInQueue();
                $q.when(issueFromQueue).then(function (issue) {
                    var promiseChangelog = pivottableService.loadAllChangelogs(issue, options).promise;
                    var promiseWorklog = pivottableService.loadAllWorklogs(issue, $q.defer(), options).promise;
                    $q.all([promiseWorklog, promiseChangelog]).then(function (data) {
                        var issue = data[0];
                        pivottableService.addWorklogsToParent(pivotTable, issue, deferred, options, result);
                    });
                });
            } else {
                // highest parent - add to queue if matches filter
                var issueFromQueue = checkInQueue(deferred);
                $q.when(issueFromQueue).then(function (issue) {
                    if (issue) {
                        // the one that was used to resolve the promise
                        pivottableService.addOnceIfMatches(pivotTable, issue, options).then(function(/*matches*/) {
                            deferred.resolve(issue); // for onParentIssue
                        });
                    } else { // jiratimesheet issue#1000 parent issue not accessible
                        deferred.resovle();
                    }
                });
            }
        }
        return deferred.promise;
    };

    this.addIssue = function(pivotTable, issue) {
        this.allIssues.push(issue);
        /*var pivotEntries =*/ pivotTable.add(issue);
    };

    // TIME-4 -> TIME-3 -> TIME-2 -> TIME-1
    // TIME-3 - skip
    // TIME-2 - skip
    // TIME-1 - mark for add!
    // { TIME-4, TIME-3, TIME-2 promises, TIME-1 issue with all worklogs}
    // TIME-5 -> TIME-1

    // returns issue or parent deferred
    this.getParentIssue = function(pivotTable, issue, options) {
        var parentKey = this.getParentIssueKey(issue, options);
        if (parentKey) {
            return this.getIssueByKey(pivotTable, parentKey, options);
        } else {
            return issue;
        }
    };

    //caches issues in pivotTable.queue
    this.getIssueByKey = function(pivotTable, issueKey, options) {
        var pivottableService = this;
        if (pivotTable.queue.hasOwnProperty(issueKey)) { // known issue
            return pivotTable.queue[issueKey]; // issue is being queued
        } else { // new issue - queue
            var deferred = $q.defer();
            pivotTable.queue[issueKey] = deferred;
            pivottableService.findIssueByKey(issueKey, options, deferred);
            return deferred;
        }
    };

    this.getParentIssueKey = function(issue, options) {
        var configOptions = options.configOptions;
        if (issue.fields.parent) {
            return issue.fields.parent.key;
        } else if (configOptions && configOptions.parentIssueField && issue.fields[configOptions.parentIssueField]) {
            return issue.fields[configOptions.parentIssueField];
        } else if (this.parentLinkField && issue.fields[this.parentLinkField.id] && configOptions.parentIssueType) {
            var parentLink = issue.fields[this.parentLinkField.id];
            if (typeof parentLink == 'object') {
                if (parentLink.data) {
                    return parentLink.data.key;
                } else {
                    return null;
                }
            } else {
                return parentLink; // FIXME: remove old issueKey
            }
        } else if (configOptions && configOptions.compositionIssueLink) {
            return this.getCompositionLinkKey(issue, configOptions);
        } else {
            return null;
        }
    };

    this.getCompositionLinkKey = function(issue, configOptions) {
        var links = issue.fields.issuelinks;
        if (links != null && links.length > 0 /* paranoic && configOptions.compositionIssueLink */) {
            for (var i = 0; i < links.length; i++) {
                // if links[i].outwardIssue issue blocks links[i].outwardIssue
                // if links[i].inwardIssue present - issue is blocked by links[i].inwardIssue
                if (links[i].outwardIssue && links[i].type.name == configOptions.compositionIssueLink){
                    return links[i].outwardIssue.key;
                }
            }
        }
        return null;
    };

    this.resolveParent = function(pivotTable, issue, deferred, options, parent) {
        this.onParentIssue(pivotTable, issue, options, parent, [], function(data) {
            issue.fields.parent = data ? data : issue; // self
            deferred.resolve();
        });
    };

    this.initMatches = function(options) {
        if (!this.matches) {
            var jqlQueryBuilder = TimesheetUtils.getSearchQueryBuilder(options);
            var jqlQuery = jqlQueryBuilder.build();
            if (jqlQuery && (options.jql || options.filterIds.length > 0)) {
                var query = "fields=~&maxResults=1000&jql=" + encodeURIComponent(jqlQuery); // exclude all fields
                this.matchIssueIds = [];
                this.matches = this.onAllIssues(query, function(data) {
                    for (var i in data.issues) {
                        var issue = data.issues[i];
                        this.matchIssueIds.push(issue.id);
                    }
                }.bind(this));
                this.matches.catch(function () {
                    // Possibly unhandled rejection
                });
            } else { // do not search for all issues
                var def = $q.defer();
                this.matches = def.promise;
                def.resolve();
                delete this.matchIssueIds;
            }
        }
    };

    this.processChildren = function (pivotTable, issue, options, previousIssues) {
        if (!this.isProcessChildren(options)) {
            return null;
        }

        if (previousIssues.indexOf(issue.key) < 0) {
            previousIssues.push(issue.key);
        } else {
            return null;
        }
        var subtasks = [];
        var service = this;
        var epicPromise = null;
        var parentPromise = null;
        var moreFields = options.moreFields && angular.isArray(options.moreFields) ? options.moreFields.join(',') + ',' : '';
        if (issue.fields.issuetype.name == 'Epic' && this.epicLinkField) {
            var jqlQueryBuilder = new TimesheetUtils.queryBuilder(" and ");
            if (options.childrenFilterId) {
                jqlQueryBuilder.addQuery('filter', options.childrenFilterId);
            }
            jqlQueryBuilder.addQuery(quote(this.epicLinkField.clauseNames[0]), issue.key);
            var jqlQuery = jqlQueryBuilder.build();
            if (jqlQuery.length != 0) {
                var query = "fields=project,issuetype,resolution,summary,priority,status,parent,issuelinks,timeoriginalestimate,timeestimate,timespent," + moreFields + this.epicLinkField.id + (this.parentLinkField ? ',' + this.parentLinkField.id : '') + ",subtasks&maxResults=1000&jql=" + encodeURIComponent(jqlQuery);
                var epicDeferred = $q.defer();
                epicPromise = epicDeferred.promise;
                var epicPromises = [];
                var epicIssuesPromise = service.onAllIssues(query, function (data) {
                    data.issues.forEach(function (child) {
                        child.fields.worklog = { total: 0, maxResults: 0, worklogs: [] }; // we need numeric fields only
                        var $epicIssues = /* nullable */service.processChildren(pivotTable, child, options, previousIssues);
                        if ($epicIssues) {
                            epicPromises.push($epicIssues);
                            $epicIssues.then(function() {
                                service.addWorklogs(child, issue, options); // sum numeric fields
                            });
                        }
                    });
                });
                epicIssuesPromise.then(function () {
                    // wait for all children
                    $q.all(epicPromises).then(epicDeferred.resolve);
                });
            }
        } else if (issue.fields.issuetype.id == options.configOptions.parentIssueType && this.parentLinkField) {
            var jqlQueryBuilder = new TimesheetUtils.queryBuilder(" and ");
            if (options.childrenFilterId) {
                jqlQueryBuilder.addQuery('filter', options.childrenFilterId);
            }
            jqlQueryBuilder.addQuery(quote(this.parentLinkField.clauseNames[0]), issue.key);
            var jqlQuery = jqlQueryBuilder.build();
            if (jqlQuery.length != 0) {
                var query = "fields=project,issuetype,resolution,summary,priority,status,parent,issuelinks,timeoriginalestimate,timeestimate,timespent," + moreFields + (this.epicLinkField ? this.epicLinkField.id + ',' : '') + this.parentLinkField.id + ",subtasks&maxResults=1000&jql=" + encodeURIComponent(jqlQuery);
                var parentDeferred = $q.defer();
                parentPromise = parentDeferred.promise;
                var parentPromises = [];
                var parentIssuesPromise = service.onAllIssues(query, function (data) {
                    data.issues.forEach(function (child) {
                        child.fields.worklog = { total: 0, maxResults: 0, worklogs: [] }; // we need numeric fields only
                        var $parentIssues = /* nullable */service.processChildren(pivotTable, child, options, previousIssues);
                        if ($parentIssues) {
                            parentPromises.push($parentIssues);
                            $parentIssues.then(function () {
                                service.addWorklogs(child, issue, options); // sum numeric fields
                            });
                        }
                    });
                });
                parentIssuesPromise.then(function () {
                    // wait for all children
                    $q.all(parentPromises).then(parentDeferred.resolve);
                });
            }
        }

        for (var i in issue.fields.subtasks) {
            var subtask = issue.fields.subtasks[i];
            subtasks.push(subtask.key);
        }
        // getCompositionLinkKeys inward!
        var links = issue.fields.issuelinks;
        var configOptions = options.configOptions;
        if (links != null && links.length > 0 && configOptions.compositionIssueLink) {
            for (var i = 0; i < links.length; i++) {
                // if links[i].outwardIssue issue blocks links[i].outwardIssue
                // if links[i].inwardIssue present - issue is blocked by links[i].inwardIssue
                if (links[i].inwardIssue && links[i].type.name == configOptions.compositionIssueLink) {
                    var subtask = links[i].inwardIssue;
                    subtasks.push(subtask.key);
                }
            }
        }
        if (subtasks.length > 0) {
            var subtasksDeferred = $q.defer();
            if (subtasks.length > 0) {
                var subtasksQueryBuilder = new TimesheetUtils.queryBuilder(" and ");
                if (options.childrenFilterId) {
                    subtasksQueryBuilder.addQuery('filter', options.childrenFilterId);
                }
                subtasksQueryBuilder.addQuery('key', "(" + subtasks.join() + ")", " in ");
                var subtasksJql = subtasksQueryBuilder.build();
                var query = "fields=project,issuetype,resolution,summary,priority,status,parent,issuelinks,timeoriginalestimate,timeestimate," + moreFields + "timespent&maxResults=1000&jql=" + encodeURIComponent(subtasksJql);
                var subtasksPromises = [epicPromise || parentPromise];
                var promise = service.onAllIssues(query, function (data) {
                    data.issues.forEach(function (child) {
                        child.fields.worklog = { total: 0, maxResults: 0, worklogs: [] }; // we need numeric fields only
                        var $subtaskChildren = /* nullable */service.processChildren(pivotTable, child, options, previousIssues);
                        if ($subtaskChildren) {
                            subtasksPromises.push($subtaskChildren);
                            $subtaskChildren.then(function () {
                                service.addWorklogs(child, issue, options); // sum numeric fields
                            });
                        }
                    });
                });
                promise.then(function () {
                    // wait for all children
                    $q.all(subtasksPromises).then(subtasksDeferred.resolve);
                });
            } else {
                subtasksDeferred.resolve();
            }
            return subtasksDeferred.promise;
        }
        return epicPromise || parentPromise;
    };

    this.checkIfMatches = function(issue, options) {
        if (!this.matches) { // for tests
            this.initMatches(options);
        }
        return this.matches.then(function(/*array of arrays of issues */) {
            var m = this.matchIssueIds == null;
            m = m && (options.projectKeys == null || options.projectKeys.length == 0 || options.projectKeys.indexOf(issue.fields.project.key) >= 0);
            m = m || (this.matchIssueIds != null && this.matchIssueIds.indexOf(issue.id) != -1);
            return m;
        }.bind(this));
    };

    this.addOnceIfMatches = function(pivotTable, issue, options) {
        var self = this;
        var match = pivotTable.matches[issue.key];
        if (!match) {
            match = pivotTable.matches[issue.key] = $q.defer();
            this.checkIfMatches(issue, options).then(function(matches) {
                if (matches) {
                    var promises = [];
                    self.getEpicFields(pivotTable, issue, options, promises);
                    var promise = /* nullable */ self.processChildren(pivotTable, issue, options, []);
                    promises.push(promise);
                    $q.all(promises).then(function() {
                        var promiseWorklog = self.loadAllWorklogs(issue, null, options).promise;
                        var promiseChangelog = self.loadAllChangelogs(issue, options).promise;
                        $q.all([promiseWorklog, promiseChangelog]).then(function () {
                            // queue for addition issue that was finally resolved with all changelogs, worklogs and filtered
                            pivotTable.queueToAdd.push(issue);
                            match.resolve(issue);
                        });
                    });
                } else {
                     // not queued for add
                    match.resolve();
                }
            }).catch(function () {
                // not queued for add
                match.resolve();
            });
        }
        return match.promise;
    };

    // add worklogs to highest parent
    this.addWorklogsToParent = function(pivotTable, issue, deferred, options, parent) {
        var pivottableService = this;
        this.onParentIssue(pivotTable, issue, options, parent, [], function(data) {
            if (!data) {
                data = issue; // cycle
            }
            // load all parent worklogs before adding child worklogs
            pivottableService.addOnceIfMatches(pivotTable, data, options).then(
                function(matches) { // parent with all worklogs loaded
                    if (matches && issue.key != data.key) {
                        pivottableService.addWorklogs(issue, data, options);
                    }
                    deferred.resolve();
                });
        });
        return deferred.promise;
    };

    this.onParentIssue = function(pivotTable, issue, options, parent, previous, callback) {
        var pivottableService = this;
        return parent.promise.then(function(data) {
            if (!data) { // jiratimesheet issue#1000 parent issue not accessible
                logger.debug("Parent issue for " + issue.key + " is not visible");
                callback(issue); // last visible parent
            } else if (previous.indexOf(data.key) >= 0) {
                logger.debug("Cycle summing subtasks for issue " + issue.key, previous);
                callback();
            } else {
                previous.push(data.key);
                var result = pivottableService.getParentIssue(pivotTable, data, options);
                if (result.hasOwnProperty('promise')) {
                    return pivottableService.onParentIssue(pivotTable, issue, options, result, previous, callback);
                } else {
                    callback(data);
                }
            }
        });
    };

    this.addWorklogs = function(source, target, options) {
        var sourceWorklog = source.worklog || source.fields.worklog;
        var targetWorklog = target.worklog || target.fields.worklog;
        // merge worklogs
        for (var i in sourceWorklog.worklogs) {
            delete source.worklog;
            delete source.fields.worklog;
            sourceWorklog.worklogs[i]._issue = source;
            targetWorklog.worklogs.push(sourceWorklog.worklogs[i]);
        }
        // sum numeric fields only once
        if (!target._subTaskIds) {
            target._subTaskIds = [];
            target._subTasks = [];
        }
        var sourceId = parseInt(source.id);
        if (target._subTaskIds.indexOf(sourceId) >= 0) {
            return; // already added by processChildren or by processIssues
        } else {
            target._subTaskIds.push(sourceId);
            target._subTasks.push(source);
            if (source._subTasks) {
                target._subTasks = target._subTasks.concat(source._subTasks);
            }
            if (source._subTaskIds) {
                target._subTaskIds = target._subTaskIds.concat(source._subTaskIds);
            }
        }
        if (['TimeTracking', 'TimeBalance'].indexOf(options.pivotTableType) >= 0 || options.includeEmpty) {
            return; // Strategy adds sub entries
        }
        for (var issueField in source.fields) {
            var field = source.fields[issueField];
            if (field && typeof field == 'number') {
                target.fields[issueField] = (target.fields[issueField] || 0) + field;
            }
        }
    };

    this.restrictedByAuditorsGroups = function(loggedInUser) {
        var deferred = $q.defer();
        configurationService.getConfiguration().then(function (config) {
            var auditorsGroups = config.auditorsGroups.val;
            var restricted = auditorsGroups != null; // for empty auditors groups
            if (restricted) {
                for (var i = 0; i < loggedInUser.groups.items.length && restricted; i++) {
                    restricted = auditorsGroups.indexOf(loggedInUser.groups.items[i].name) == -1;
                }
            }
            deferred.resolve(restricted);
        });
        return deferred.promise;
    };

    this.restrictedByAuditorsRoles = function(issue, roles) {
        var project = issue.fields.project.key;
        if (!this.$restrictedProjects.hasOwnProperty(project)) {
            var url = '/api/restricted/' + project + '?roles=' + roles + '&_=' + Date.now();
            this.$restrictedProjects[project] = $http.get(url).then(function (response) {
                if (response.data) {
                    this.restrictedProjects.push(issue.fields.project.key);
                }
                return response.data;
            }.bind(this)).catch(function () {
                this.restrictedProjects.push(issue.fields.project.key);
            }.bind(this));
        }
        return this.$restrictedProjects[project];
    };

    this.getPermissions = function(query) {
        if (!this.permissions.hasOwnProperty(query)) {
            var deferred = $q.defer();
            this.permissions[query] = deferred.promise;
            var promises = this.getPermissionsPromises;
            var previous = promises.length < MAX_BULK_LENGTH ? 0 : promises[promises.length - MAX_BULK_LENGTH];
            promises.push(deferred.promise);
            var service = this;
            $q.when(previous).then(function() { // not more than MAX_BULK_LENGTH concurrent requests
                AP.request({
                    url: '/rest/api/2/mypermissions' + query,
                    dataType: 'json',
                    success: function (data) {
                        data = TimesheetUtils.getJson(data);
                        deferred.resolve(data.permissions);
                    },
                    error: function () {
                        var args = Array.prototype.slice.call(arguments);
                        args.push(query, deferred);
                        service.onError.apply(service, args);
                    }
                });
            });
        }
        return this.permissions[query];
    };

    this.havePermissions = function(permissionsReq /*, 'projectKey=DEMO', 'issueKey=DEMO-1' */) {
        var query = '?' + [].slice.call(arguments, 1).join('&');
        return this.getPermissions(query).then(function (permissions) {
            var res = [];
            for (var  i = 0; i < permissionsReq.length; i++) {
                res.push(permissions[permissionsReq[i]] != null && permissions[permissionsReq[i]].havePermission == true);
            }
            return res;
        });
    };

    this.isAdministrator = function() {
        return this.havePermissions(['ADMINISTER']).then(function(permissions) {
            return permissions[0];
        });
    };

    this.isProjectAdmin = function (projectKey) {
        return this.havePermissions(['PROJECT_ADMIN'], 'projectKey=' + projectKey).then(function(permissions) {
            return permissions[0];
        });
    };

    this.findIssueByKey = function (key, options, deferred) {
        var promises = this.findIssueByKeyPromises;
        var previous = promises.length < MAX_BULK_LENGTH ? 0 : promises[promises.length - MAX_BULK_LENGTH];
        if (!deferred) {
            deferred = $q.defer();
        }
        promises.push(deferred.promise);
        var service = this;
        $q.when(previous).then(function() { // not more than MAX_BULK_LENGTH concurrent requests
            if (!deferred.promise.$$state || deferred.promise.$$state.status == 0) {
                var url = '/rest/api/2/issue/' + key + '?fields=' + options._fields;
                if (['IssueWorkedTimeByStatus', 'IssuePassedTimeByStatus', 'TimeBalance',
                        'IssueTimeBalance','IssuePassedTimeByResolution'].indexOf(options.pivotTableType) >= 0) {
                    url += '&expand=changelog';
                }
                AP.request({
                    retryCount: RETRY_COUNT,
                    url: url,
                    dataType: 'json',
                    success: function(data) {
                        var issue = TimesheetUtils.getJson(data);
                        if (issue.fields && !issue.fields.worklog) {
                            issue.fields.worklog = {total: 0, maxResults: 0, worklogs: []};
                        }
                        deferred.resolve(issue);
                    },
                    error: function(jqXHR) {
                        if (jqXHR && [403, 404].indexOf(jqXHR.status) >= 0) {
                            deferred.resolve(); // jiratimesheet issue#1000/issue#1164 issue not accessible
                        } else {
                            var args = Array.prototype.slice.call(arguments);
                            args.push(url, deferred);
                            service.onError.apply(service, args);
                        }
                    }
                });
            }
        });
        return deferred.promise;
    };

    this.getIssueWorklog = function(issueKey, issueId) {
        if (this.cacheWorklog) {
            var issueWorklog = window.localStorage.getItem('i' + issueId);
            if (issueWorklog) {
                this.updateCacheExpiry(function(cacheExpiry) {
                    cacheExpiry[issueId] = logger._; // session id
                });
                return $q.resolve(TimesheetUtils.getJson(LZString.decompressFromUTF16(issueWorklog)));
            }
        }
        var deferred = $q.defer();
        var promises = this.issueWorklogs;
        var previous = promises.length < MAX_BULK_LENGTH ? 0 : promises[promises.length - MAX_BULK_LENGTH];
        promises.push(deferred.promise);
        var service = this;
        $q.when(previous).then(function() { // not more than MAX_BULK_LENGTH concurrent requests
            AP.request({
                retryCount: RETRY_COUNT,
                url: '/rest/api/2/issue/' + issueKey + '/worklog',
                dataType: 'json',
                success: function(data) {
                    var worklog = TimesheetUtils.getJson(data);
                    if (service.cacheWorklog) try {
                        window.localStorage.setItem('i' + issueId, LZString.compressToUTF16(data)); // JSON string
                        worklog.worklogs.forEach(function(entry) {
                            window.localStorage.setItem('w' + entry.id, issueId);
                        });
                        service.updateCacheExpiry(function(cacheExpiry) {
                            cacheExpiry[issueId] = logger._; // session id
                        });
                    } catch (err) {
                        // TODO: expire old data and try again
                        $log.log('cache is full', issueId, err);
                    }
                    deferred.resolve(worklog);
                },
                error: function () {
                    deferred.reject();
                }
            });
        });
        return deferred.promise;
    };

    this.filterWorklogs = function(issue, deferred, options) {
        var promises = [];
        var service = this;
        var issueWorklog = issue.worklog || issue.fields.worklog;
        if (issueWorklog.worklogs && issueWorklog.worklogs.length > 0) {
            angular.forEach(issueWorklog.worklogs, function(worklog) {
                var tags = TimesheetUtils.getTags(worklog.comment);
                if (tags && tags.length > 0) {
                    angular.forEach(tags, function(tag) {
                        if (service.workLabels[tag]) {
                            service.workLabels[tag]++;
                        } else {
                            service.workLabels[tag] = 1;
                        }
                    });
                }
                if (service.groups || service.excludeGroups || service.needUserInfos) {
                    var username = worklog.author ? worklog.author.accountId || worklog.author.key || worklog.author.name || worklog.author : ''; // key || name
                    if (!service.userGroups.hasOwnProperty(username)) {
                        var userGroups = service.userGroups[username] = service.getUserInfo(username, worklog.author && worklog.author.accountId ? '' :  worklog.author && worklog.author.key ? 'key' : 'username');
                        promises.push(userGroups);
                        userGroups.then(service.initWorklogAuthors.bind(service));
                    } else if (!service.userGroups[username].$$state
                                || service.userGroups[username].$$state.status == 0) {
                        // wait for previous call (condition may be safely removed, if causes problems)
                        promises.push(service.userGroups[username]);
                    }
                }
            });
        }
        if (options.configOptions.auditorsRoles) {
            var $restricted = service.restrictedByAuditorsRoles(issue, options.configOptions.auditorsRoles);
            promises.push($restricted);
        }

        if (promises.length == 0 && !deferred) {
            return issue;
        }
        var deferred = deferred || $q.defer();
        $q.all(promises).then(function() {
            deferred.resolve(issue);
        });
        return deferred;
    };

    this.initAuthors = function (listId, user) {
        if (!user || !user.groups) {
            return;
        }
        if (this.excludeGroups) {
            for (var i = 0; i < user.groups.items.length; i++) {
                if (this.excludeGroups.indexOf(user.groups.items[i].name) >= 0) {
                    return;
                }
            }
        }
        if (this.needUserInfos) {
            if (this.groups) {
                user.groups.items = user.groups.items.filter(function (group) {
                    return (this.groups.indexOf(group.name) >= 0);
                }.bind(this));
            }
            this.userInfos[user.accountId] = user;
        }
        if (this.groups) {
            for (var i = 0; i < user.groups.items.length; i++) {
                if (this.groups.indexOf(user.groups.items[i].name) >= 0) {
                    this[listId].push(user.accountId);
                    break;
                }
            }
        } else if (this.excludeGroups) {
            this[listId].push(user.accountId);
        }
    };

    this.initWorklogAuthors = function (user) {
        this.initAuthors('worklogAuthors', user);
    };

    this.loadAllWorklogs = function(issue, deferred, options) {
        var service = this;
        var worklog = issue.worklog || issue.fields.worklog;
        if (!worklog) {
            worklog = issue.fields.worklog = {total: 1, maxResults: 0, worklogs: []}; // force load worklog
        }
        if (worklog.total > worklog.maxResults) {
            var def = deferred || $q.defer();
            var copyAndFilterWorklogs = function(data) {
                angular.copy(data, worklog);
                service.filterWorklogs(issue, def, options);
                return data;
            };
            this.getIssueWorklog(issue.key, issue.id).then(copyAndFilterWorklogs, def.reject);
            return def;
        } else {
            // all were loaded
            return this.filterWorklogs(issue, deferred, options); // issue or deferred (new or existing)
        }
    };

    this.filterChangelogs = function(issue, deferred/*, options*/) {
        var promises = [];
        var service = this;
        var issueChangelog = issue.changelog;
        if (issueChangelog.histories && issueChangelog.histories.length > 0) {
            angular.forEach(issueChangelog.histories, function(changelog) {
                if (service.groups || service.excludeGroups || service.needUserInfos) {
                    var username = changelog.author ? changelog.author.accountId || changelog.author.key || changelog.author.name || changelog.author : ''; // key || name
                    if (!service.userGroups.hasOwnProperty(username)) {
                        var userGroups = service.userGroups[username] = service.getUserInfo(username);
                        promises.push(userGroups);
                        userGroups.then(service.initChangelogAuthors.bind(service));
                    } else if (!service.userGroups[username].$$state
                        || service.userGroups[username].$$state.status == 0) {
                        // wait for previous call (condition may be safely removed, if causes problems)
                        promises.push(service.userGroups[username]);
                    }
                }
            });
        }

        if (promises.length == 0 && !deferred) {
            return issue;
        }
        deferred = deferred || $q.defer();
        $q.all(promises).then(function() {
            deferred.resolve(issue);
        });
        return deferred;
    };

    this.initChangelogAuthors = function (user) {
        this.initAuthors('changelogAuthors', user);
    };

    this.getIssueChangelog = function (issue, startAt, nextPage) {
        var deferred = $q.defer();
        var self = this;
        var url = nextPage || '/rest/api/2/issue/' + issue.key + '/changelog?startAt=' + startAt;
        AP.request({
            retryCount: RETRY_COUNT,
            url: url,
            dataType: 'json',
            success: function (data) {
                var log = TimesheetUtils.getJson(data);
                if (!log.values) {
                    deferred.reject();
                }
                Array.prototype.push.apply(issue.changelog.histories, log.values);
                if (log.isLast) {
                    deferred.resolve(issue);
                } else {
                    self.getIssueChangelog(issue, log.startAt + log.maxResults, log.nextPage).then(deferred.resolve, deferred.reject);
                }
            },
            error: function () {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                self.onError.apply(self, args);
            }
        });
        return deferred.promise;
    };

    this.loadAllChangelogs = function (issue, options) {
        var service = this;
        var changelog = issue.changelog;
        if (!changelog) {
            return {};
        }
        var def = $q.defer();
        if (changelog.total > changelog.maxResults) {
            var filterChangelogs = function(data) {
                service.filterChangelogs(issue, def, options);
                return data;
            };
            var self = this;
            var promises = this.issueChangelogs;
            var previous = promises.length < MAX_BULK_LENGTH ? 0 : promises[promises.length - MAX_BULK_LENGTH];
            promises.push(def.promise);
            if (changelog.histories.length > 1 // JRACLOUD-67458
                    && changelog.histories[0].created > changelog.histories[1].created) {
                changelog.histories.splice(0, changelog.histories.length);
                changelog.maxResults = 0;
            }
            $q.when(previous).then(function() {
                self.getIssueChangelog(issue, changelog.maxResults).then(filterChangelogs, def.reject);
            });
        } else {
            this.filterChangelogs(issue, def, options);
            TimesheetUtils.sortByProperty(changelog.histories, 'created'); // JRACLOUD-67458
        }
        return def;
    };

    this.timeFieldsToAdd = function (options) {
        if (['TimeTracking', 'TimeBalance'].indexOf(options.pivotTableType) >=0
            || this.isTimeTrackingColumnsInMoreFields(options.moreFields)) {
            return ',' + this.timeFields.join(',');
        }
        return "";
    };

    this.isTimeTrackingColumnsInMoreFields = function (moreFields) {
        return moreFields && angular.isArray(moreFields) && TimesheetUtils.findByCondition(function (value) {
            return value.indexOf('timetracking') == 0; // no startsWith in IE11
        }, moreFields);
    };

    this.getMoreFields = function (options) {
        var moreFields = options.moreFields && angular.isArray(options.moreFields) ? options.moreFields.slice() : [];
        if (options.groupByField) moreFields.unshift(options.groupByField);
        if (options.categorizeByField) moreFields.unshift(options.categorizeByField);
        if (options.orderByField) moreFields.unshift(options.orderByField);
        if (this.isTimeTrackingColumnsInMoreFields(moreFields)) {
            Array.prototype.push.apply(moreFields, this.timeFields.filter(function (timeField) {
                return moreFields.indexOf(timeField) < 0;
            }));
        }
        return moreFields;
    };

    this.getEpicFields = function (pivotTable, issue, options, promises) {
        var moreFields = this.getMoreFields(options);
        moreFields.forEach(function (moreField) {
            if (this.isEpicField(moreField)) {
                var promise = this.getEpic(pivotTable, issue, options);
                if (promise) {
                    promises.push(promise);
                    promise.then(function (epic) {
                        if (epic) {
                            issue.fields[moreField] = /* moreField == this.epicLinkField.id ? epic.key : */ epic.fields[moreField];
                            // uncoment and add gh-epic-link below in isEpicField()
                            // when Lawrence Lachman wants to include sub-tasks
                        }
                    });
                }
            }
        }.bind(this));
    };

    this.isEpicField = function(fieldName) {
        if (!this.epicFields.hasOwnProperty(fieldName)) {
            for (var i = 0; i < this.allFields.length; i++) {
                var field = this.allFields[i];
                if (field.id == fieldName) {
                    return this.epicFields[fieldName] = field.custom && field.schema != null
                        // TODO: more epic field
                        && ["com.pyxis.greenhopper.jira:gh-epic-color",
                        "com.pyxis.greenhopper.jira:gh-epic-status",
                        "com.pyxis.greenhopper.jira:gh-epic-label"].indexOf(field.schema.custom) >= 0;
                }
            }
            this.epicFields[fieldName] = false;
        }
        return this.epicFields[fieldName];
    };

    this.initEpicLinkField = function() {
        if (!this.hasOwnProperty('epicLinkField')) {
            this.epicLinkField = null;
            for (var i = 0; i < this.allFields.length; i++) {
                var field = this.allFields[i];
                if (field.custom && field.schema != null) {
                    if (field.schema.custom == "com.pyxis.greenhopper.jira:gh-epic-link") {
                        this.epicLinkField = field;
                    } else if (field.schema.custom == "com.atlassian.jpo:jpo-custom-field-parent") {
                        this.parentLinkField = field;
                    }
                    if (this.epicLinkField && this.parentLinkField) {
                        break;
                    }
                }
            }
        }
    };

    this.getEpic = function(pivotTable, issue, options) {

        if (!this.epicLinkField) return null;

        var result = this.getParentIssue(pivotTable, issue, options);
        if (result.hasOwnProperty('promise')) {
            var deferred = $q.defer();
            var pivottableService = this;
            this.onParentIssue(pivotTable, issue, options, result, [], function(data) {
                if (data && data.fields.issuetype.name == 'Epic') { // Parent Issue Field configured
                    deferred.resolve(data);
                } else if (data && data.fields[pivottableService.epicLinkField.id]) {
                    pivottableService.getIssueByKey(pivotTable, data.fields[pivottableService.epicLinkField.id], options).promise.then(deferred.resolve);
                } else {
                    deferred.resolve();
                }
            });
            return deferred.promise;
        } else if (issue.fields[this.epicLinkField.id]) {
            return this.getIssueByKey(pivotTable, issue.fields[this.epicLinkField.id], options).promise;
        } else {
            return null;
        }
    };

    this.onError = function(jqXHR, type, error, url, deferred) {
        if (jqXHR && jqXHR.status == 401) {
            this.loginExpired();
        } else if (jqXHR && [500, 502, 503, 504].indexOf(jqXHR.status) >= 0) {
            this.serviceUnavailable();
        } else if (jqXHR && jqXHR.status == 403) {
            this.forbidden();
        } else {
            this.unexpectedError(jqXHR, error, url);
        }
        deferred.reject();
    };

    this.loginExpired = function() {
        AP.flag.create({
            title: 'Jira session expired',
            body: 'Please login again to restore connection with Jira',
            type: 'error'
        });
    };

    this.serviceUnavailable = function() {
        AP.flag.create({
            title: 'Jira Unavailable',
            body: 'The requested resource failed to load. If the problem persists, please contact Atlassian Cloud Support.',
            type: 'error'
        });
    };

    this.forbidden = function() {
        AP.flag.create({
            title: 'Forbidden',
            body: 'You need to accept WRITE and DELETE scopes for the app in UPM',
            type: 'error'
        });
    };

    this.unexpectedError = function(jqXHR, error, cause) {
        function strip(html) {
           var tmp = document.createElement("DIV");
           tmp.innerHTML = html;
           return tmp.textContent || tmp.innerText || "";
        }
        var body = '';
        if (jqXHR && jqXHR.responseText) {
            try {
                var data = TimesheetUtils.getJson(jqXHR.responseText);
                body = data.errorMessages ? data.errorMessages.join(' ') : 'Unknown';
                if (data.errors) {
                    body += '\n' + JSON.stringify(data.errors);
                }
                data.status = jqXHR.status;
                // FIXME: status == 400 || 404
                /* it logs too many errors, reasons of which are still not clear, e.g.:
                Issue Does Not Exist - /rest/api/2/issue/undefined/worklog/undefined?adjustEstimate=auto
                A value with ID \'22854\' does not exist for the field \'filter\' - /rest/api/2/search?...jql=filter=22854...
                Cannot find worklog with id: \'69756\' -
                Worklog must not be null - timeLogged: 'Invalid time duration entered -
                */
                //logger.error(data, cause);
            } catch(err) {
                body = cause + ' ' + jqXHR.status + ' ' + strip(jqXHR.responseText);
                //logger.error(jqXHR.status + ' ' + body, cause);
            }
        } else {
            body = error;
            // it logs too many useless errors for straight forward REST calls
            //logger.error(error, cause);
        }
        AP.flag.create({
            title: 'Unexpected error',
            body: body,
            type: 'error'
        });
    };

    this.getGadgetConfiguration = function(dashboardId, dashboardItemId) {
        var service = this;
        var deferred = $q.defer();
        AP.request({
            url: '/rest/api/2/dashboard/' + dashboardId + '/items/' + dashboardItemId + '/properties',
            success: function(data) {
                data = TimesheetUtils.getJson(data);
                if (data.keys.length > 0) { // assume it has DASHBOARD_PROPERTY_GADGET_CONFIG
                    AP.request({
                        url: '/rest/api/2/dashboard/' + dashboardId + '/items/' + dashboardItemId + '/properties/' + DASHBOARD_PROPERTY_GADGET_CONFIG,
                        success: function(data) {
                            data = TimesheetUtils.getJson(data).value;
                            if (data.user && !TimesheetUtils.isUuid(data.user)) {
                                service.getUserInfo(data.user, 'key').then(function (user) {
                                    if (user && user.accountId) {
                                        data.user = user.accountId;
                                        service.saveGadgetConfiguration(dashboardId, dashboardItemId, data)
                                            .catch(function () {
                                                // ignore
                                            });
                                    }
                                    deferred.resolve(data);
                                }).catch(function () {
                                    deferred.resolve(data);
                                })
                            } else {
                                deferred.resolve(data);
                            }
                        },
                        error: function () {
                            deferred.resolve({});
                        }
                    });
                } else {
                    deferred.resolve({});
                }
            },
            error: function () {
                deferred.resolve({});
            }
        });
        return deferred.promise;
    };

    this.saveGadgetConfiguration = function(dashboardId, dashboardItemId, configuration) {
        var deferred = $q.defer();
        AP.request({
            url: '/rest/api/2/dashboard/' + dashboardId + '/items/' + dashboardItemId + '/properties/' + DASHBOARD_PROPERTY_GADGET_CONFIG,
            type: 'PUT',
            contentType: 'application/json',
            headers: {
                "Accept": "application/json"
            },
            data: JSON.stringify(configuration),
            success: function(data) {
                deferred.resolve(TimesheetUtils.getJson(data));
            },
            error: function () {
                deferred.reject();
            }
        });
        return deferred.promise;
    };

    this.saveWorklog = function (worklog, configOptions, jiraConfig) {
        return worklog.id ? this.updateWorklog(worklog) : this.createWorklog(worklog, configOptions, jiraConfig);
    };

    this.createWorklog = function(worklog, configOptions, jiraConfig) {
        var saveWorklog = function (worklog) {
            var deferred = $q.defer();
            service.createWorklogPromise = service.createWorklogPromise.then(function () { // not more than 1 concurrent request
                var worklogData = service.getCreateWorklogRequest(worklog);
                if (worklog.user) {
                    $http.post('/logWork', {
                        url: url,
                        json: JSON.parse(worklogData),
                        user: worklog.user.accountId
                    }).then(function (res) {
                        var data = res.data;
                        if (res.status === 200) {
                            service.removeWorklogFromCache(data); // is also removed by clearWorklogCache but late
                            deferred.resolve(data);
                        } else {
                            throw Error();
                        }
                    }).catch(function () {
                        deferred.reject();
                        AP.flag.create({
                            title: 'Unexpected error',
                            body: "Can not log work.",
                            type: 'error'
                        });
                    });
                } else {
                    AP.request({
                        url: url,
                        type: 'POST',
                        contentType: 'application/json',
                        headers: {
                            "Accept": "application/json"
                        },
                        data: worklogData,
                        success: function (data) {
                            data = TimesheetUtils.getJson(data);
                            service.removeWorklogFromCache(data); // is also removed by clearWorklogCache but late
                            deferred.resolve(data);
                        },
                        error: function () {
                            var args = Array.prototype.slice.call(arguments);
                            args.push(url, deferred);
                            service.onError.apply(service, args);
                        }
                    });
                }
            });
            return deferred.promise;
        };

        var deferred = $q.defer();
        var service = this;
        var params = this.getEstimateParams(worklog, 'reduceBy');
        var url = '/rest/api/2/issue/' + worklog.issueKey + '/worklog' + params;
        var promises = [];
        var timeSpents = TimesheetUtils.getTimeSpents(worklog.timeSpent, jiraConfig);
        var started = worklog.date;
        var entry, startTime;
        for (var i = 0; i < timeSpents.length; i++) {
            entry = angular.copy(worklog);
            entry.timeSpent = timeSpents[i];
            if (started.getHours() == 0 && started.getMinutes() == 0) {
                startTime = TimesheetUtils.sameDay(started, new Date()) ?
                    Math.max(0, TimesheetUtils.getCurrentTimeMs() - TimesheetUtils.getTimeSpentMs(entry.timeSpent, jiraConfig)) :
                    (configOptions.workingTimeInStatus.from || 9) * 60 * 60 * 1000;
            } else {
                startTime = 0;
            }
            entry.date = new Date(started.getTime() + startTime);
            promises.push(saveWorklog(entry));
            do {
                started = TimesheetUtils.addDays(started, -1);
            } while (TimesheetUtils.isWeekend(started, configOptions))
        }
        $q.all(promises).then(deferred.resolve, deferred.reject);
        return deferred.promise;
    };

    this.getWorklog = function(issueKey, worklogId) {
        var deferred = $q.defer();
        var service = this;
        var url = '/rest/api/2/issue/' + issueKey + '/worklog/' + worklogId;
        AP.request({
            url: url,
            type: 'GET',
            contentType: 'application/json',
            headers: {
                "Accept": "application/json"
            },
            success: function(data) {
                deferred.resolve(TimesheetUtils.getJson(data));
            },
            error: function() {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.updateWorklog = function(worklog) {
        var deferred = $q.defer();
        var service = this;
        var params = this.getEstimateParams(worklog);
        var url = '/rest/api/2/issue/' + worklog.issueKey + '/worklog/' + worklog.id + params;
        AP.request({
            url: url,
            type: 'PUT',
            contentType: 'application/json',
            headers: {
                "Accept": "application/json"
            },
            data: service.getUpdateWorklogRequest(worklog),
            success: function(data) {
                service.removeWorklogFromCache(worklog);  // is also removed by clearWorklogCache but late
                deferred.resolve(TimesheetUtils.getJson(data));
            },
            error: function() {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.deleteWorklog = function(worklog) {
        var deferred = $q.defer();
        var service = this;
        var params = this.getEstimateParams(worklog, 'increaseBy');
        var url = '/rest/api/2/issue/' + worklog.issueKey + '/worklog/' + worklog.id + params;
        AP.request({
            url: url,
            type: 'DELETE',
            contentType: 'application/json',
            success: function() {
                service.removeWorklogFromCache(worklog);  // is also removed by clearWorklogCache but late
                deferred.resolve(true);
            },
            error: function () {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.getEstimateParams = function (worklog, adjustmentType) {
        var params = "?adjustEstimate=" + (worklog.estimateType || 'auto');
        if (worklog.estimateType == 'new') {
            params += "&newEstimate=" + encodeURIComponent(worklog.estimateValue);
        } else if (worklog.estimateType == 'manual') {
            // delete worklog only
            params += "&" + adjustmentType + "=" + encodeURIComponent(worklog.adjustmentAmount);
        }
        return params;
    };

    this.getCreateWorklogRequest = function(worklog) {
        return JSON.stringify({
            comment: worklog.comment,
            started: TimesheetUtils.formatDate(worklog.date),
            timeSpent: worklog.timeSpent,
            visibility: worklog.visibilityGroup ?
                {type: 'group', value: worklog.visibilityGroup} : worklog.visibilityRole ?
                    {type: 'role', value: worklog.visibilityRole} : null
        });
    };

    this.getUpdateWorklogRequest = function(worklog) {
        return JSON.stringify({
            comment: worklog.comment,
            started: worklog.started,
            timeSpent: worklog.timeSpent,
            visibility: worklog.visibilityGroup ?
                {type: 'group', value: worklog.visibilityGroup} : worklog.visibilityRole ?
                    {type: 'role', value: worklog.visibilityRole} : null
        });
    };

    this.onDeletedWorklogs = function(since, callback) {
        var deferred = $q.defer();
        var service = this;
        var url = '/rest/api/2/worklog/deleted?since=' + since;
        AP.request({
            url: url,
            type: 'GET',
            contentType: 'application/json',
            success: function(data) {
                var deletedWorklogs = TimesheetUtils.getJson(data);
                callback(deletedWorklogs);
                if (!deletedWorklogs.lastPage) {
                    service.onDeletedWorklogs(deletedWorklogs.until, callback).then(deferred.resolve);
                } else {
                    deferred.resolve(deletedWorklogs.until + 1); // avoid same last worklog next time
                }
            },
            error: function () {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.onUpdatedWorklogs = function(since, callback) {
        var deferred = $q.defer();
        var service = this;
        var url = '/rest/api/2/worklog/updated?since=' + since;
        AP.request({
            url: url,
            type: 'GET',
            contentType: 'application/json',
            success: function(data) {
                callback(TimesheetUtils.getJson(data)).then(function(updatedWorklogs) {
                    if (!updatedWorklogs.lastPage) {
                        service.onOutdatedWorklogs(updatedWorklogs.until, callback).then(deferred.resolve);
                    } else {
                        deferred.resolve(updatedWorklogs.until + 1); // avoid same last worklog next time
                    }
                });
            },
            error: function () {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.onWorklogs = function(updatedWorklogs, callback) {
        var worklogIds = updatedWorklogs.values.map(function (worklog) {
          return worklog.worklogId;
        });
        if (!worklogIds.length) {
            return $q.resolve(updatedWorklogs);
        }
        var deferred = $q.defer();
        var service = this;
        var url = '/rest/api/2/worklog/list';
        AP.request({
            url: url,
            type: 'POST',
            contentType: 'application/json',
            data: JSON.stringify({ids: worklogIds}),
            success: function(data) {
                callback(TimesheetUtils.getJson(data));
                deferred.resolve(updatedWorklogs);
            },
            error: function () {
                var args = Array.prototype.slice.call(arguments);
                args.push(url, deferred);
                service.onError.apply(service, args);
            }
        });
        return deferred.promise;
    };

    this.removeWorklogFromCache = function(worklog) {
        if (this.cacheWorklog) try {
            var issueId = worklog.issueId || window.localStorage.getItem('w' + worklog.worklogId);
            var i = window.localStorage.getItem('i' + issueId);
            if (i) {
                var issueWorklog = TimesheetUtils.getJson(LZString.decompressFromUTF16(i));
                issueWorklog.worklogs.forEach(function(entry) {
                    window.localStorage.removeItem('w' + entry.id);
                });
                window.localStorage.removeItem('i' + issueId);
                this.updateCacheExpiry(function(cacheExpiry) {
                    delete cacheExpiry[issueId];
                });
            } else if (worklog.id || worklog.worklogId) { // cacheExpiry
                window.localStorage.removeItem('w' + (worklog.id || worklog.worklogId));
            }
        } catch (err) {
            $log.log('Failed to remove worklog ' + (worklog.id || worklog.worklogId || 'for issue ' + worklog.issueId) + ' from cache', err);
        }
    }

    var CACHE_EXPIRY = 'cacheExpiry';

    this.updateCacheExpiry = function(callback) {
        try {
            var cacheExpiry = TimesheetUtils.getJson(window.localStorage.getItem(CACHE_EXPIRY)) || {};
            callback(cacheExpiry);
            window.localStorage.setItem(CACHE_EXPIRY, JSON.stringify(cacheExpiry));
        } catch (err) {
            $log.log('Failed to update cache expiry', err);
        }
    };

    this.clearWorklogCache = function(config) {
        this.cacheWorklog = config.cacheWorklog && config.cacheWorklog.val;
        if (this.cacheWorklog && !this.allIssues) try {
            var cacheId = window.localStorage.getItem('cacheId');
            var id = logger.baseUrl + '#0';
            if (cacheId != id) {
                window.localStorage.clear();
                window.localStorage.setItem('cacheId', id);
            }
            var service = this;
            var updatedSince = window.localStorage.getItem('updatedSince');
            var deletedSince = window.localStorage.getItem('deletedSince');
            var $updatedSince;
            var defaultSince = Date.now() - 3600000; // -1 minute
            if (!updatedSince) {
                $updatedSince = $q.resolve(defaultSince);
            } else {
                $updatedSince = this.onUpdatedWorklogs(parseInt(updatedSince), function(data) {
                    return service.onWorklogs(data, function(worklogs) {
                        worklogs.forEach(service.removeWorklogFromCache.bind(service));
                    });
                });
            }
            var $deletedSince;
            if (!deletedSince) {
                $deletedSince = $q.resolve(defaultSince);
            } else {
                $deletedSince = this.onDeletedWorklogs(parseInt(deletedSince), function(deletedWorklogs) {
                    deletedWorklogs.values.forEach(service.removeWorklogFromCache.bind(service));
                });
            }
            // FIXME: does not include changes during last minute
            return $q.all([$updatedSince, $deletedSince]).then(function(untils) {
                window.localStorage.setItem('updatedSince', untils[0]);
                window.localStorage.setItem('deletedSince', untils[1]);
                });
        } catch (err) {
            $log.log('Failed to clear worklog cache', err);
        }
    };

    this.expireWorklogCache = function() {
        if (this.cacheWorklog) {
            var cacheExpiry = TimesheetUtils.getJson(window.localStorage.getItem(CACHE_EXPIRY));
            if (cacheExpiry) {
                for (var key in cacheExpiry) {
                    if (logger._ - cacheExpiry[key] > 3600000*24*7) { // 1w
                        this.removeWorklogFromCache({issueId: key});
                    }
                }
            }
        }
    };

    this.resetState = function() {
        delete this.epicLinkField;
        delete this.parentLinkField;
        this.allIssues = null;
        this.allFilters = null;
        this.allProjects = null;
        this.allFields = null;
        this.allNumericFields = null;
        this.filterOrProjectId = null;
        this.childrenFilterId = null;
        this.sumSubTasks = null;
        this.includeEmpty = null;
        this.permissions = {};
        this.jiraConfiguration = null;
    };

    this.checkAndUpdateOptions = function(options) {
        var moreFields = this.getMoreFields(options);
        var changelogReports = ['IssueWorkedTimeByStatus', 'IssuePassedTimeByStatus', 'TimeBalance', 'IssuePassedTimeByResolution'];
        var noWorklogReports = ['TimeTracking', 'IssuePassedTimeByStatus', 'TimeBalance', 'IssuePassedTimeByResolution'];
        var changed = this.allIssues == null
            || !angular.equals(this.filterOrProjectId, options.filterOrProjectId)
            || this.childrenFilterId != options.childrenFilterId
            || this.sumSubTasks != options.sumSubTasks
            || this.includeEmpty != options.includeEmpty
            || !angular.equals(this.startDate, options.startDate)
            || !angular.equals(this.endDate, options.endDate)
            || this.username != options.username
            || this.groups != options.groups
            || this.excludeGroups != options.excludeGroups
            || (this.pivotTableType == "TimeTracking" && this.timeTrackingColumns != options.timeTrackingColumns)
            || (this.pivotTableType == "TimeBalance" && this.timeBalanceColumns != options.timeBalanceColumns)
            || (this.pivotTableType == "IssuePassedTimeByStatus" && this.timeInStatusCategories != options.timeInStatusCategories)
            || (changelogReports.indexOf(this.pivotTableType) < 0 && changelogReports.indexOf(options.pivotTableType) >= 0) // reload with changelog
            || (noWorklogReports.indexOf(this.pivotTableType) >= 0 && noWorklogReports.indexOf(options.pivotTableType) <= 0) // reload with worklog
            || (changelogReports.indexOf(this.pivotTableType) >= 0 && this.pivotTableType != options.pivotTableType) // reload with new dateCondition
            || !angular.equals(this.moreFields, moreFields)
            || hasInProgressIssues(this) != hasInProgressIssues(options);

        if (changed) {
            this.allIssues = [];
            this.filterOrProjectId = angular.copy(options.filterOrProjectId);
            this.childrenFilterId = options.childrenFilterId;
            this.sumSubTasks = options.sumSubTasks;
            this.includeEmpty = options.includeEmpty;
            this.startDate = options.startDate;
            this.endDate = options.endDate;
            this.username = options.username;
            this.groups = options.groups;
            this.excludeGroups = options.excludeGroups;
            this.groupByField = options.groupByField;
            this.categorizeByField = options.categorizeByField;
            this.configOptions = options.configOptions;
            this.userGroups = {};
            TimesheetUtils.clearObject(this.userInfos);
            if (this.pivotTableType == "TimeTracking") {
                this.timeTrackingColumns = options.timeTrackingColumns;
            } else if (this.pivotTableType == "TimeBalance") {
                this.timeBalanceColumns = options.timeBalanceColumns;
            } else if (this.pivotTableType == "IssuePassedTimeByStatus") {
                this.timeInStatusCategories = options.timeInStatusCategories;
            }
            this.worklogAuthors.splice(0, this.worklogAuthors.length);
            this.changelogAuthors.splice(0, this.changelogAuthors.length);
            this.matches = null; // checkIfMatches
            this.pivotTableType = options.pivotTableType;
            this.moreFields = moreFields;
            this.issueWorklogs.splice(0, this.issueWorklogs.length);
            this.issueChangelogs.splice(0, this.issueChangelogs.length);
            this.findIssueByKeyPromises.splice(0, this.findIssueByKeyPromises.length);
            this.getPermissionsPromises.splice(0, this.getPermissionsPromises.length);
            this.onAllIssuesPromises.splice(0, this.onAllIssuesPromises.length);
            this.createWorklogPromise = $q.resolve();
            this.getUserInfoPromises.splice(0, this.getUserInfoPromises.length);
            this.$restrictedProjects = {};
            this.restrictedProjects.splice(0, this.restrictedProjects.length);
        }
        return changed;
    };

    this.addDays = function(stringDate, days) {
        if (stringDate == null || stringDate == "") {
            return stringDate;
        }
        var date = TimesheetUtils.addDays(new Date(stringDate), days);
        return TimesheetUtils.formatDateForSearch(date);
    };

    this.sendNow = function(routeParams) {
        return $http.post('/sendNow', routeParams);
    };
}]);
