app.directive('issueField', ['$compile', 'applicationLoggingService', function($compile, logger) {
    return {
        restrict: 'E',
        scope: {
            field: '=',
            name: '=',
            issue: '=',
            once: '=',
            jiraConfig: '=',
            allFields: '='
        },
        link: function (scope, element) {
            var NULL_VALUE = "&nbsp;";
            var isSecondsField = function(field) {
                return ['aggregatetimeoriginalestimate',
                        'aggregatetimeestimate',
                        'aggregatetimespent',
                        'timeoriginalestimate',
                        'timeestimate',
                        'timespent',
                        'originalestimateremaining']
                        .indexOf(field) != -1;
            };
            var sprintRegex = /com\.atlassian\.greenhopper\.service\.sprint\.Sprint@[\d\w]+\[([\s\S]*)?\]/;
            var m;
            var getNameOrKey = function(field) {
                if (angular.isObject(field) && field.hasOwnProperty('name')) {
                    return field.name;
                } else if (angular.isObject(field) && field.hasOwnProperty('value')) {
                    return field.value + (field.hasOwnProperty('child') ? ':' + getNameOrKey(field.child) : '');
                } else if (angular.isObject(field) && field.hasOwnProperty('key')) {
                    var childScope = scope.$new();
                    childScope.issueField = field;
                    childScope.hostBaseUrl = scope.$parent.hostBaseUrl;
                    return $compile('<issue-key issue="issueField" show-summary="' + (['parent', 'issue'].indexOf(scope.name) >= 0) + '"/>')(childScope);
                } else if (angular.isObject(field) && field.hasOwnProperty('hasEpicLinkFieldDependency')) { // Portfolio for Jira
                    if (field.showField && field.data) {
                         return field.data.key;
                    } else {
                        return NULL_VALUE;
                    }
                } else if (angular.isObject(field) && field.hasOwnProperty('weekNumber')) {
                    return "Week " + field.weekNumber;
                } else if (angular.isString(field) && (m = field.match(sprintRegex))) {
                    logger.trace("Sprint field", m[1]);
                    var splits = m[1].split(',');
                    for (var i = 0; i < splits.length; i++) {
                        var split = splits[i];
                        logger.trace("Sprint field split", split);
                        if (split.indexOf('name=') == 0) {
                            return split.substr(5);
                        }
                    }
                    return NULL_VALUE;
                } else if(isSecondsField(scope.name)) {
                    return DrawHelper.getFormattedTime(field, scope.jiraConfig);
                } else {
                    return '<span>' + (angular.isString(field) ? field.replace(/</g, '&lt;') : field) + '</span>';
                }
            };

            if (scope.once && typeof scope.field == 'number') {
                var key = scope.issue.key + '/' + scope.name;
                var showOnce = scope.once.showOnce;
                if (!showOnce) {
                    showOnce = scope.once.showOnce = [];
                }
                if (showOnce.indexOf(key) < 0) {
                    showOnce.push(key);
                } else {
                    return;
                }
            }

            var value;
            if (!scope.field) {
                if (scope.name == 'projectcategory' && scope.issue && scope.issue.fields.project && scope.issue.fields.project.projectCategory) {
                    value = scope.issue.fields.project.projectCategory.name;
                } else if (scope.name == 'timetrackingprogress') {
                    var actual = scope.issue.fields.timespent || 0;
                    var remaining = scope.issue.fields.timeestimate || 0;
                    scope.progress = {estimate: remaining, timespent: actual};
                    value = $compile('<span ng-bind-html="progress | timeTracking"/>')(scope);
                } else if (scope.name == 'timetrackingestimate') {
                    var actual = scope.issue.fields.timespent || 0;
                    var remaining = scope.issue.fields.timeestimate || 0;
                    value = DrawHelper.getFormattedTime(actual + remaining, scope.jiraConfig);
                } else if (scope.name == 'timetrackingvariance') {
                    var original = scope.issue.fields.timeoriginalestimate || 0;
                    var actual = scope.issue.fields.timespent || 0;
                    var remaining = scope.issue.fields.timeestimate || 0;
                    var estimate = actual + remaining;
                    value = DrawHelper.getFormattedTime(original - estimate, scope.jiraConfig);
                } else if (scope.name == 'timetrackingoriginalestimateremaining') {
                    var original = scope.issue.fields.timeoriginalestimate || 0;
                    var actual = scope.issue.fields.timespent || 0;
                    scope.originalestimateremaining = {issue: scope.issue, value: original - actual};
                    value = $compile('<span ng-bind-html="originalestimateremaining | originalEstimateRemainingLozenge:jiraConfig"/>')(scope);
                } else {
                    value = '&nbsp;';
                }
            } else if (/\/rest\/api(\/\d+)\/user/.test(scope.field.self)) {
                // FIXME: code duplication
                var href;
                if (scope.$parent.isGadget || element.injector().modules.serverApp) {
                    var reportParams = angular.copy(scope.$parent.reportParamsObject);
                    delete reportParams.allUsers;
                    delete reportParams.group;
                    delete reportParams.groupByField;
                    reportParams.pivotTableType = 'Timesheet';
                    reportParams.user = scope.field.accountId;
                    scope.reportParams = (typeof $ != 'undefined' ? $ : window).param(reportParams, /* traditional */ true);
                    // CAUTION: malformated string to be inserted between double quotes
                    href = '{{$parent.hostBaseUrl}}/plugins/servlet/ac/timereports/timereports#!{{reportParams}}" target="_parent';
                } else {
                    href = '#!/?pivotTableType=Timesheet&user={{field.accountId}}&allUsers=&group=&groupByField=&_={{$id}}';
                }
                value = $compile('<a ng-mouseenter="$parent.userHoverPopup.init(field)"'
                    + ' ng-class="{inactive: !field.active, disabled: !field.accountId, userHoverPopup: true}"'
                    + ' ng-href="' + href + '" ng-bind="field.displayName || field.name || field.accountId"></a>')(scope);
                // TODO: value = userLink($compile, scope, scope.field);
            } else if (TimesheetUtils.isDate(scope.field)) {
                var fieldObject = TimesheetUtils.findByProperty('id', scope.name, scope.allFields || []);
                var dateFormat = fieldObject && fieldObject.schema.type == 'datetime' ? 'short' : 'shortDate';
                scope.value = new Date(scope.field);
                value = $compile("<span>{{value | date:'" + dateFormat + "'}}</span>")(scope);
            } else if (Array.isArray(scope.field)) {
                value = '';
                if (scope.field.length > 0 && (scope.field[0].inwardIssue || scope.field[0].outwardIssue)) {
                    scope.field.forEach(function(v) {
                        if (v.inwardIssue) {
                            element.append(v.type.inward + '&nbsp;');
                            element.append(getNameOrKey(v.inwardIssue));
                        } else {
                          element.append(v.type.outward + '&nbsp;');
                          element.append(getNameOrKey(v.outwardIssue));
                        }
                        element.append('<br/>');
                    });
                } else {
                    scope.field.forEach(function(v) {
                        element.append(getNameOrKey(v));
                        element.append('<br/>');
                    });
                }
            } else {
                value = getNameOrKey(scope.field);
            }
            element.append(value);
        }
    }
}]);
