app.directive('pivotCell', ['$compile', function ($compile) {
    return {
        restrict: 'E',
        scope: {
            key: '=',
            columns: '=',
            logWork: '=',
            editWork: '=',
            deleteWork: '=',
            jiraConfig: '=',
            removeCachedWorklog: '='
        },
        link: function (scope, element) {
            if (scope.logWork && scope.key.keyValue == '3action') {
                var column = scope.columns[scope.key.keyValue];
                var html;
                if (column.isTotalColumn) {
                    html = "&nbsp;";
                } else if (column.entries) {
                    scope.worklog = {issueKey: column.entries[0].rowKey.keyValue}; // new worklog
                    column.worklog = scope.worklog; // pass to timesheetController#startWork()
                    html = $compile('<span ng-show="!$root.workTimer && key.showStart && !$parent.cannotLogWorkIssue[worklog.issueKey]">' +
                        '   <span><i class="fa fa-play" ng-click="logWork()"></i></span>' +
                        '   <span><i class="fa fa-plus-square" ng-click="$parent.addWorklogDialog(null, worklog.issueKey)"></i></span>' +
                        '</span>')(scope);
                } else if (column.worklog && column.worklog.id) {
                    scope.worklog = column.worklog;
                    scope.worklog.issueKey = column.rowKey.keyValue;
                    html = $compile('<old-worklog worklog="worklog" edit-work="editWork" delete-work="deleteWork" ' +
                    'cannot-edit="$parent.cannotEditOwnWorklogIssue[worklog.issueKey] || $parent.cannotEditAllWorklogIssue[worklog.issueKey]"' +
                    'cannot-delete="$parent.cannotDeleteOwnWorklogIssue[worklog.issueKey] || $parent.cannotDeleteAllWorklogIssue[worklog.issueKey]"/>')(scope);
                } else { // new worklog
                    scope.entry = column;
                    scope.worklog = column.worklog;
                    html = $compile('<new-worklog worklog="worklog" log-work="logWork" entry="entry" remove-cached-worklog = "removeCachedWorklog" />')(scope);
                }
                element.append(html);
            } else if (scope.key.filterName) {
                element.append($compile('<span ng-bind-html="columns[key.keyValue] | ' + scope.key.filterName + '"/>')(scope));
            } else {
                element.append($compile('<span style="color: {{columns[key.keyValue].color}}" title="{{columns[key.keyValue].title}}" ng-bind="columns[key.keyValue] | prettyHours:jiraConfig:$parent.pivotTableType == \'IssuePassedTimeByStatus\' && !($parent.workingTimeInStatus.from && $parent.workingTimeInStatus.to)"/>')(scope));
            }
        }
    }
}]);

app.directive('columnKey', ['$compile', function($compile) {
    return {
        restrict: 'A',
        scope: {
            columnKey: '='
        },
        link: function (scope, element) {
            scope.$watch('columnKey', function() {
                // FIXME: code duplication
                if (scope.columnKey.user) {
                    var href;
                    if (scope.$parent.isGadget || element.injector().modules.serverApp) {
                        var reportParams = angular.copy(scope.$parent.reportParamsObject);
                        delete reportParams.allUsers;
                        delete reportParams.group;
                        reportParams.pivotTableType = 'Timesheet';
                        reportParams.user = scope.columnKey.user.accountId;
                        scope.reportParams = (typeof $ != 'undefined' ? $ : window).param(reportParams, /* traditional */ true);
                        // CAUTION: malformated string to be inserted between double quotes
                        href = '{{$parent.hostBaseUrl}}/plugins/servlet/ac/timereports/timereports#!{{reportParams}}" target="_parent';
                    } else {
                        scope.startDate = TimesheetUtils.formatDateForSearch(scope.columnKey.options.startDate);
                        scope.endDate = TimesheetUtils.formatDateForSearch(scope.columnKey.options.endDate);
                        href = "{{columnKey.user.accountId ? '#!/?pivotTableType=Timesheet&user=' + columnKey.user.accountId + '&startDate=' + startDate + '&endDate=' + endDate + '&group=' + '&_=' + $id : ''}}";
                    }
                    element.html('<a ng-mouseenter="$parent.userHoverPopup.init(columnKey.user)" ' +
                        'ng-class="{inactive: !columnKey.user.active, disabled: !columnKey.user.accountId, userHoverPopup: true}" ' +
                        'ng-href="' + href + '" ng-bind="columnKey.user.displayName || columnKey.user.name || columnKey.user.accountId"></a>');
                    // TODO: element.html(userLink($compile, scope, scope.columnKey.user), /* keepGroupByField */ true);
                } else if (scope.columnKey.date) {
                    element.html("<span>{{columnKey.date | date:'EEE' | i18n}}<br/>{{columnKey.date | date:'dd'}}/{{columnKey.date | date:'MMM' | i18n}}</span>");
                } else if (scope.columnKey.weekNumber) {
                    scope.startDate = scope.columnKey.firstDate || scope.columnKey.startDate;
                    scope.endDateIncluded = TimesheetUtils.addDays(scope.columnKey.endDate, -1);
                    scope.endDate = scope.columnKey.lastDate || scope.endDateIncluded;
                    element.html("<span title=\"{{startDate | date:'d'}} {{startDate | date:'MMM' | i18n}}-{{endDate | date:'d'}} {{endDate | date:'MMM' | i18n}}\">{{'Week' | i18n}}<br/>{{columnKey.weekNumber}}</span>");
                } else if (scope.columnKey.monthKey) {
                    element.html("<span title=\"{{columnKey.endDate | date:'1-d'}} {{columnKey.endDate | date:'MMM' | i18n}}\">{{columnKey.keyValue | date:'MMM' | i18n}}</span>");
                } else {
                    if (['TimeTracking', 'TimeEntry', 'IssueTime', 'Timespent', 'TimeBalance', 'IssueTimeBalance'].indexOf(scope.$parent.pivotTableType) >= 0) {
                        element.html('<span i18n="columnKey.keyValue"></span>');
                    } else if (scope.$parent.pivotTableType == 'Calendar') {
                        element.html('<span i18n="columnKey.day"></span>');
                    } else {
                        element.html("<span>{{columnKey.keyValue}}</span>");
                    }
                }
                $compile(element.contents())(scope);
            });
        }
    }
}]);

