app.directive('worklogComment', ['$compile', function ($compile) {
    return {
        restrict: 'E',
        scope: {
            worklog: '=',
            noComment: '='
        },
        replace: true,
        link: function(scope, element) {
          var truncatedHtml = $compile('<span title="{{worklog.comment.length > 175 ? worklog.comment : \'\'}}" ng-bind="worklog.comment || noComment | limitTo:175"></span>')(scope);
          var fullHtml = $compile('<span ng-bind="worklog.comment"></span>')(scope);
          if (window.matchMedia) {
            var mediaQueryList = window.matchMedia('print');
            mediaQueryList.addListener(function(mql) {
              if (mql.matches) {
                scope.$apply(function() {
                  element.html(fullHtml);
                });
              } else {
                element.html(truncatedHtml);
              }
            });
          }
          element.html(truncatedHtml);
        }
    }
}]);

app.directive('excelViewBak', ['$compile', '$templateCache', '$templateRequest',
        function($compile, $templateCache, $templateRequest) {
    return {
        restrict: 'A',
        scope: {
            excelView: '=',
            pivotTable: '='
        },
        link: function(scope, elem) {
            if (!$templateCache.get(scope.excelView)) {
                $templateRequest(scope.excelView);
            }
            $(elem).click(function() {
                var excelFile = "<html xmlns:o='urn:schemas-microsoft-com:office:office' xmlns:x='urn:schemas-microsoft-com:office:excel' xmlns='http://www.w3.org/TR/REC-html40'>\n";
                excelFile += "<head>\n";
                excelFile += "<!--[if gte mso 9]>\n";
                excelFile += "<xml>\n";
                excelFile += "<x:ExcelWorkbook>";
                excelFile += "<x:ExcelWorksheets>";
                excelFile += "<x:ExcelWorksheet>";
                excelFile += "<x:Name>";
                excelFile += "{worksheet}";
                excelFile += "</x:Name>";
                excelFile += "<x:WorksheetOptions>";
                excelFile += "<x:DisplayGridlines/>";
                excelFile += "</x:WorksheetOptions>";
                excelFile += "</x:ExcelWorksheet>";
                excelFile += "</x:ExcelWorksheets>";
                excelFile += "</x:ExcelWorkbook>\n";
                excelFile += "</xml>\n";
                excelFile += "<![endif]-->\n";
                excelFile += "</head>\n";
                excelFile += "<body>\n";
                var content = $compile('<div><ng-include src="excelView"></ng-include></div>')(scope);
                scope.$apply();
                excelFile += content.html();
                excelFile += "</body>\n";
                excelFile += "</html>\n";
                console.log(excelFile);
                //var base64data = "base64," + AJS.$.base64.encode(html);
                //window.open('data:application/vnd.ms-excel;filename=exportData.xls;' + base64data);
            });
        }
    }
}]);

