app.directive('auiUserPicker', ['translations', function (translations) {
    return {
        restrict: 'A',
        scope: {
            ngValue: '=',
            ngUserChangedFunc: '='
        },
        link: function (scope, elem) {
            translations.then(function (translations) {
                $(elem).auiSelect2({
                    hasAvatar: false,
                    allowClear: true,
                    placeholder: translations["Search for user"], // needed for allowClear
                    multiple: false,
                    ajax: {
                        url: "/rest/api/2/user/picker", // JIRA-relative URL to the REST end-point
                        type: "GET",
                        dataType: 'json',
                        cache: true,
                        // query parameters for the remote ajax call
                        data: function data(term) {
                            return {
                                query: term || '%', // https://community.atlassian.com/t5/Jira-Software-questions/Getting-a-list-of-all-users-through-REST-API/qaq-p/657667
                                maxResults: 1000,
                                showAvatar: true
                            };
                        },
                        // parse data from the server into form select2 expects
                        results: function results(data) {
                            data = TimesheetUtils.getJson(data);
                            return {
                                results: data.users
                            };
                        },
                        // select2 uses $.ajax as a default transport function so we have to override it
                        // to use AP.request for cross-origin communication
                        transport: function transport(params) {
                            AP.request({
                                url: params.url + '?' + $.param(params.data),
                                headers: {
                                    "Accept": "application/json",
                                    "x-atlassian-force-account-id": true
                                },
                                success: params.success,
                                error: params.error
                            });
                        }
                    },
                    // specify id parameter of each user entity
                    id: function id(user) {
                        return user.accountId;
                    },
                    // define how selected element should look like
                    formatSelection: function formatSelection(user) {
                        return Select2.util.escapeMarkup(user.displayName);
                    },
                    // define how single option should look like
                    formatResult: function formatResult(user) {
                        return user.html;
                    },
                    //  define message showed when there are no matches
                    formatNoMatches: function formatNoMatches(/* query */) {
                        return "No matches found";
                    },
                    initSelection: function(element, callback) {
                        // assert(element.val() == scope.ngValue.key)
                        callback(scope.ngValue);
                    }
                });
            });

            scope.$watch('ngValue', function(userInfo) {
                if (userInfo) {
                    elem.select2("val", userInfo.accountId); // trigger initSelection
                } else {
                    elem.select2("val", null); // trigger initSelection
                }
            });

            $(elem).change(function() {
                if (scope.ngUserChangedFunc != null) {
                    scope.ngUserChangedFunc($(elem).val());
                    scope.$apply();
                }
            });
        }
    }
}]);

