function CsvView(allIssues) {
    this.allIssues = allIssues;
}

// see also PivotKey.GroupByField
CsvView.fieldValueFn = function(userFieldFormatter, keyFieldFormatter, timeFormatter) {
    return function(issue, fieldObject, options, worklog) {
        var NULL_VALUE = "";
        var sprintRegex = /com\.atlassian\.greenhopper\.service\.sprint\.Sprint@[\d\w]+\[([\s\S]*)?\]/;
        var maxFractionDigits = options.jiraConfig.maxFractionDigits;

        var getNameOrKey = function(field) {
            var m;
            if (typeof field == 'object' && field && /\/rest\/api(\/\d+)\/user/.test(field.self)) {
                return userFieldFormatter(field, options);
            } else if (typeof field == 'object' && field && field.hasOwnProperty('name')) {
                return field.name;
            } else if (typeof field == 'object' && field && field.hasOwnProperty('key')) {
                return keyFieldFormatter(field, options);
            } else if (typeof field == 'object' && field && field.hasOwnProperty('value')) {
                return field.value + (field.hasOwnProperty('child') ? ':' + getNameOrKey(field.child) : '');
            } else if (typeof field == 'object' && field && field.hasOwnProperty('hasEpicLinkFieldDependency')) { // Portfolio for Jira
                if (field.showField && field.data) {
                    return field.data.key;
                } else {
                    return NULL_VALUE;
                }
            } else if (typeof field == 'string' && (m = field.match(sprintRegex))) {
                var splits = m[1].split(',');
                for (var i = 0; i < splits.length; i++) {
                    var split = splits[i];
                    if (split.indexOf('name=') == 0) {
                        return split.substr(5);
                    }
                }
                return NULL_VALUE;
            } else {
                return field;
            }
        };

        var getValue = function(field) {
            if (TimesheetUtils.isDate(field)) {
                return moment(field).format(TimesheetUtils.dateTimeFormat);
            } else if (Array.isArray(field)) {
                var value = '';
                if (field.length > 0 && (field[0].inwardIssue || field[0].outwardIssue)) {
                    value = field.map(function(v) {
                        if (v.inwardIssue) {
                            return v.type.inward + ' ' + getNameOrKey(v.inwardIssue);
                        } else {
                          return v.type.outward + ' ' + getNameOrKey(v.outwardIssue);
                        }
                    }).join(', ');
                } else {
                    field.forEach(function(v) {
                        if (value.length > 0) {
                            value += ', ';
                        }
                        value += getNameOrKey(v);
                    });
                }
                return value;
            } else {
                return getNameOrKey(field);
            }
        };

        var fieldName = fieldObject.id || fieldObject;

        var getFieldValueObject = function(issue) {
            var value = issue.fields[fieldName];
            if (fieldObject && fieldObject.schema && fieldObject.schema.custom == 'com.pyxis.greenhopper.jira:gh-epic-link') {
                if (value) {
                    value = {key: value};
                } else if (issue.fields.issuetype.name == 'Epic') {
                    value = {key: issue.key};
                }
            }
            if (!value) {
                if (fieldName == 'parent' && options._groupByField == fieldName) {
                    value = issue.key ? {key : issue.key} : NULL_VALUE;
                } else if (fieldName == 'projectcategory' && issue.fields.project && issue.fields.project.projectCategory) {
                    value = issue.fields.project.projectCategory;
                } else {
                    value = NULL_VALUE;
                }
            }
            return value;
        };

        var field = getFieldValueObject(issue);
        var value = getValue(field);
        if (['timeestimate', 'timeoriginalestimate', 'timespent'].indexOf(fieldName) != -1 && value != NULL_VALUE) {
            value = timeFormatter((value || 0), options);
        } else if (fieldName == 'workeduser') {
            value = worklog && worklog.author ? userFieldFormatter(worklog.author, options) : NULL_VALUE;
        } else if (fieldName == 'workedusergroup') {
            value = worklog && worklog.author
            && options.userInfos[worklog.author.accountId] && options.userInfos[worklog.author.accountId].groups
            && options.userInfos[worklog.author.accountId].groups.items.length
                ? options.userInfos[worklog.author.accountId].groups.items[0].name
                : NULL_VALUE;
        } else if (fieldName == 'workeddate') {
            value = worklog && worklog.started ? moment(worklog.started).format(TimesheetUtils.dateTimeFormat): NULL_VALUE;
        } else if (fieldName == 'workedweek') {
            value = worklog ? 'Week ' + new PivotKey.WeekOfTheYear(new Date(worklog.started), options).weekNumber : PivotKey.NULL_VALUE;
        } else if (fieldName == 'workcreated') {
            value = worklog && worklog.created ? moment(worklog.created).format(TimesheetUtils.dateTimeFormat) : NULL_VALUE;
        } else if (fieldName == 'commentfirstword') {
            if (worklog && worklog.comment) {
                var comment = worklog.comment.replace(/\n/g, ' ');
                var i = comment.indexOf(' ');
                field = i > 0 ? comment.substring(0, i) : comment;
            } else {
                field = NULL_VALUE;
            }
            value = field;
        } else if (fieldName == 'worklabel') {
            var tags = TimesheetUtils.getTags(worklog.comment);
            if (tags && tags.length > 0) {
                tags.sort();
                field = tags.join(", ");
            } else {
                field = NULL_VALUE;
            }
            value = field;
        } else if (fieldName == 'timetrackingprogress') {
            var actual = issue.fields.timespent || 0;
            var remaining = issue.fields.timeestimate || 0;
            if (actual + remaining == 0) {
                value = "";
            } else {
                var percent = Math.round(actual * 100 / (actual + remaining));
                value = DrawHelper.formatDecimal(percent, maxFractionDigits) + '%';
            }
        } else if (fieldName == 'timetrackingestimate') {
            var actual = issue.fields.timespent || 0;
            var remaining = issue.fields.timeestimate || 0;
            value = timeFormatter((actual + remaining), options);
        } else if (fieldName == 'timetrackingvariance') {
            var original = issue.fields.timeoriginalestimate || 0;
            var actual = issue.fields.timespent || 0;
            var remaining = issue.fields.timeestimate || 0;
            var estimate = actual + remaining;
            value = timeFormatter((original - estimate), options);
        } else if (fieldName == 'timetrackingoriginalestimateremaining') {
            var original = issue.fields.timeoriginalestimate || 0;
            var actual = issue.fields.timespent || 0;
            value = timeFormatter((original - actual), options);
        }
        return value;
    }
};

CsvView.prototype.fieldValue = CsvView.fieldValueFn(function csvUserFieldFormatter(field/*, options */) {
    return field.displayName || field.name;
}, function csvKeyFieldFormatter(field/*, options */) {
    var name = field.hasOwnProperty('fields') && field.fields.hasOwnProperty('summary') ? ' ' + field.fields.summary : '';
    return field.key + name;
}, function csvTimeFormatter(value, options) {
    return DrawHelper.formatDecimal(value / options.periodDuration, options.jiraConfig.maxFractionDigits);
});

CsvView.prototype.escape = function(value) {
    return value ? '"' + value.replace(/"/g, '""') + '"' : "";
};
CsvView.prototype.enquote = function(value) {
    return value.indexOf(',') >= 0 ? this.escape(value) : value;
};
/*
    Provides raw worklogs.

    Supported options: pivotTableType, monthView, startDate, endDate, user
    TODO? groups, sumSubTask
*/
CsvView.prototype.generate = function(options) {
    var maxFractionDigits = options.jiraConfig.maxFractionDigits;
    var pivotTable = PivotTableFactory.createPivotTable(options);
    var isTimeView = ["TimeTracking", "TimeBalance"].indexOf(options.pivotTableType) >=0;
    var timeColumns = [];
    if (options.pivotTableType == "TimeTracking") {
        timeColumns = options.timeTrackingColumns;
    } else if (options.pivotTableType == "TimeBalance") {
        timeColumns = options.timeBalanceColumns;
    }
    var csv = "";
    var exportColumns = options.configOptions.exportColumns;
    var timeFormat = options.configOptions.durationTypeForExport || 'h';
    if (timeFormat == 'd') {
        options.periodDuration = 3600 * parseFloat(options.jiraConfig.workingHoursPerDay || 8);
    } else if (timeFormat == 'm') {
        options.periodDuration = 60;
    } else {
        options.periodDuration = 3600;
    }
    var issueColumnsPart = ['project', 'issuetype', 'key', 'summary', 'priority']
        .filter(function (column) {
            return exportColumns.indexOf(column) >= 0;
        });
    csv += issueColumnsPart
        .map(function (column) {
            return options.translations[column] + ',';
        })
        .join('');
    var exportDescriptionStatus = exportColumns.indexOf("descriptionstatus") >= 0;
    if (options.pivotTableType == "TimeBalance") {
        if (exportColumns.indexOf("datestarted") >= 0) {
            csv += options.translations["Date Started"] + ",";
        }
        if (exportColumns.indexOf("displayname") >= 0) {
            csv += options.translations["Display Name"] + ",";
        }
        csv += options.translations["Changed"] + ",";
    }
    var exportWorkCreated = exportColumns.indexOf("workcreated") >= 0;
    for (var i = 0; i < options.moreFields.length; i++) {
        var moreField = options.moreFields[i];
        if (['timeestimate', 'timeoriginalestimate', 'timespent'].indexOf(moreField.id) >= 0) {
            csv += this.escape(moreField.name + ' (' + timeFormat + ')') + ',';
        } else {
            csv += this.escape(moreField.name) + ",";
        }
        if (moreField.id == "workcreated") {
            exportWorkCreated = false;
        }
    }
    if (isTimeView) {
        csv += timeColumns
            .map(function (column) {
                return options.translations[column] + (column != '6progress' ? ' (' + timeFormat + ')' : '');
            })
            .join(',');
    } else {
        if (exportWorkCreated) {
            csv += options.translations["Work Created"] + ",";
        }
        if (exportColumns.indexOf("worklogid") >= 0) {
            csv += options.translations["Worklog Id"] + ",";
        }
        if (exportColumns.indexOf("datestarted") >= 0) {
            csv += options.translations["Date Started"] + ",";
        }
        if (["IssuePassedTimeByStatus", "IssuePassedTimeByResolution"].indexOf(options.pivotTableType) >= 0) {
            csv += options.translations["Date Ended"] + ",";
        }
        if (exportColumns.indexOf("displayname") >= 0) {
            csv += options.translations["Display Name"] + ",";
        }
        if (exportColumns.indexOf("emailaddress") >= 0) {
            csv += options.translations["Email Address"] + ",";
        }
        csv += options.translations["Time Spent"] + " (" + timeFormat + ")"; // find comma from here in next lines
        if (exportDescriptionStatus) {
            if (options.pivotTableType == "IssuePassedTimeByStatus") {
                csv += "," + options.translations["Status"];
            } else if (options.pivotTableType == "IssuePassedTimeByResolution") {
                csv += "," + options.translations["Resolution"] + ",";
            } else {
                csv += "," + options.translations["Work Description"];
            }
        }
    }
    csv += "\n";
    var totalTimeSpentSeconds = 0;
    for (var i in this.allIssues) {
        var pivotEntries = pivotTable.add(this.allIssues[i]);
        for (var i in pivotEntries) {
            var pivotEntry = pivotEntries[i];
            // rowKey is PivotKey.Issue
            var issue = pivotEntry.rowKey._issue || pivotEntry.rowKey.issue;
            var worklog = pivotEntry.rowKey.worklog || pivotEntry.worklog || {}; // for PassedTimeByStatus & TimeBalance
            worklog.comment = worklog.comment ? worklog.comment.replace(/<([^>]+)>/g, '&lt;$1&gt;').replace(/\n/g, ' ') : '';
            if (exportColumns.indexOf("project") >= 0) {
                csv += this.escape(issue.fields.project.name) + ",";
            }
            if (exportColumns.indexOf("issuetype") >= 0) {
                csv += issue.fields.issuetype.name + ",";
            }
            if (exportColumns.indexOf("key") >= 0) {
                csv += issue.key + ",";
            }
            if (exportColumns.indexOf("summary") >= 0) {
                csv += this.escape(issue.fields.summary) + ",";
            }
            if (exportColumns.indexOf("priority") >= 0) {
                csv += this.escape(this.fieldValue(issue, 'priority', options)) + ",";
            }
            if (options.pivotTableType == "TimeBalance") {
                if (exportColumns.indexOf("datestarted") >= 0) {
                    csv += (worklog.started ? moment(worklog.started).format(TimesheetUtils.dateTimeFormat) : '') + ",";
                }
                if (exportColumns.indexOf("displayname") >= 0) {
                    csv += (worklog.author ? this.escape(worklog.author.displayName || '') : '') + ",";
                }
                csv += this.escape(worklog.comment) + ",";
            }
            for (var i = 0; i < options.moreFields.length; i++) {
                var moreField = options.moreFields[i];
                csv += this.escape(String(this.fieldValue(issue, moreField, options, worklog))) + ",";
            }
            if (isTimeView) {
                for (var i in timeColumns) {
                    if (i > 0) {
                        csv += ",";
                    }
                    if (timeColumns[i] == '6progress') {
                        // see timeTracking filter
                        var value = pivotEntry.values['6progress'];
                        if (value.timespent + value.estimate == 0) {
                            csv += "";
                        } else {
                            var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent)); //calcColumnFunction(row);
                            csv += this.enquote(DrawHelper.formatDecimal(percents, maxFractionDigits) + '%');
                        }
                    } else if (timeColumns[i] == '5originalestimateremaining') {
                        csv += this.enquote(DrawHelper.formatDecimal(pivotEntry.values[timeColumns[i]].value / options.periodDuration, maxFractionDigits));
                    } else {
                        csv += this.enquote(DrawHelper.formatDecimal(pivotEntry.values[timeColumns[i]] / options.periodDuration, maxFractionDigits));
                    }
                }
                csv += "\n";
            } else {
                if (exportWorkCreated) {
                    csv += (worklog.created ? moment(worklog.created).format(TimesheetUtils.dateTimeFormat) : '') + ",";
                }
                if (exportColumns.indexOf("worklogid") >= 0) {
                    csv += worklog.id + ",";
                }
                if (exportColumns.indexOf("datestarted") >= 0) {
                    csv += (worklog.workDate ? moment(worklog.workDate).format(TimesheetUtils.dateTimeFormat) : '') + ",";
                }
                if (["IssuePassedTimeByStatus", "IssuePassedTimeByResolution"].indexOf(options.pivotTableType) >= 0) {
                    csv += moment(worklog.toDate).format(TimesheetUtils.dateTimeFormat) + ",";
                }
                if (exportColumns.indexOf("displayname") >= 0) {
                    csv += (worklog.author ? this.escape(worklog.author.displayName || '') : '') + ",";
                }
                if (exportColumns.indexOf("emailaddress") >= 0) {
                    csv += (worklog.author ? this.escape(worklog.author.emailAddress || ''): '') + ",";
                }
                csv += worklog.timeSpentSeconds ? this.enquote(DrawHelper.formatDecimal(worklog.timeSpentSeconds / options.periodDuration, maxFractionDigits)) : ''; // find comma from here in next lines
                if (exportDescriptionStatus) {
                    csv += ",";
                    csv += this.escape((worklog._issue ? worklog._issue.key + ' ' : '') +
                        (["IssuePassedTimeByStatus", "IssuePassedTimeByResolution"].indexOf(options.pivotTableType) >= 0 ? pivotEntry.columnKey.keyValue : worklog.comment));
                }
                totalTimeSpentSeconds += worklog.timeSpentSeconds || 0;
                csv += "\n";
            }
        }
    }

    var colspanForTotals = exportColumns.length; // field after totals
    if (exportDescriptionStatus) {
        colspanForTotals--;
    }
    if (exportColumns.indexOf("timespentd") >= 0) {
        colspanForTotals--;
    }
    if (exportColumns.indexOf("workcreated") >= 0 && !exportWorkCreated) {
        colspanForTotals--;
    }
    var comas = "";
    var moreFieldsLength = options.moreFields.length;
    var colspan = isTimeView ? issueColumnsPart.length : colspanForTotals;
    if (options.pivotTableType == "TimeBalance") {
        if (exportColumns.indexOf("datestarted") >= 0) {
            colspan++;
        }
        if (exportColumns.indexOf("displayname") >= 0) {
            colspan++;
        }
        colspan++;
    }
    comas += TimesheetUtils.repeat(",", colspan + moreFieldsLength);
    if (["IssuePassedTimeByStatus", "IssuePassedTimeByResolution"].indexOf(options.pivotTableType) >= 0) {
        comas += ",";
    }
    csv += (colspan ? "Total" : "") + comas;
    if (isTimeView) {
        for (var i in timeColumns) {
            if (i > 0) {
                csv += ",";
            }
            if (timeColumns[i] == '6progress') {
                // see timeTracking filter
                var value = pivotTable.totals['6progress'].value;
                if (value.timespent + value.estimate == 0) {
                    csv += "";
                } else {
                    var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent)); //calcColumnFunction(row);
                    csv += this.enquote(DrawHelper.formatDecimal(percents, maxFractionDigits) + '%');
                }
            } else if (timeColumns[i] == '5originalestimateremaining') {
                csv += this.enquote(DrawHelper.formatDecimal(pivotTable.totals[timeColumns[i]].value.value / options.periodDuration, maxFractionDigits));
            } else {
                csv += this.enquote(DrawHelper.formatDecimal(pivotTable.totals[timeColumns[i]].sum / options.periodDuration, maxFractionDigits));
            }
        }
        csv += "\n";
    } else {
        csv += this.enquote(DrawHelper.formatDecimal(totalTimeSpentSeconds / options.periodDuration, maxFractionDigits)) + (exportDescriptionStatus ? "," : "") + "\n";
    }
    var restrictedProjects = options.restrictedProjects && options.restrictedProjects.join(',');
    if (restrictedProjects) {
        csv += options.translations["Data is limited by Auditors Roles"] + " " + restrictedProjects + "\n";
    }
    return csv;
};
CsvView.prototype.download = function(options) {
    var csvFile = '\ufeff' + this.generate(options);
    var blob = new Blob([csvFile], {encoding:"utf-8", type: 'application/vnd.ms-excel;charset=utf-8;'});
    saveAs(blob, "exportData.csv");
};

if (typeof module === 'object' && module.exports) {
    module.exports = CsvView;
    var PivotTableFactory = require('./PivotTable');
    var TimesheetUtils = require('../../timesheetUtils');
    var DrawHelper = require('../../app/directives/DrawHelper');
    var moment = require('moment-timezone');
}
