var PivotKey = {};

PivotKey.Generic = function (keyName, keyValue, filterName, order) {
    this.keyName = keyName;
    this.keyValue = keyValue;
    this.filterName = filterName;
    this.order = order;
};
PivotKey.Generic.comparator = function(key1, key2) {
    if ((key1.order == null) || (key2.order == null)) {
        return StringComparator(key1.keyValue, key2.keyValue);
    }
    return key1.order - key2.order;
};

PivotKey.Issue = function(issue, worklog) {
    this.issue = issue;
    this.keyName = "issue";
    this.keyValue = this.issue.key;
    this.worklog = worklog;
};

// jira-api/src/main/java/com/atlassian/jira/issue/comparator/KeyComparator.java
function KeyComparator(key1, key2) {
    if ((key1 == null) && (key2 == null)) {
        return 0;
    } else if (key1 == null) {
        return 1;
    } else if (key2 == null) {
        return -1;
    }

    var pair1 = key1.split('-');
    var pair2 = key2.split('-');

    // issue key may not have project key
    // data imported from Bugzilla may not comply with atlassian issue key format
    // this added to make jira more fault tolerant
    if ((pair1.length == 1) && (pair2.length == 1)) {
        var id1 = parseInt(pair1[0]);
        var id2 = parseInt(pair2[0]);
        var diff = id1 - id2;
        return diff;
    } else if (pair1.length == 1) {
        return 1;
    } else if (pair2.length == 1) {
        return -1;
    }

    var project1 = pair1[0];
    var project2 = pair2[0];

    // compare the project part
    for (var i = 0; i < project1.length && i < project2.length && !diff; i++) {
        diff = project1.charCodeAt(i) - project2.charCodeAt(i);
    }
    if (diff) {
        return diff;
    }

    // if one of the project parts is shorter than the other, that will be less than
    var diff = project1.length - project2.length;
    if (diff) {
        return diff;
    }

    // same project, compare numbers
    var id1 = parseInt(pair1[1]);
    var id2 = parseInt(pair2[1]);
    var diff = id1 - id2;

    return diff;
}

PivotKey.Issue.comparator = KeyComparator;

PivotKey.User = function(worklog, issue, options) {
    this.user = worklog.author;
    this.keyName = "user";
    this.keyValue = worklog.author ? worklog.author.accountId || worklog.author.key || worklog.author.name || worklog.author : '';
    this.options = options;
};
PivotKey.User.comparator = function(key1, key2) {
    return StringComparator(key1.keyValue, key2.keyValue);
};

function isDate(value) {
    return Object.prototype.toString.call(value) === '[object Date]';
}

PivotKey.Issue.getRowComparator = function (fieldName/*, direction */) {
    return function (row1, row2) {
        // FIXME: orderComparator for Priorty
        var value1 = row1.rowKey.issue ? PivotKey.getValue(row1.rowKey.issue.fields[fieldName], fieldName) : null;
        var value2 = row2.rowKey.issue ? PivotKey.getValue(row2.rowKey.issue.fields[fieldName], fieldName) : null;
        // FIXME: direction
        if (typeof value1 === 'string' || typeof value2 === 'string') {
          return StringComparator(value1, value2);
        } else if (isDate(value1) || isDate(value2)) {
          return DateComparator(value1, value2);
        } else if (typeof value1 === 'number' || typeof value2 === 'number') {
          return NumberComparator(value1, value2);
        } else {
          return 0;
        }
    };
};

PivotKey.getStatuses = function(issue, configOptions) {
    var changelog = issue.changelog ? issue.changelog : {};
    var histories = changelog.histories ? changelog.histories : [];
    var author = issue.fields.creator;
    var statuses = [];
    histories.map(function(history) {
        var created = TimesheetUtils.convertDate(history.created, !configOptions.preserveStartedTime && history.author && history.author.timeZone);
        var items = history.items || [];
        items.map(function(item) {
            if (item.fieldtype == 'jira' && item.field == 'status') {
                var status = {
                    id: item.from,
                    name: item.fromString,
                    date: created.getTime(),
                    author: author
                };
                statuses.push(status);
            }
        });
        author = history.author;
    });
    statuses.push({ // latest
        id: issue.fields.status.id,
        name: issue.fields.status.name,
        date: Date.now(),
        author: author
    });
    return statuses;
};
var checkStatusCategory = function (status, configOptions) {
    return !configOptions.timeInStatusCategories ||
        configOptions.timeInStatusCategories.length == 0 ||
        configOptions.timeInStatusCategories.indexOf(status.categoryId) >= 0
};

PivotKey.getStatusKeys = function(worklog, issue, configOptions) {
    var statuses = PivotKey.getStatuses(issue, configOptions);
    return PivotKey.getStatusKeysEx(worklog, issue, configOptions, statuses);
};

PivotKey.getStatusKeysEx = function(worklog, issue, configOptions, statuses) {
    var timespent = worklog.timeSpentSeconds;
    var workDate = worklog.workDate.getTime(); // from WorkedTimePivotStrategy
    var result = [];
    var i = 0;
    while (i < statuses.length - 1 && workDate > statuses[i].date) i++; // skip passed statuses
    for (/* continue */; i < statuses.length - 1 && timespent > 0 && workDate < statuses[i].date; i++) {
        var status = statuses[i];
        var elapsed = configOptions.startedTimeInStatus ? timespent : PivotKey.getTimeInStatus(new Date(workDate), new Date(status.date), configOptions);
        var statusEx = configOptions.statuses[status.id] || status;
        var statusKey = {
            key: new PivotKey.Status(status.name, status.id, status.date, status.author, statusEx.order, statusEx.categoryId),
            timespent: Math.min(timespent, elapsed)
        };
        if (checkStatusCategory(statusKey.key, configOptions)) {
            result.push(statusKey);
        }
        // remaining work
        workDate = status.date;
        timespent -= statusKey.timespent;
    }
    if (timespent > 0) { // latest
        var status = statuses[statuses.length - 1];
        var statusEx = configOptions.statuses[status.id] || status;
        var statusKey = {
            key: new PivotKey.Status(status.name, status.id, status.date, status.author, statusEx.order, statusEx.categoryId),
            timespent: timespent
        };
        if (checkStatusCategory(statusKey.key, configOptions)) {
            result.push(statusKey);
        }
    }
    return result;
};

PivotKey.getLabelKeys = function (worklog/*, issue, configOptions */) {
    var timespent = worklog.timeSpentSeconds;
    var result = [];
    var tags = TimesheetUtils.getTags(worklog.comment);
    tags.sort();
    if (!tags.length) {
        tags.push("");
    }
    for (var i = 0; i < tags.length; i++) {
        result.push({
            key: new PivotKey.Generic("Label", tags[i]),
            timespent: timespent
        });
    }
    return result;
};

PivotKey.getTimeInStatus = function(startDate, endDate, configOptions) {
  if (configOptions.workingTimeInStatus.from && configOptions.workingTimeInStatus.to) {
    var startTime = startDate.getTime();
    startDate.setHours(configOptions.workingTimeInStatus.from, 0, 0, 0);
    startTime = Math.max(startTime, startDate.getTime()); // Max(X, 9:00)
    var endTime = endDate.getTime();
    endDate.setHours(configOptions.workingTimeInStatus.to, 0, 0, 0);
    startTime = Math.min(startTime, endDate.getTime()); // Min(Max(X, 9:00), 17:00) => 9:00 < X < 17:00
    endTime = Math.min(endTime, endDate.getTime()); // Min(Y, 17:00)
    // pass back actual times to PassedTimePivotStrategy.getEntries
    startDate.setTime(startTime);
    endDate.setTime(endTime);
    var start = new Date(startTime);
    var end = new Date(startTime);
    end.setHours(configOptions.workingTimeInStatus.to, 0, 0, 0);
    var timeSpentSeconds = 0;
    while (start.getTime() < endTime) {
      if (!TimesheetUtils.isWeekend(start, configOptions)) {
          var t1 = Math.min(end.getTime(), endTime); // Min(17:00, Min(Y, 17:00))
          var t0 = Math.min(end.getTime(), start.getTime()); // Min(17:00, X)
          timeSpentSeconds += (t1 - t0) / 1000;
      }
      var date = start.getDate() + 1;
      start.setDate(date);
      start.setHours(configOptions.workingTimeInStatus.from, 0, 0, 0, 0);
      end.setDate(date);
    }
    return timeSpentSeconds;
  } else {
    return (endDate.getTime() - startDate.getTime()) / 1000; // timeSpentSeconds
  }
};

PivotKey.Status = function(status, id, date, author, order, categoryId) {
    this.keyValue = status;
    this.statusId = id;
    this.statusDate = date;
    this.author = author;
    this.order = order;
    this.categoryId = categoryId;
};
PivotKey.Status.comparator = function(key1, key2) {
    return key1.order - key2.order;
};

PivotKey.getResolutions = function(issue, configOptions) {
    var changelog = issue.changelog ? issue.changelog : {};
    var histories = changelog.histories ? changelog.histories : [];
    var author = issue.fields.creator;
    var statuses = [];
    histories.map(function(history) {
        var created = TimesheetUtils.convertDate(history.created, !configOptions.preserveStartedTime && history.author && history.author.timeZone);
        var items = history.items || [];
        items.map(function(item) {
            if (item.fieldtype == 'jira' && item.field == 'resolution') {
                var status = {
                    id: item.from ? 1 : 0,
                    name: item.from ? 'Resolved' : 'Unresolved',
                    date: created.getTime(),
                    author: author
                };
                statuses.push(status);
            }
        });
        author = history.author;
    });
    statuses.push({ // latest
        id: issue.fields.resolution ? 1 : 0,
        name: issue.fields.resolution ? 'Resolved' : 'Unresolved',
        date: Date.now(),
        author: author
    });
    return statuses;
};

PivotKey.HourOfTheDay = function(date) {
	this.keyName = "dayOfTheWeek";
    this.date = TimesheetUtils.truncateMinutes(date);
    this.keyValue = this.date.getTime();
};

PivotKey.DayOfTheWeek = function(date) {
	this.keyName = "dayOfTheWeek";
    this.date = TimesheetUtils.truncateTime(date);
    this.keyValue = this.date.getTime();
};

PivotKey.WeekOfTheYear = function(date, options) {
    this.keyName = "weekOfTheYear";
    this.startDate = TimesheetUtils.getPrevMonday(date, options.reportingDay);
    this.endDate = TimesheetUtils.addDays(this.startDate, 7);
    this.weekNumber = TimesheetUtils.getNormalizedWeekNumber(this.startDate); //normalize for correct ordering
    this.keyValue = this.startDate.getTime();
};

PivotKey.MonthOfTheYear = function(date) {
    this.keyName = "monthOfTheYear";
    this.startDate = TimesheetUtils.getFirstDayOfTheMonth(date);
    this.endDate = TimesheetUtils.getLastDayOfTheMonth(date);
    this.monthKey = TimesheetUtils.getMonthKey(date);
    this.keyValue = this.startDate.getTime();
};

PivotKey.NULL_VALUE = "&nbsp;";
PivotKey.sprintRegex = /com\.atlassian\.greenhopper\.service\.sprint\.Sprint@[\d\w]+\[([\s\S]*)?\]/;

PivotKey.getNameOrKey = function(field) {
    var m;
    if (field && typeof field === 'object' && /\/rest\/api(\/\d+)\/user/.test(field.self)) {
        return field.displayName || field.name || field.key || field.accountId || PivotKey.NULL_VALUE;
    } else if (field && typeof field === 'object' && field.hasOwnProperty('name')) {
        return field.name;
    } else if (field && typeof field === 'object' && field.hasOwnProperty('key')) {
        return field.key;
    } else if (field && typeof field === 'object' && field.hasOwnProperty('value')) {
        return field.value + (field.hasOwnProperty('child') ? ':' + PivotKey.getNameOrKey(field.child) : '');
    } else if (field && typeof field === 'object' && field.hasOwnProperty('hasEpicLinkFieldDependency')) { // Portfolio for Jira
        if (field.showField && field.data) {
            return field.data.key;
        } else {
            return PivotKey.NULL_VALUE;
        }
    } else if (typeof field === 'string' && (m = field.match(PivotKey.sprintRegex))) {
        var splits = m[1].split(',');
        for (var i = 0; i < splits.length; i++) {
            var split = splits[i];
            if (split.indexOf('name=') == 0) {
                return split.substr(5);
            }
        }
        return PivotKey.NULL_VALUE;
    } else {
        return field;
    }
};

PivotKey.getValue = function(field, fieldName) {
    if (TimesheetUtils.isDate(field)) {
        return TimesheetUtils.truncateTime(moment(field).toDate());
    } else if (Array.isArray(field)) {
        var value = '';
        field.forEach(function(v) {
            if (value.length > 0) {
                value += ', ';
            }
            value += PivotKey.getNameOrKey(v);
        });
        return value;
    } else if (fieldName == 'priority') {
        return field.id;
    } else {
        return PivotKey.getNameOrKey(field);
    }
};

PivotKey.getFieldValueObject = function(fieldName, fieldObject, issue) {
    var value = issue.fields[fieldName];
    if (fieldObject && fieldObject.schema && fieldObject.schema.custom == 'com.pyxis.greenhopper.jira:gh-epic-link') {
        if (value) {
            value = {key: value};
        } else if (issue.fields.issuetype.name == 'Epic') {
            value = {key: issue.key};
        }
    }
    if (!value) {
        if (fieldName == 'issue') {
          value = issue;
        } else if (fieldName == 'parent') {
            value = {key : issue.key, fields: {
                summary: issue.fields.summary,
                resolution: issue.fields.resolution,
                issuetype: issue.fields.issuetype,
                priority: issue.fields.priority
            }};
        } else if (fieldName == 'projectcategory' && issue.fields.project.projectCategory) {
            value = issue.fields.project.projectCategory;
        } else {
            value = PivotKey.NULL_VALUE;
        }
    }
    return value;
};

PivotKey.getFieldAndValue = function (issue, fieldName, fieldObject, worklog, options) {
    var field;
    var value;
    switch (fieldName) {
        case "workeduser":
            field = worklog && worklog.author ? worklog.author : PivotKey.NULL_VALUE;
            value = (field.displayName || field.key || field.name || field.accountId || PivotKey.NULL_VALUE);
            break;
        case "workedusergroup":
            field = value = worklog && worklog.author
            && options.userInfos[worklog.author.accountId] && options.userInfos[worklog.author.accountId].groups
            && options.userInfos[worklog.author.accountId].groups.items.length
                ? options.userInfos[worklog.author.accountId].groups.items[0].name
                : PivotKey.NULL_VALUE;
            break;
        case "workeddate":
            field = value = worklog ? worklog.started : PivotKey.NULL_VALUE;
            break;
        case "workedweek":
            if (worklog) {
                field = new PivotKey.WeekOfTheYear(new Date(worklog.started), options);
                value = moment(field.startDate).format('YYYYMMDD');
            } else {
                field = value = PivotKey.NULL_VALUE;
            }
            break;
        case "workcreated":
            field = value = worklog ? worklog.created : PivotKey.NULL_VALUE;
            break;
        case "commentfirstword":
            if (worklog && worklog.comment) {
                var comment = worklog.comment.replace(/\n/g, ' ');
                var i = comment.indexOf(' ');
                field = i > 0 ? comment.substring(0, i) : comment;
            } else {
                field = PivotKey.NULL_VALUE;
            }
            value = field;
            break;
        case "worklabel":
            var tags = TimesheetUtils.getTags(worklog.comment);
            if (tags && tags.length > 0) {
                tags.sort();
                field = tags.join(", ");
            } else {
                field = PivotKey.NULL_VALUE;
            }
            value = field;
            break;
        case 'timetrackingprogress':
            var actual = issue.fields.timespent || 0;
            var remaining = issue.fields.timeestimate || 0;
            value = {estimate: remaining, timespent: actual};
            break;
        case 'timetrackingestimate':
            var actual = issue.fields.timespent || 0;
            var remaining = issue.fields.timeestimate || 0;
            value = actual + remaining;
            break;
        case 'timetrackingvariance':
            var original = issue.fields.timeoriginalestimate || 0;
            var actual = issue.fields.timespent || 0;
            var remaining = issue.fields.timeestimate || 0;
            var estimate = actual + remaining;
            value = original - estimate;
            break;
        case 'timetrackingoriginalestimateremaining':
            var original = issue.fields.timeoriginalestimate || 0;
            var actual = issue.fields.timespent || 0;
            value = {issue: issue, value: original - actual};
            break;
        default:
            field = PivotKey.getFieldValueObject(fieldName, fieldObject, issue);
            value = PivotKey.getValue(field);
    }
    return [field, value];
};

PivotKey.GroupByField = function(issue, worklog, options) {
  var fieldName = options.groupByField;
  var fieldObject = options.groupByFieldObject;

  if (fieldName != 'issue') {
      this._issue = issue;
  }
  this._worklog = worklog;
  var projectAndValue = options.categorizeByField ?
      PivotKey.getFieldAndValue(issue, options.categorizeByField, options.categorizeByFieldObject, worklog, options)
      : [{name: ''}, ''];
  this.project = projectAndValue[0];
  var fieldAndValue = PivotKey.getFieldAndValue(issue, fieldName, fieldObject, worklog, options);
  this.field = fieldAndValue[0];
  this.keyValue =  (options.categorizeByField ? projectAndValue[1] + ':' : '' ) + fieldAndValue[1];
  this.keyName = fieldName;
  this._project = projectAndValue[1];
  this._field = fieldAndValue[1];
};

PivotKey.GroupByField.comparator = StringComparator;
PivotKey.GroupByField.dataComparator = function RowComparator(row1, row2) {
    return row1.rowKey._issue && row2.rowKey._issue
        ? KeyComparator(row1.rowKey._issue.key, row2.rowKey._issue.key)
        : 0;
};

function StringComparator(key1, key2) {
    if ((key1 == null) && (key2 == null)) {
        return 0;
    } else if (key1 == null) {
        return 1;
    } else if (key2 == null) {
        return -1;
    }

    return key1.localeCompare(key2);
}

function DateComparator(key1, key2) {
    if ((key1 == null) && (key2 == null)) {
        return 0;
    } else if (key1 == null) {
        return 1;
    } else if (key2 == null) {
        return -1;
    }

    return key1.getTime() - key2.getTime();
}

function NumberComparator(key1, key2) {
    if ((key1 == null) && (key2 == null)) {
        return 0;
    } else if (key1 == null) {
        return 1;
    } else if (key2 == null) {
        return -1;
    }

    return key1 - key2;
}

if (typeof module === 'object' && module.exports) {
    module.exports = PivotKey;
    var TimesheetUtils = require('../../timesheetUtils');
}
