'use strict';

var configurationModule = angular.module('configuration', []);

configurationModule.service("configurationService", ['$q', '$http', 'dataService', 'applicationLoggingService',
  function ($q, $http, dataService, logger) {
    var DATA_PROPERTY_CONFIGURATION = 'configuration';
    var DATA_PROPERTY_PREFERENCES_4 = 'preferences4';

    var AP = window.RA || window.AP;

    // DO data in services: http://blog.nebithi.com/angularjs-dos-and-donts/
    this.data = {
        allCompositionIssueLinks: null,
        allParentIssueFields: null
    };

    this.defaults = [
        {key: 'auditorsGroups'},
        {key: 'restrictedGroups'},
        {key: 'compositionIssueLink'},
        {key: 'parentIssueField'},
        {key: 'parentIssueType'},
        {key: 'weekendType', val: '60'},
        {key: 'preserveStartedTime'},
        {key: 'timeInStatusCategories'},
        {key: 'onErrorIgnore'},
        {key: 'timeTrackingColumns'},
        {key: 'timeBalanceColumns'},
        {key: 'exportColumns'},
        {key: 'prettyDuration'}, // deprecated
        {key: 'durationType', val: 'h'},
        {key: 'durationTypeForExport', val: 'h'},
        {key: 'maxFractionDigits', val: 2},
        {key: 'disableLogWork'},
        {key: 'worklogVisibilityGroup'},
        {key: 'worklogVisibilityRole'},
        {key: 'timeentryPanelGroups'},
        {key: 'timeentryPanelProjects'},
        {key: 'workDescriptionRequired'},
        {key: 'workLabels', val: []},
        {key: 'workingTimeInStatus', val: {}},
        {key: 'startedTimeInStatus'},
        {key: 'inProgressIssuesJql'},
        {key: 'dateFields'},
        {key: 'reportingDay'},
        {key: 'workUnderLimitColor'},
        {key: 'workOverLimitColor'},
        {key: 'showDetailsCount'}
    ];

    if (window.isTimesheet) {
        this.defaults.push({key: 'auditorsRoles'});
        this.defaults.push({key: 'logWorkForOthersGroups'});
    }

    try {
        var x = '__storage_test__';
        window.localStorage.setItem(x, x);
        window.localStorage.removeItem(x);
        this.defaults.push({key: 'cacheWorklog'});
    } catch (e) {
        // ignore
    }

    var $config = null;
    this.getConfiguration = function() {
        if ($config == null) {
            $config = dataService.getProperty(DATA_PROPERTY_CONFIGURATION).then(function(data) {
                var configs = this.convert(data);
                var configuration = {};
                for (var i = 0; i < this.defaults.length; i++) {
                    var config = this.defaults[i];
                    configuration[config.key] = configs.hasOwnProperty(config.key)
                        ? { key: config.key, val: configs[config.key] }
                        : config;
                }
                //backward compatibility
                if (configuration.auditorsGroups.val && !Array.isArray(configuration.auditorsGroups.val)) {
                    configuration.auditorsGroups.val = configuration.auditorsGroups.val.split(',');
                }
                if (configuration.workingTimeInStatus.val === true) {
                    configuration.workingTimeInStatus.val = {from: 9, to: 17};
                }
                if (configuration.exportColumns.val && configuration.exportColumns.val.indexOf('username') != -1) {
                    configuration.exportColumns.val.splice(configuration.exportColumns.val.indexOf('username'), 1);
                }

                return configuration;
            }.bind(this));
        }
        return $config;
    };

    this.saveConfiguration = function(config) {
        var hasValue = config.val != null && config.val !== '' && (!angular.isArray(config.val) || config.val.length > 0);
        return hasValue ? this.updateConfiguration(config) : this.deleteConfiguration(config);
    };

    this.convert = function (configs) {
        if (angular.isArray(configs)) {
            configs = configs.reduce(function (configs, config) {
                configs[config.key] = config.val;
                return configs;
            }, {});
        }
        return configs;
    };

    this.updateConfiguration = function(config) {
        var deferred = $q.defer();
        dataService.getProperty(DATA_PROPERTY_CONFIGURATION, null, []).then(function(configs) {
            configs = this.convert(configs);
            configs[config.key] = config.val;
            dataService.setProperty(DATA_PROPERTY_CONFIGURATION, configs).then(function() {
                deferred.resolve(config);
            });
        }.bind(this));
        return deferred.promise;
    };

    this.deleteConfiguration = function(config) {
        var deferred = $q.defer();
        dataService.getProperty(DATA_PROPERTY_CONFIGURATION, null, []).then(function(configs) {
            configs = this.convert(configs);
            delete configs[config.key];
            dataService.setProperty(DATA_PROPERTY_CONFIGURATION, configs).then(function() {
                deferred.resolve({key: config.key});
            });
        }.bind(this));
        return deferred.promise;
    };

    var $userConfiguration = null;
    this.getUserConfiguration = function (user) {
        if (!user) return;
        if ($userConfiguration == null) {
            $userConfiguration = $q.all([this.getUserPreferences(user), this.getConfiguration()]).then(function (configs) {
                var userPref = configs[0];
                var config = angular.copy(configs[1]);
                for (var key in userPref) {
                    if (config[key] != null &&
                        ((angular.isString(userPref[key].val) && userPref[key].val.indexOf('default') < 0)
                        || (!angular.isString(userPref[key].val) && userPref[key].val != null))) {
                        // rewrite config val
                        config[key].val = userPref[key].val;
                    }
                }
                return config;
            }.bind(this));
        }
        return $userConfiguration;
    };

    var $userPreferences = null;
    this.getUserPreferences = function (user) {
        if (!user) return;
        if ($userPreferences == null) {
            $userPreferences = dataService.getProperty(DATA_PROPERTY_PREFERENCES_4, user).then(function (userPref) {
                var userPreferences = {
                    weekendType: {key: 'weekendType', val: 'default'},
                    workingTimeInStatus: {key: 'workingTimeInStatus'},
                    durationType: {key: 'durationType', val: 'default'},
                    durationTypeForExport: {key: 'durationTypeForExport', val: 'h'},
                    inProgressIssuesJql: {key: 'inProgressIssuesJql'},
                    maxFractionDigits: {key: 'maxFractionDigits'},
                    dateFields: {key: 'dateFields'},
                    showDetailsCount: {key: 'showDetailsCount'}
                };
                for (var key in userPref) {
                    userPreferences[key] = {key: key, val: userPref[key]};
                }
                return userPreferences;
            }.bind(this));
        }
        return $userPreferences;
    };

    this.saveUserPreferences = function(config, user) {
        if (config.val == null || config.val == '') {
            return this.deleteUserPreferences(config, user);
        } else {
            if (angular.isArray(config.val)) {
                if (config.val.indexOf('default') >= 0) {
                    config.val = ['default'];
                }
            } else if (angular.isString(config.val)) {
                if (config.val.indexOf('default') >= 0) {
                    config.val = 'default';
                }
            }
            return this.updateUserPreferences(config, user);
        }
    };

    this.updateUserPreferences = function(config, user) {
        var deferred = $q.defer();
        dataService.getProperty(DATA_PROPERTY_PREFERENCES_4, user, {}).then(function(configs) {
            configs = this.convert(configs); // FIXME: remove
            configs[config.key] = config.val;
            dataService.setProperty(DATA_PROPERTY_PREFERENCES_4 + user.accountId, configs).then(function() {
                deferred.resolve(config);
            });
        }.bind(this));
        return deferred.promise;
    };

    this.deleteUserPreferences = function(config, user) {
        var deferred = $q.defer();
        dataService.getProperty(DATA_PROPERTY_PREFERENCES_4, user).then(function(configs) {
            delete configs[config.key];
            dataService.setProperty(DATA_PROPERTY_PREFERENCES_4 + user.accountId, configs).then(function() {
                deferred.resolve({key: config.key});
            });
        }.bind(this));
        return deferred.promise;
    };

    this.getAllCompositionIssueLinks = function() {
        var service = this;
        var deferred = $q.defer();

        if (service.data.allCompositionIssueLinks == null) {
            AP.request({
                url: '/rest/api/2/issueLinkType',
                dataType: 'json',
                success: function(data) {
                    data = TimesheetUtils.getJson(data);
                    service.data.allCompositionIssueLinks = [];
                    for (var i = 0; i < data.issueLinkTypes.length; i++) {
                        service.data.allCompositionIssueLinks.push(data.issueLinkTypes[i].name);
                    }
                    deferred.resolve(service.data.allCompositionIssueLinks);
                },
                error: logger.error
            });
        } else {
            deferred.resolve(service.data.allCompositionIssueLinks);
        }
        return deferred.promise;
    };

    this.getAllParentIssueFields = function() {
        var service = this;
        var deferred = $q.defer();

        if (service.data.allParentIssueFields == null) {
            AP.request({
                url: '/rest/api/2/field',
                dataType: 'json',
                success: function(data) {
                    data = TimesheetUtils.getJson(data);
                    service.data.allParentIssueFields = [];
                    for (var i = 0; i < data.length; i++) {
                        if (data[i].custom == true && data[i].schema != null && data[i].schema.custom == "com.pyxis.greenhopper.jira:gh-epic-link") {
                            service.data.allParentIssueFields.push({id:data[i].id, name: data[i].name});
                        }
                    }
                    deferred.resolve(service.data.allParentIssueFields);
                },
                error: logger.error
            });
        } else {
            deferred.resolve(service.data.allParentIssueFields);
        }

        return deferred.promise;
    };

    this.getAllIssueTypes = function() {
        var service = this;
        var deferred = $q.defer();

        if (service.data.allIssueTypes == null) {
            AP.request({
                url: '/rest/api/2/issuetype',
                dataType: 'json',
                success: function(data) {
                    data = TimesheetUtils.getJson(data);
                    service.data.allIssueTypes = data;
                    deferred.resolve(service.data.allIssueTypes);
                },
                error: logger.error
            });
        } else {
            deferred.resolve(service.data.allIssueTypes);
        }

        return deferred.promise;
    };

    this.getProjects = function() {
        var service = this;
        var deferred = $q.defer();

        if (service.data.allProjects == null) {
            var url = '/rest/api/2/project';
            AP.request({
                url: url,
                dataType: 'json',
                success: function(data) {
                    data = TimesheetUtils.getJson(data);
                    service.data.allProjects = data;
                    deferred.resolve(data);
                },
                error: logger.error
            })
        } else {
            deferred.resolve(service.data.allProjects)
        }

        return deferred.promise;
    };

    this.getRoles = function(projectKey) {
        var service = this;

        if (service.data.allRoles == null) {
            service.data.allRoles = {};
        }
        if (!service.data.allRoles.hasOwnProperty(projectKey)) {
            var deferred = $q.defer();
            service.data.allRoles[projectKey] = deferred.promise;
            var url = '/rest/api/2/project/' + projectKey + '/role';
            AP.request({
                url: url,
                dataType: 'json',
                success: function(data) {
                    data = TimesheetUtils.getJson(data);
                    deferred.resolve(data);
                },
                error: function () {
                    deferred.reject();
                    /*
                    // FIXME: ACJIRA-978
                    var args = Array.prototype.slice.call(arguments);
                    args.push(url, deferred);
                    pivottableService.onError.apply(pivottableService, args);
                    */
                }
            });
            return deferred.promise;
        } else {
            return service.data.allRoles[projectKey];
        }
    };

    this.getAllRoles = function() {
        var service = this;
        var deferred = $q.defer();
        var allRoles = {};
        var styles = {};
        this.getProjects().then(function (projects) {
            var promises = [];
            for (var proj in projects) {
                var project = projects[proj];
                if (!styles[project.style]) {
                    styles[project.style] = true;
                    (function (project) {
                        var promise = service.getRoles(project.key).then(function (roles) {
                            delete roles['atlassian-addons-project-access'];
                            for (var role in roles) {
                                allRoles[role] = {style: project.style};
                            }
                        });
                        promises.push(promise);
                    })(project);
                }
            }
            $q.all(promises).then(function() {
                deferred.resolve(allRoles);
            });
        });
        return deferred.promise;
    };

    // FIXME: https://ecosystem.atlassian.net/browse/ACJIRA-424
    this.getAllRolesBak = function() {
        var service = this;
        var deferred = $q.defer();

        if (service.data.allRoles == null) {
            AP.request({
                url: '/rest/api/2/role',
                dataType: 'json',
                success: function(data) {
                    data = TimesheetUtils.getJson(data);
                    service.data.allRoles = data;
                    deferred.resolve(service.data.allRoles);
                },
                error: logger.error
            });
        } else {
            deferred.resolve(service.data.allRoles);
        }

        return deferred.promise;
    };

    this.getAllStatuses = function () {
        var service = this;
        var deferred = $q.defer();

        if (service.data.allStatuses == null) {
            var url = '/rest/api/2/status';
            AP.request({
                url: url,
                dataType: 'json',
                success: function (data) {
                    data = TimesheetUtils.getJson(data);
                    var statuses = {};
                    for (var i = 0; i < data.length; i++) {
                        statuses[data[i].id] = {order: i, categoryId: "" + data[i].statusCategory.id};
                    }
                    service.data.allStatuses = statuses;
                    deferred.resolve(statuses);
                },
                error: logger.error
            })
        } else {
            deferred.resolve(service.data.allStatuses)
        }
        return deferred.promise;
    };

    this.getAllStatusCategories = function () {
        var service = this;
        var deferred = $q.defer();

        if (service.data.allStatusCategories == null) {
            var url = '/rest/api/2/statuscategory';
            AP.request({
                url: url,
                dataType: 'json',
                success: function (data) {
                    data = TimesheetUtils.getJson(data);
                    service.data.allStatusCategories = data;
                    deferred.resolve(data);
                },
                error: logger.error
            })
        } else {
            deferred.resolve(service.data.allStatusCategories)
        }
        return deferred.promise;
    };

    this.resetState = function() {
        $config = null;
        $userPreferences = null;
        $userConfiguration = null;
        this.data.allCompositionIssueLinks = null;
        this.data.allParentIssueFields = null;
        this.data.allStatusCategories = null;
        this.data.allStatuses = null;
        this.data.allRoles = null;
        this.data.allProjects = null;
    };

    this.getDefaultTimeTrackingColumns = function () {
        return ['1timeoriginalestimate', '2esttimeremaining', '3timespent', '4diff', '5originalestimateremaining', '6progress'];
    };

    this.getAllTimeTrackingColumns = function () {
        var defaultTimeTrackingColumns = this.getDefaultTimeTrackingColumns().slice();
        defaultTimeTrackingColumns.splice(1, 0, '12estimate');
        return defaultTimeTrackingColumns;
    };

    this.getDefaultTimeBalanceColumns = function () {
        return ["3timespent", "12estimate", "4diff", "1timeoriginalestimate", "6progress"];
    };

    this.getAllTimeBalanceColumns = function () {
        return this.getDefaultTimeBalanceColumns().concat(['2esttimeremaining', '5originalestimateremaining']);
    };

    this.getDefaultExportColumns = function () {
        return ['project', 'issuetype', 'key', 'summary', 'priority', 'datestarted', 'displayname', 'descriptionstatus'];
    };

    this.getAllExportColumns = function () {
        var allExportColumns = this.getDefaultExportColumns().slice();
        allExportColumns.splice(5, 0, 'workcreated', 'worklogid');
        allExportColumns.splice(9, 0, 'emailaddress');
        return allExportColumns;
    };

    this.getDefaultDateFields = function () {
        return ['created', 'updated'];
    };

    this.getAllDateFields = function () {
        return ['created', 'updated', 'resolved'];
    };

}]);

configurationModule.service('dataService', ['$q', 'applicationLoggingService', '$http', function ($q, logger) {
    var AP = window.RA || window.AP;
    var ADDON_URL = '/rest/atlassian-connect/1/addons/timereports';
    var PROPERTIES_URL = ADDON_URL + '/properties/';

    // https://developer.atlassian.com/static/connect/docs/latest/concepts/hosted-data-storage.html

    var $init = $q.defer();
    var hasProperty = {};

    this.getProperty = function(prefix, user, defaultValue) {
        var deferred = $q.defer();
        var dataService = this;

        var getProperty = function(name) {
            var deferred = $q.defer();
            AP.request({
                url: PROPERTIES_URL + name,
                success: function(data) {
                    var property = TimesheetUtils.getJson(data);
                    var value = TimesheetUtils.getJson(property.value);
                    deferred.resolve(value);
                },
                error: function () {
                    deferred.resolve(defaultValue || {});
                }
            });
            return deferred.promise;
        };

        $init.promise.then(function() {
            var newKey = prefix + (user ? user.accountId : '');
            var oldKey = prefix + (user ? user.key : '');
            if (!hasProperty[newKey]) {
                if (!hasProperty[oldKey] || (user && !user.key)) {
                    deferred.resolve(defaultValue || {});
                } else {
                    getProperty(oldKey).then(function (property) {
                        var resetProperty = function () {
                            if (!property) {
                                dataService.deleteProperty(oldKey).catch(logger.error);
                            } else {
                                dataService.setProperty(newKey, property).then(function () {
                                    return dataService.deleteProperty(oldKey);
                                }).catch(logger.error);
                            }
                            deferred.resolve(property);
                        };
                        var changeUser = function (i) {
                            var deferredUser = $q.defer();
                            AP.request({
                                url: '/rest/api/2/user?key=' + encodeURIComponent(property[i].user),
                                dataType: 'json',
                                success: function (data) {
                                    data = TimesheetUtils.getJson(data);
                                    property[i].user = data.accountId;
                                    deferredUser.resolve();
                                },
                                error: deferredUser.resolve
                            });
                            return deferredUser.promise;
                        };
                        if (!property) {
                            resetProperty();
                        } else {
                            var userPromises = [];
                            for (var i = 0; i < property.length; i++) {
                                if (property[i].user) {
                                    userPromises.push(changeUser(i));
                                }
                            }
                            $q.all(userPromises).then(resetProperty);
                        }
                    })
                }
            } else {
                getProperty(newKey).then(deferred.resolve);
            }
        });
        return deferred.promise;
    };

    this.setProperty = function(name, value) {
        var deferred = $q.defer();

        AP.request({
            url: PROPERTIES_URL + name,
            type: 'PUT',
            data: JSON.stringify(value),
            contentType: 'application/json',
            success: function(data) {
                var property = TimesheetUtils.getJson(data);
                var value = TimesheetUtils.getJson(property.value);
                hasProperty[name] = true;
                deferred.resolve(value);
            },
            error: function(e) {
                deferred.reject(e);
            }
        });
        return deferred.promise;
    };

    this.deleteProperty = function(name) {
        var deferred = $q.defer();

        AP.request({
            url: PROPERTIES_URL + name,
            type: 'DELETE',
            contentType: 'application/json',
            success: function () {
                hasProperty[name] = false;
                deferred.resolve();
            },
            error: function(e) {
                deferred.reject(e);
            }
        });
        return deferred.promise;
    };

    AP.request({
        url: PROPERTIES_URL,
        success: function(data) {
            data = TimesheetUtils.getJson(data);
            for (var i = 0; i < data.keys.length; i++) {
                var key = data.keys[i];
                hasProperty[key.key] = true;
            }
            $init.resolve();
        }
    });

    // ACJIRA-877
    AP.request({
        url: ADDON_URL,
        success: function(data) {
            logger.license = TimesheetUtils.getJson(data);
        }
    });
}]);
