app.controller('ConfigurationController', ['$scope', '$route', '$routeParams', '$location', 'timesheetParams',
       '$q', '$sce', '$timeout', '$window', 'configurationService', '$http', 'loggedInUser', 'translations',
    function ($scope, $route, $routeParams, $location, timesheetParams,
        $q, $sce, $timeout, $window, configurationService, $http, loggedInUser, translations) {

        var AP = window.RA || window.AP;

        $scope.pages = {
            'access': 'Access',
            'hierarchy': 'Issues Hierarchy',
            'defaults': 'User Defaults',
            'general': 'General'
        };
        $scope.pageKeys = Object.keys($scope.pages);
        $scope.page = $scope.pageKeys.indexOf(timesheetParams.page) >= 0 ? timesheetParams.page : 'access';
        init();

        $scope.isTimesheet = window.isTimesheet;

        function showSaveSuccess(varName) {
            showSaveMessage(varName + 'SaveSuccess');
        }

        function showSaveError(varName) {
            showSaveMessage(varName + 'SaveError');
        }

        function showSaveMessage(varName) {
            $scope[varName] = true;
            $timeout(function(){$scope[varName] = false;}, 2000);
        }

        function init() {

            $scope.activePage = function (page) {
                $scope.page = page;
                if (angular.isFunction(AP.history.pushState)) {
                    AP.history.pushState($.param({page: $scope.page}, /* traditional */ true));
                }
            };

            $scope.contentTemplate = function() {
                return $scope.templates['configure']; // furl
            };

            $scope.saveAuditorsGroups = function() {
                $scope.auditorsGroups.val = $scope.auditorsGroups.val ? $scope.auditorsGroups.val.split(',') : undefined;
                configurationService.saveConfiguration($scope.auditorsGroups).then(function(config){
                    $scope.auditorsGroups = config;
                    showSaveSuccess('auditorsGroups');
                });
            };

            $scope.saveRestrictedGroups = function() {
                $scope.restrictedGroups.val = $scope.restrictedGroups.val ? $scope.restrictedGroups.val.split(',') : undefined;
                configurationService.saveConfiguration($scope.restrictedGroups).then(function(config){
                    $scope.restrictedGroups = config;
                    showSaveSuccess('restrictedGroups');
                });
            };

            $scope.saveAuditorsRoles = function() {
                configurationService.saveConfiguration($scope.auditorsRoles).then(function(config){
                    $scope.auditorsRoles = config;
                    showSaveSuccess('auditorsRoles');
                });
            };

            $scope.saveLogWorkForOthersGroups = function() {
                $scope.logWorkForOthersGroups.val = $scope.logWorkForOthersGroups.val ? $scope.logWorkForOthersGroups.val.split(',') : undefined;
                configurationService.saveConfiguration($scope.logWorkForOthersGroups).then(function(config){
                    $scope.logWorkForOthersGroups = config;
                    showSaveSuccess('logWorkForOthersGroups');
                });
            };

            $scope.saveCompositionIssueLink = function() {
                configurationService.saveConfiguration($scope.compositionIssueLink).then(function(config){
                    $scope.compositionIssueLink = config;
                    showSaveSuccess('compositionIssueLink');
                });
            };

            $scope.saveParentIssueField = function() {
                configurationService.saveConfiguration($scope.parentIssueField).then(function(config){
                    $scope.parentIssueField = config;
                    showSaveSuccess('parentIssueField');
                });
            };

            $scope.saveParentIssueType = function() {
                configurationService.saveConfiguration($scope.parentIssueType).then(function(config){
                    $scope.parentIssueType = config;
                    showSaveSuccess('parentIssueType');
                });
            };

            $scope.saveWeekendType = function() {
                var weekendType = angular.copy($scope.weekendType);
                if (weekendType.val == '60') { // do not store default value
                    weekendType.val = null;
                }
                configurationService.saveConfiguration(weekendType).then(function (config) {
                    if (!config.val) {
                        config.val = '60';
                    }
                    $scope.weekendType = config;
                    showSaveSuccess('weekendType');
                });
            };

            $scope.savePreserveStartedTime = function() {
                configurationService.saveConfiguration($scope.preserveStartedTime).then(function (config) {
                    $scope.preserveStartedTime = config;
                    showSaveSuccess('preserveStartedTime');
                });
            };

            $scope.saveTimeInStatusCategories = function() {
                $scope.timeInStatusCategories.val = $scope.timeInStatusCategories.val ? $scope.timeInStatusCategories.val.split(',') : undefined;
                configurationService.saveConfiguration($scope.timeInStatusCategories).then(function (config) {
                    $scope.timeInStatusCategories = config;
                    showSaveSuccess('timeInStatusCategories');
                });
            };

            $scope.saveTimeTrackingColumns = function() {
                $scope.timeTrackingColumns.val = $scope.timeTrackingColumns.val ? $scope.timeTrackingColumns.val.split(',') : undefined;
                configurationService.saveConfiguration($scope.timeTrackingColumns).then(function (config) {
                    $scope.timeTrackingColumns = config;
                    showSaveSuccess('timeTrackingColumns');
                });
            };

            $scope.saveTimeBalanceColumns = function() {
                $scope.timeBalanceColumns.val = $scope.timeBalanceColumns.val ? $scope.timeBalanceColumns.val.split(',') : undefined;
                configurationService.saveConfiguration($scope.timeBalanceColumns).then(function (config) {
                    $scope.timeBalanceColumns = config;
                    showSaveSuccess('timeBalanceColumns');
                });
            };

            $scope.saveExportColumns = function() {
                $scope.exportColumns.val = $scope.exportColumns.val ? $scope.exportColumns.val.split(',') : undefined;
                configurationService.saveConfiguration($scope.exportColumns).then(function (config) {
                    $scope.exportColumns = config;
                    showSaveSuccess('exportColumns');
                });
            };

            $scope.saveWorkingTimeInStatus = function() {
                configurationService.saveConfiguration($scope.workingTimeInStatus).then(function(config){
                    $scope.workingTimeInStatus = config;
                    showSaveSuccess('workingTimeInStatus');
                });
            };

            $scope.saveStartedTimeInStatus = function() {
                configurationService.saveConfiguration($scope.startedTimeInStatus).then(function(config){
                    $scope.startedTimeInStatus = config;
                    showSaveSuccess('startedTimeInStatus');
                });
            };

            $scope.saveDurationType = function() {
                if ($scope.durationType.val == 'h') { // do not store default value
                    $scope.durationType.val = null;
                }
                var $cleanUp;
                if ($scope.prettyDuration.val != null) {
                    $scope.prettyDuration.val = null;
                    $cleanUp = configurationService.saveConfiguration($scope.prettyDuration); // erase old config
                } else {
                    $cleanUp = $q.resolve();
                }
                $cleanUp.then(function() {
                    configurationService.saveConfiguration($scope.durationType).then(function(config) {
                        $scope.durationType = config;
                        if (!$scope.durationType.val) {
                            $scope.durationType.val = 'h';
                        }
                        showSaveSuccess('durationType');
                    });
                    if (!$scope.durationType.val) {
                        $scope.durationType.val = 'h';
                    }
                });
            };

            $scope.saveMaxFractionDigits = function() {
                if ($scope.maxFractionDigits.val == 2) { // do not store default value
                    $scope.maxFractionDigits.val = null;
                }
                configurationService.saveConfiguration($scope.maxFractionDigits).then(function(config){
                    $scope.maxFractionDigits = config;
                    if ($scope.maxFractionDigits.val == null) {
                        $scope.maxFractionDigits.val = 2;
                    }
                    showSaveSuccess('maxFractionDigits');
                });
                if ($scope.maxFractionDigits.val == null) {
                    $scope.maxFractionDigits.val = 2;
                }
            };

            $scope.saveCacheWorklog = function() {
                configurationService.saveConfiguration($scope.cacheWorklog).then(function(config){
                    $scope.cacheWorklog = config;
                    showSaveSuccess('cacheWorklog');
                });
            };

            $scope.saveWorklogVisibilityGroup = function() {
                configurationService.saveConfiguration($scope.worklogVisibilityGroup).then(function(config){
                    $scope.worklogVisibilityGroup = config;
                    showSaveSuccess('worklogVisibilityGroup');
                });
            };

            $scope.saveWorklogVisibilityRole = function() {
                configurationService.saveConfiguration($scope.worklogVisibilityRole).then(function(config){
                    $scope.worklogVisibilityRole = config;
                    showSaveSuccess('worklogVisibilityRole');
                });
            };

            $scope.saveTimeentryPanelGroups = function() {
                $scope.timeentryPanelGroups.val = $scope.timeentryPanelGroups.val ? $scope.timeentryPanelGroups.val.split(',') : undefined;
                configurationService.saveConfiguration($scope.timeentryPanelGroups).then(function(config){
                    $scope.timeentryPanelGroups = config;
                    showSaveSuccess('timeentryPanelGroups');
                });
            };

            $scope.saveTimeentryPanelProjects = function() {
                $scope.timeentryPanelProjects.val = $scope.timeentryPanelProjects.val ? $scope.timeentryPanelProjects.val.split(',') : undefined;
                configurationService.saveConfiguration($scope.timeentryPanelProjects).then(function(config){
                    $scope.timeentryPanelProjects = config;
                    showSaveSuccess('timeentryPanelProjects');
                });
            };

            var saveWorkLabels = function() {
                configurationService.saveConfiguration($scope.workLabels).then(function(config){
                    $scope.workLabels = config;
                    if (!$scope.workLabels.val) {
                        $scope.workLabels.val = [];
                    }
                    showSaveSuccess('workLabels');
                });
            };
            $scope.addWorkLabel = function(newWorkLabel) {
                if (newWorkLabel && $scope.workLabels.val.indexOf(newWorkLabel) == -1) {
                    $scope.workLabels.val.push(newWorkLabel);
                    $scope.workLabels.val.sort(); // TODO: localeCompare?
                    saveWorkLabels();
                } else {
                    showSaveError('workLabels');
                }
            };
            $scope.removeWorkLabel = function(index) {
                $scope.workLabels.val.splice(index, 1);
                saveWorkLabels();
            };

            $scope.saveDisableLogWork = function() {
                configurationService.saveConfiguration($scope.disableLogWork).then(function (config) {
                    $scope.disableLogWork = config;
                    showSaveSuccess('disableLogWork');
                });
            };

            $scope.saveWorkDescriptionRequired = function() {
                configurationService.saveConfiguration($scope.workDescriptionRequired).then(function (config) {
                    $scope.workDescriptionRequired = config;
                    showSaveSuccess('workDescriptionRequired');
                });
            };

            $scope.saveInProgressIssuesJql = function(configForm) {
                if (configForm.inProgressIssuesJql.$valid) { // do not save empty value in case of $invalid
                    configurationService.saveConfiguration($scope.inProgressIssuesJql).then(function (config) {
                        $scope.inProgressIssuesJql = config;
                        showSaveSuccess('inProgressIssuesJql');
                    });
                }
            };

            $scope.saveReportingDay = function() {
                configurationService.saveConfiguration($scope.reportingDay).then(function(config){
                    $scope.reportingDay = config;
                    showSaveSuccess('reportingDay');
                });
            };

            $scope.saveWorkUnderLimitColor = function() {
                if ($scope.workUnderLimitColor.val === '#000000') {
                    delete $scope.workUnderLimitColor.val;
                }
                configurationService.saveConfiguration($scope.workUnderLimitColor).then(function(config){
                    $scope.workUnderLimitColor = {key: 'workUnderLimitColor', val: config.val || '#000000'};
                    showSaveSuccess('workUnderLimitColor');
                });
            };

            $scope.saveWorkOverLimitColor = function() {
                if ($scope.workOverLimitColor.val === '#000000') {
                    delete $scope.workOverLimitColor.val;
                }
                configurationService.saveConfiguration($scope.workOverLimitColor).then(function(config){
                    $scope.workOverLimitColor = {key: 'workOverLimitColor', val: config.val || '#000000'};
                    showSaveSuccess('workOverLimitColor');
                });
            };

            $scope.getApiKey = function () {
                $http.get('/api/key?accountId=' + encodeURIComponent(loggedInUser.accountId))
                    .then(function (res) {
                        AP.flag.create({
                            title: 'REST Key',
                            body: res.data,
                            type: 'success'
                        });
                    }, function (err) {
                        AP.flag.create({
                            title: 'REST Key',
                            body: err.data || err || 'Something went wrong',
                            type: 'error'
                        });
                    });
            };

            var $config = configurationService.getConfiguration();
            var $availableLinks = configurationService.getAllCompositionIssueLinks();
            var $parentIssueFields = configurationService.getAllParentIssueFields();
            var $parentIssueTypes = configurationService.getAllIssueTypes();
            var $roles = configurationService.getAllRoles();
            var $statusCategories = configurationService.getAllStatusCategories();
            var $projects = configurationService.getProjects();

            $q.all([$config, $availableLinks, $parentIssueFields, $parentIssueTypes, $roles, $statusCategories, $projects]).then(function (args) {

                var config = args[0];
                var availableLinks = args[1];
                $scope.parentIssueFields = args[2];
                $scope.parentIssueTypes = args[3];
                var roles = args[4];
                var statusCategories = args[5];
                var projects = args[6];

                var correctEmptyValue = function(option) {
                    if (option.val == null) {
                        option.val = '';
                    }
                };

                $scope.auditorsGroups = config.auditorsGroups;
                $scope.restrictedGroups = config.restrictedGroups;
                $scope.auditorsRoles = config.auditorsRoles;
                $scope.logWorkForOthersGroups = config.logWorkForOthersGroups;
                $scope.compositionIssueLink = config.compositionIssueLink;
                correctEmptyValue($scope.compositionIssueLink);
                $scope.parentIssueField = config.parentIssueField;
                correctEmptyValue($scope.parentIssueField);
                $scope.parentIssueType = config.parentIssueType;
                correctEmptyValue($scope.parentIssueType);
                $scope.weekendType = config.weekendType;
                $scope.preserveStartedTime = config.preserveStartedTime;
                $scope.timeInStatusCategories = config.timeInStatusCategories;
                $scope.workingTimeInStatus = config.workingTimeInStatus;
                $scope.startedTimeInStatus = config.startedTimeInStatus;
                $scope.prettyDuration = config.prettyDuration;
                $scope.durationType = config.durationType;
                $scope.maxFractionDigits = config.maxFractionDigits;
                $scope.worklogVisibilityGroup = config.worklogVisibilityGroup;
                $scope.worklogVisibilityRole = config.worklogVisibilityRole;
                $scope.timeentryPanelGroups = config.timeentryPanelGroups;
                $scope.timeentryPanelProjects = config.timeentryPanelProjects;
                $scope.timeTrackingColumns = config.timeTrackingColumns;
                $scope.timeBalanceColumns = config.timeBalanceColumns;
                $scope.exportColumns = config.exportColumns;
                $scope.disableLogWork = config.disableLogWork;
                $scope.workDescriptionRequired = config.workDescriptionRequired;
                $scope.workLabels = config.workLabels;
                $scope.cacheWorklog = config.cacheWorklog; // might be unavailable in current browser
                $scope.inProgressIssuesJql = config.inProgressIssuesJql;
                $scope.workUnderLimitColor = {key: 'workUnderLimitColor', val: config.workUnderLimitColor.val || '#000000'};
                $scope.workOverLimitColor = {key: 'workOverLimitColor', val: config.workOverLimitColor.val || '#000000'};

                if ($scope.prettyDuration.val) {
                    $scope.durationType.val = 'pretty';
                }

                $scope.allCompositionIssueLinks = TimesheetUtils.makeUISelectList(availableLinks, true);
                $scope.parentIssueFields.unshift({id: '', name: ''});
                $scope.parentIssueTypes.unshift({id: '', name: ''});
                $scope.worklogVisibilityRoles = config.worklogVisibilityRole;
                $scope.projectsRoles = new TimesheetSelectOptions(TimesheetGeneralOption, null, null, {'classic': 'Classic Roles', 'next-gen': 'Next-Gen Roles'});
                for (var role in roles) {
                    $scope.projectsRoles.addOption(role, role, false, roles[role].style);
                }
                $scope.projectsRoles.sort();

                $scope.timeInStatusCategoriesData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.timeInStatusCategories.val);
                for (var i in statusCategories) {
                    var statusCategory = statusCategories[i];
                    $scope.timeInStatusCategoriesData.addOption(statusCategory.name, statusCategory.id);
                }

                $scope.timeTrackingColumnsData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.timeTrackingColumns.val);
                var timeTrackingColumns = configurationService.getAllTimeTrackingColumns();
                for (var i in timeTrackingColumns) {
                    $scope.timeTrackingColumnsData.addOption(translations[timeTrackingColumns[i]], timeTrackingColumns[i]);
                }

                $scope.timeBalanceColumnsData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.timeBalanceColumns.val);
                var timeBalanceColumns = configurationService.getAllTimeBalanceColumns();
                for (var i in timeBalanceColumns) {
                    $scope.timeBalanceColumnsData.addOption(translations[timeBalanceColumns[i]], timeBalanceColumns[i]);
                }

                $scope.exportColumnsData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.exportColumns.val);
                var exportColumns = configurationService.getAllExportColumns();
                for (var i in exportColumns) {
                    $scope.exportColumnsData.addOption(translations[exportColumns[i]], exportColumns[i]);
                }

                $scope.timeentryPanelProjectsData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.timeentryPanelProjects.val);
                for (var i = 0; i < projects.length; i++) {
                    $scope.timeentryPanelProjectsData.addOption(projects[i].name + ' (' + projects[i].key + ')' , projects[i].key);
                }

                $scope.allWeekendTypes = [];
                $scope.allWeekendTypes.push({id: '60', label: 'Sat-Sun'}); // default
                $scope.allWeekendTypes.push({id: '56', label: 'Fri-Sat'}); // Islamic
                $scope.allWeekendTypes.push({id: '45', label: 'Thu-Fri'}); // Arabic
                $scope.allWeekendTypes.push({id: '0', label: 'Sun'}); // Indian
                $scope.allWeekendTypes.push({id: '5', label: 'Fri'}); // Solar
                $scope.allWeekendTypes.push({id: '560', label: 'Fri-Sun'}); // 4 10 hours
                $scope.allDurationTypes = [];
                $scope.allDurationTypes.push({id: 'm', label: 'Minutes'});
                $scope.allDurationTypes.push({id: 'h', label: 'Hours'}); // default
                $scope.allDurationTypes.push({id: 'd', label: 'Days'});
                $scope.allDurationTypes.push({id: 'pretty', label: 'Pretty Duration'});

                $scope.reportingDay = config.reportingDay;
                $scope.reportingDayOptions = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.reportingDay.val);
                $scope.reportingDayOptions.addOption('', undefined);
                $scope.reportingDayOptions.addOption(translations['Today'], -1);
                var day = moment();
                for (var i = 0; i < 7; i++) {
                    var d = (moment.localeData().firstDayOfWeek() + i) % 7; // in the order of locale week
                    day.day(d);
                    $scope.reportingDayOptions.addOption(moment.localeData().weekdays(day), d);
                }
            });
        }
    }
]);
