function TimesheetStrategy(rowKeyClass, columnKeyClass, options) {
    this.rowKeyClass = rowKeyClass;
    this.columnKeyClass = columnKeyClass;
    this.options = TimesheetStrategy.getStrategyOptions(options);
    this.configOptions = options.configOptions;
}

TimesheetStrategy.getStrategyOptions = function(options) {
    var monthView = options.monthView;
    var calculatedStartDate =
        options.endDate
            ? TimesheetUtils.addDays(options.endDate, -6)
            : TimesheetUtils.getLastMondayDate(options.reportingDay);
    var startDate =
        options.startDate
            ? moment(options.startDate).toDate()
            : monthView
                ? TimesheetUtils.addDays(calculatedStartDate, -3*7)
                : calculatedStartDate;
    var endDate =
        options.endDate
            ? moment(options.endDate).toDate()
            : monthView
                ? TimesheetUtils.addDays(startDate, 4 * 7 - 1)
                : TimesheetUtils.addDays(startDate, 6);
    var result = {
        startDate: startDate,
        endDate: endDate
    };
    if (options.worklogAuthors) {
        result.worklogAuthors = options.worklogAuthors;
    } else if (options.user) {
        result.worklogAuthors = [options.user];
    }
    if (options.restrictedProjects) {
        result.restrictedProjects = options.restrictedProjects;
    }
    result.loggedInUser = options.loggedInUser;
    result.monthView = options.monthView;
    result.sum = options.sum;
    result.includeEmpty = options.includeEmpty;
    result.reportingDay = options.reportingDay;
    result.showWeekends = options.showWeekends;
    result.groupByField = options.groupByField;
    result.groupByFieldObject = options.groupByFieldObject;
    result.categorizeByField = options.categorizeByField;
    result.categorizeByFieldObject = options.categorizeByFieldObject;
    result.orderByField = options.orderByField;
    result.userInfos = options.userInfos;
    result.configOptions = options.configOptions;
    return result;
};

TimesheetStrategy.prototype.getEntries = function(issue) {
    var entries = [];
    var issueWorklog = issue.worklog || issue.fields.worklog;
    for (var i = 0; i < issueWorklog.worklogs.length; i++) {
        var worklog = issueWorklog.worklogs[i];
        var workDate = worklog.workDate = TimesheetUtils.convertDate(worklog.started,
            !this.configOptions.preserveStartedTime && worklog.author ? worklog.author.timeZone : this.options.loggedInUser.timeZone);
        var endDateIncluded = TimesheetUtils.addDays(this.options.endDate, 1); // endDate should be in range
        if (workDate >= this.options.startDate && workDate < endDateIncluded) { // filtering by start and end dates
            var inGroups = !this.options.worklogAuthors ||  // filtering by author
                (worklog.author && this.options.worklogAuthors.indexOf(worklog.author.accountId) >= 0);
            var restricted = this.options.restrictedProjects ? this.options.restrictedProjects.indexOf(worklog._issue ? worklog._issue.fields.project.key : issue.fields.project.key) >= 0 : false;
            restricted = restricted && (!worklog.author || (worklog.author.accountId != this.options.loggedInUser.accountId));
            if (inGroups && !restricted) {
                var rowKey = new this.rowKeyClass(issue, worklog, this.options);
                if (this.options.includeEmpty && /* sumSubTasks */ issue._subTasks) {
                    rowKey._issue = issue; // trigger summing numeric fields
                    if (!issue.nonEmptySubtasks) {
                        issue.nonEmptySubtasks = [];
                    }
                    if (worklog._issue && issue.nonEmptySubtasks.indexOf(worklog._issue.key) < 0) {
                        issue.nonEmptySubtasks.push(worklog._issue.key);
                        // sum numeric fields child issues with worklogs
                        for (var issueField in worklog._issue.fields) {
                            var field = worklog._issue.fields[issueField];
                            if (field && typeof field == 'number') {
                                if (!issue.fields['original_' + issueField]) {
                                    issue.fields['original_' + issueField] = issue.fields[issueField]; // keep original value
                                }
                                issue.fields[issueField] = (issue.fields[issueField] || 0) + field;
                            }
                        }

                    }
                }
                var columnKey = new this.columnKeyClass(workDate, this.options);
                var entry = new WorkedTimePivotEntry(rowKey, columnKey, worklog);
                entries.push(entry);
            }
        }
    }
    if ((this.options.includeEmpty || issue._inProgressIssue) && ['workeduser', 'workedusergroup', 'workeddate', 'workedweek', 'workcreated', 'commentfirstword', 'worklabel'].indexOf(this.options.groupByField) < 0) {
        var rowKey = new this.rowKeyClass(issue, null, this.options);
        var addEntry = function (issue, value) {
            rowKey = TimesheetUtils.clone(rowKey);
            rowKey._issue = issue;
            var entry = new EmptyPivotEntry(rowKey);
            entry.value = value;
            entries.push(entry);
        };
        if (entries.length == 0) {
            rowKey._issue = issue; // trigger summing numeric fields
            addEntry(issue, this.options.groupByField ? {} : undefined);
        }
        if (issue._subTasks && issue._subTasks.length > 0) {
            issue._subTasks.forEach(function (subTask) {
                if (!issue.nonEmptySubtasks || issue.nonEmptySubtasks.indexOf(subTask.key) < 0) {
                    addEntry(subTask, {});
                }
            });
        }
    }
    return entries;
};

TimesheetStrategy.prototype.getColumnsFunction = function() {
    if (!this.columnsTemplate) {
        var columnsFunction;
        switch (this.options.sum) {
            case "month":
                columnsFunction = this.getYearColumns;
                break;
            case "week":
                columnsFunction = this.getMonthColumns;
                break;
            case "day":
                columnsFunction = this.getWeekColumns;
                break;
            default:
                columnsFunction = this.options.monthView ? this.getMonthColumns : this.getWeekColumns;
        }
        this.columnsTemplate = columnsFunction.apply(this, this.options, this.configOptions);
    }
    return TimesheetUtils.clone(this.columnsTemplate);
};

TimesheetStrategy.prototype.getYearColumns = function() {
    return this.getColumns(TimesheetUtils.getMonthForYear);
};

TimesheetStrategy.prototype.getMonthColumns = function() {
    return this.getColumns(TimesheetUtils.getWeeksForMonth);
};

TimesheetStrategy.prototype.getWeekColumns = function() {
    return this.getColumns(TimesheetUtils.getDatesForWeek);
};

TimesheetStrategy.prototype.getDayColumns = function() {
    return this.getColumns(TimesheetUtils.getHoursForDay);
};

TimesheetStrategy.prototype.getColumns = function(sourceFn) {

    return sourceFn(this.options, this.configOptions).map(function(c) {
        var column = new this.columnKeyClass(moment(c.date).toDate(), this.options);
        column.isToday = c.isToday;
        column.isWeekend = c.isWeekend;
        column.firstDate = c.firstDate;
        column.lastDate = c.lastDate;
        column.workingDays = c.workingDays;
        return column;
    }, this).reduce(function(result, column) {
        result[column.keyValue] = new PivotColumn(column);
        return result;
    }, {});
};

if (typeof module === 'object' && module.exports) {
    module.exports = TimesheetStrategy;
    var TimesheetUtils = require('../../timesheetUtils');
    var moment = require('moment-timezone');
    var PivotColumn = require('./PivotColumn');
    var PivotEntryModule = require('./PivotEntry');
    var WorkedTimePivotEntry = PivotEntryModule.WorkedTimePivotEntry;
    var EmptyPivotEntry = PivotEntryModule.EmptyPivotEntry;
}
