app.controller('GadgetController', ['$scope', '$rootScope', '$route', '$routeParams', '$q', '$location', '$sce', '$timeout' , 'loggedInUser', 'pivottableService',
    function ($scope, $rootScope, $route, $routeParams, $q, $location, $sce, $timeout, loggedInUser, pivottableService) {

        init();

        function init() {

            $scope.contentTemplate = function() {
                return $scope.templates['dialog']; // furl
            };

            var getCurrentConfiguration = function() {
                var isoFormat = 'YYYY-MM-DD';
                return TimesheetUtils.normalizeObject({
                    title: $scope.title,
                    pivotTableType: $scope.$parent.pivotTableType,
                    timeTrackingColumns: $scope.$parent.pivotTableType == 'TimeTracking' ? $scope.$parent.timeTrackingColumns : null,
                    timeBalanceColumns: $scope.$parent.pivotTableType == 'TimeBalance' ? $scope.$parent.timeBalanceColumns : null,
                    timeInStatusCategories: $scope.$parent.pivotTableType == 'IssuePassedTimeByStatus' ? $scope.$parent.timeInStatusCategories : null,
                    group: $scope.$parent.group,
                    excludeGroup: $scope.$parent.excludeGroup,
                    user: $scope.$parent.user,
                    allUsers: $scope.$parent.allUsers.enabled,
                    showDetails: $scope.$parent.showDetails.enabled,
                    sum: $scope.$parent.sum,
                    view: $scope.$parent.view,
                    sumSubTasks: $scope.$parent.sumSubTasks.enabled,
                    includeEmpty: $scope.$parent.includeEmpty.enabled,
                    filterOrProjectId: $scope.$parent.filterOrProjectId,
                    childrenFilterId: $scope.$parent.childrenFilterId,
                    groupByField: $scope.$parent.groupByField,
                    categorizeByField: $scope.$parent.categorizeByField,
                    moreFields: $scope.$parent.moreFields,
                    orderByField: $scope.$parent.orderByField,
                    numOfWeeks: $scope.$parent.numOfWeeks.value,
                    offset: $scope.$parent.offset,
                    startDate: $scope.$parent.startDate.value ? moment($scope.$parent.startDate.value).format(isoFormat) : undefined,
                    endDate: $scope.$parent.endDate.value ? moment($scope.$parent.endDate.value).format(isoFormat) : undefined,
                    reportingDay: $scope.$parent.reportingDayOptions.selected, // ignore parent.reportingDay default value
                    hideWeekends: !$scope.$parent.showWeekends.enabled // make it pass through normalizeObject
                }, true)
            };
            function showChildrenFilter() {
                $scope.showChildrenFilter = (['TimeTracking', 'TimeBalance'].indexOf($scope.$parent.pivotTableType) >= 0 ||
                    pivottableService.isTimeTrackingColumnsInMoreFields($scope.$parent.moreFields))
                    && $scope.$parent.sumSubTasks.enabled;
            }
            $scope.pivotTableTypeChanged = function() {
                $scope.$parent.pivotTableType = $scope.$parent.pivotTableTypeOptions.selected ? $scope.$parent.pivotTableTypeOptions.selected : '';
                showChildrenFilter();
            };
            $scope.timeTrackingColumnsChanged = function(values) {
                $scope.$parent.timeTrackingColumns = values != null && values != "" ? values.split(',') : null;
            };
            $scope.timeBalanceColumnsChanged = function(values) {
                $scope.$parent.timeBalanceColumns = values != null && values != "" ? values.split(',') : null;
            };
            $scope.timeInStatusCategoriesChanged = function(values) {
                $scope.$parent.timeInStatusCategories = values != null && values != "" ? values.split(',') : null;
            };
            $scope.filterByChanged = function(values) {
                $scope.$parent.filterOrProjectId = values != null && values != "" ? values.split(',') : null;
                if (!$scope.$parent.filterOrProjectId && $scope.$parent.includeEmpty.enabled) {
                    $scope.$parent.includeEmpty.wasEnabled = true;
                    $scope.$parent.includeEmpty.enabled = false;
                } else if ($scope.$parent.includeEmpty.wasEnabled) {
                    $scope.$parent.includeEmpty.enabled = true;
                    delete $scope.$parent.includeEmpty.wasEnabled;
                }
            };
            $scope.childrenFilterChanged = function() {
                $scope.$parent.childrenFilterId = $scope.$parent.childrenFilterOptions.selected;
            };
            $scope.groupChange = function(groups) {
                $scope.$parent.group = groups != null && groups != "" ? groups.split(',') : null;
            };
            $scope.excludeGroupChange = function(groups) {
                $scope.$parent.excludeGroup = groups != null && groups != "" ? groups.split(',') : null;
            };
            $scope.userChange = function(user) {
                $scope.$parent.user = user;
            };
            $scope.allUsersChange = function(allUsers) {
                $scope.$parent.allUsers.enabled = allUsers;
            };
            $scope.showDetailsChange = function(showDetails) {
                $scope.$parent.showDetails.enabled = showDetails;
            };
            $scope.sumSubTasksChange = function(sumSubTasks) {
                $scope.$parent.sumSubTasks.enabled = sumSubTasks;
                showChildrenFilter();
            };
            $scope.includeEmptyChange = function(includeEmpty) {
                $scope.$parent.includeEmpty.enabled = includeEmpty;
            };
            $scope.groupByChanged = function() {
                $scope.$parent.groupByField = $scope.$parent.groupByOptions.selected ? $scope.$parent.groupByOptions.selected : "";
            };
            $scope.categorizeByChanged = function() {
                $scope.$parent.categorizeByField = $scope.$parent.categorizeByOptions.selected ? $scope.$parent.categorizeByOptions.selected : "";
            };
            $scope.moreFieldsChanged = function() {
                $scope.$parent.moreFields = $scope.$parent.moreFieldsOptions.selected;
                showChildrenFilter();
            };
            $scope.orderByChanged = function() {
                $scope.$parent.orderByField = $scope.$parent.orderByOptions.selected ? $scope.$parent.orderByOptions.selected : "";
            };
            $scope.numOfWeeksChange = function(numOfWeeks) {
                $scope.$parent.numOfWeeks.value = numOfWeeks;
            };
            $scope.offsetChange = function(offset) {
                $scope.$parent.offset = offset || 0;
            };
            $scope.reportingDayChanged = function() {
                $scope.$parent.reportingDay = $scope.$parent.reportingDayOptions.selected;
            };
            $scope.startChangeDate = function(date) {
                if (date) {
                    $scope.$parent.startDate.value = moment(date).toDate();
                    $scope.$parent.startDate.calculated = null;
                } else {
                    $scope.$parent.startDate.calculated = true;
                }
            };
            $scope.endChangeDate = function(date) {
                if (date) {
                    $scope.$parent.endDate.value = moment(date).toDate();
                    $scope.$parent.endDate.calculated = null;
                } else {
                    $scope.$parent.endDate.calculated = true;
                }
            };
            $scope.sumChanged = function() {
                $scope.$parent.sum = $scope.$parent.sumOptions.selected ? $scope.$parent.sumOptions.selected : "";
            };
            $scope.monthViewChanged = function(monthView) {
                $scope.$parent.view = monthView ? 'month' : '';
            };

            pivottableService.getGadgetConfiguration($scope.$parent.dashboardId, $scope.$parent.dashboardItemId)
                .then(function (config) {
                    var configKeys = Object.keys(config);
                    if (config != null && configKeys.length > 0) {
                        for (var i = 0; i < configKeys.length; i++) {
                            $scope.$parent[configKeys[i]] = config[configKeys[i]];
                        }
                    }
                    // issue#793
                    if (config.user) {
                        $scope.userInfo = $scope.$parent.paramUserInfo;
                    } else {
                        $scope.$parent.user = null;
                    }
                    if (config.numOfWeeks) {
                        $scope.$parent.numOfWeeks = {value: $scope.$parent.numOfWeeks}
                    }
                    $scope.$parent.allUsers = {enabled: $scope.$parent.allUsers === true};
                    $scope.$parent.showWeekends = {enabled: !$scope.$parent.hideWeekends};
                    $scope.$parent.showDetails = {enabled: $scope.$parent.showDetails == true};
                    $scope.$parent.monthView = {enabled: $scope.$parent.view == 'month'};
                    $scope.$parent.sumSubTasks = {enabled: $scope.$parent.sumSubTasks == true};
                    $scope.$parent.includeEmpty = {enabled: $scope.$parent.includeEmpty == true};
                    $scope.$parent.numOfWeeks = {value: $scope.$parent.numOfWeeks.value};
                    if (Array.isArray($scope.$parent.filterOrProjectId) && !$scope.$parent.filterOrProjectId.length) {
                        $scope.$parent.filterOrProjectId = null;
                        $scope.$parent.includeEmpty.enabled = false;
                    }
                    if (!config.startDate || !$scope.$parent.startDate) {
                        $scope.$parent.startDate = {calculated: true};
                    } else {
                        $scope.$parent.startDate = {value: moment($scope.$parent.startDate).toDate()};
                    }
                    if (!config.endDate || !$scope.$parent.endDate) {
                        $scope.$parent.endDate = {calculated: true}
                    } else {
                        $scope.$parent.endDate = {value: moment($scope.$parent.endDate).toDate()};
                    }
                    showChildrenFilter();
                });

            $scope.saveGadgetConfig = function() {
                var configuration = getCurrentConfiguration();
                pivottableService.saveGadgetConfiguration($scope.dashboardId, $scope.dashboardItemId, configuration)
                    .then(function() {
                        $rootScope.$emit('gadgetConfigSave');
                    });
            };

            $scope.cancelGadgetConfigSaving = function() {
                $rootScope.$emit('gadgetConfigCancel');
            };
        }
    }
]);
