function PivotRow(rowKey) {
    this.rowKey = rowKey;
    this.columns = {};
    this.sum = 0;
    this.num = 0;
    this.detailsNum = 0;
    this._issueKeys = [];
    this.data = [];
}
PivotRow.prototype.add = function(pivotEntry) {
    for (var i = 0; i < pivotEntry.columnKeys.length; i++) {
        var columnKey = pivotEntry.columnKeys[i];
        var value = pivotEntry.values[columnKey.keyValue];
        var column = TimesheetUtils.getOrCreate(this.columns, columnKey, PivotColumn);
        column.add(pivotEntry, value);
    }
    this.detailsNum += pivotEntry.columnKeys.length;
    if (typeof pivotEntry.value == 'number') {
        this.sum += pivotEntry.value;
    }
    if (this.rowKey._issue) {
        var issueFields = this.rowKey._issue.fields;
        if (this.rowKey._issue.key != pivotEntry.rowKey._issue.key
                && this.rowKey._issue._sumIssuesIds.indexOf(pivotEntry.rowKey._issue.id) < 0) {
            this.rowKey._issue._sumIssuesIds.push(pivotEntry.rowKey._issue.id);
            for (var issueField in pivotEntry.rowKey._issue.fields) { // iterate over original fields
                var field = pivotEntry.rowKey._issue.fields[issueField];
                if (field && typeof field == 'number') {
                    issueFields[issueField] = issueFields[issueField] || 0; // init
                    issueFields[issueField] += field; // add
                }
            }
        } else if (!this.rowKey._issue._sumIssuesIds) {
            this.rowKey = TimesheetUtils.clone(this.rowKey);
            this.rowKey._issue._sumIssuesIds = [];
            if (this.moreFields) {
                for (var i = 0; i < this.moreFields.length; i++) {
                    var moreField = this.moreFields[i];
                    var field = this.rowKey._issue.fields[moreField];
                    if (typeof field != 'number') {
                        delete this.rowKey._issue.fields[moreField];
                    }
                }
            }
        }
    }
    if (pivotEntry.value !== undefined) {
        this.data.push(pivotEntry);
    }
    var issueKey = pivotEntry.rowKey._issue ? pivotEntry.rowKey._issue.key : pivotEntry.rowKey.keyValue;
    if (this._issueKeys.indexOf(issueKey) < 0) {
        this.num++;
        this._issueKeys.push(issueKey);
    }
};

if (typeof module === 'object' && module.exports) {
    module.exports = PivotRow;
    var TimesheetUtils = require('../../timesheetUtils');
    var PivotColumn = require('./PivotColumn');
}