function TimesheetSelectOptions(optionClass, selected, sortableProperty, sections) {
    this.options = [];
    this.optionClass = optionClass;
    this.sortableProperty = sortableProperty != null ? sortableProperty : "label";
    this.selected = selected != null ? selected : ""; // null or undefined
    this.sections = sections;
}

TimesheetSelectOptions.prototype.getOptions = function() {
    var options = this.options;
    var sections = {};
    if (this.sections) {
        options = [];
        for (var i = 0; i < this.options.length; i++) {
            var option = this.options[i];
            var sectionId = option.section || option.id.split('_')[0];
            if (!sections.hasOwnProperty(sectionId)) {
                var section = sections[sectionId] = {label: this.sections[sectionId], children: []};
                options.push(section);
            }
            sections[sectionId].children.push(option);
        }
    }
    return options;
};

TimesheetSelectOptions.prototype.addOption = function(label, key, addToHead, section) {
    var newOption = new this.optionClass(label, key, section);
    if (addToHead == true) {
        this.options.unshift(newOption);
    }
    else {
        this.options.push(newOption);
    }
    return this;
};

TimesheetSelectOptions.prototype.findOption = function(value, isLabel) {
    for (var i = 0; i < this.options.length; i++) {
        if (this.options[i].isOption(value, isLabel)) {
            return this.options[i];
        }
    }
    return null;
};

TimesheetSelectOptions.prototype.sort = function(desc) {
    TimesheetUtils.sortByProperty(this.options, this.sortableProperty, desc);
};

function TimesheetGeneralOption(label, id, section) {
    this.label = label;
    this.id = id;
    this.section = section;
}

TimesheetGeneralOption.prototype.isOption = function(value, isLabel) {
    return isLabel ? this.label == value : this.id == value;
};

if (typeof module === 'object' && module.exports) {
    module.exports.TimesheetSelectOptions = TimesheetSelectOptions;
    module.exports.TimesheetGeneralOption = TimesheetGeneralOption;
}
