app.service('validationService', [function () {

    this.validate = function(params) {
        var errors = [];
        errors = errors.concat(this.validateDates(params.startDate.value, params.endDate.value));
        return errors;
    };

    this.validateDates = function(start, end) {
        var errors = [];
        if (start != null) {
            if (moment(start).isAfter(moment())) {
                errors.push('error.startDate.afterNow');
            } else if (end != null && moment(start).isAfter(moment(end))) {
                errors.push('error.startDate.afterEnd');
            }
        }
        return errors;
    };
}]);
