Promise.all([
    new Promise(function (resolve) {
        AP.getLocation(resolve);
    }),
    new Promise(function (resolve) {
        AP.context.getContext(resolve);
    }),
    new Promise(function (resolve) {
        AP.user.getLocale(resolve);
})]).then(function (values) {
    var location = values[0], context = values[1];
    window.loggedInUserLocale = values[2];
    var match = location.match(/^(https?\:\/\/[^:\/?#]*(\:[0-9]+)?)([^?#]*)(\?[^#]*)?(#.*)?$/);
    //var search = match[4];
    //https://developer.atlassian.com/static/connect/docs/1.1.33/guides/connect-cookbook.html#all.js
    var getUrlParam = function (param, defaultValue) {
        var matches = (new RegExp('\\W' + param + '=([^&]+)')).exec(window.location.search);
        var codedParam = matches ? matches[1] : (defaultValue || '');
        return decodeURIComponent(codedParam);
    };

    var baseUrl = match[1];

    if (!window.loggerUrl) return; // tests

    var b = function() { // buildNumber
        var scripts = document.getElementsByTagName('script');
        for (var i = 0; i < scripts.length; i += 1) {
            var script = scripts[i];
            var src = script.getAttribute('src');
            var m;
            if (src && (m = src.match(/\/(all|app)\.js\?([0-9a-f]+)/))) {
                return m[2];
            }
        }
        return 1;
    }();

    var isConfiguration = getUrlParam('configuration');
    var isWelcome = getUrlParam('welcome');
    var isIssueTimeBalance = getUrlParam('issuetimebalance');
    var dashboard = context.jira && context.jira.dashboard  ? context.jira.dashboard.id: null;
    var dashboardItem = context.jira && context.jira.dashboardItem  ? context.jira.dashboardItem.id: null;
    var issueKey = context.jira && context.jira.issue ? context.jira.issue.key : null;
    var issueId = context.jira && context.jira.issue ? context.jira.issue.id : null;
    var projectKey = context.jira && context.jira.project ? context.jira.project.key : null;
    var isGadget = !!dashboard || !!issueKey;
    var dialog = getUrlParam('dialog');
    var license = context.license && context.license.active ? 'active' : 'none'; // FIXME: expired?

    var v = getUrlParam('v', 1); // app version
    if (v == '1') { // TODO: remove
        mailerUrl = undefined;
    }

    // FIXME: dashboardItemId, license, hostBaseUrl
    var params = {
        isGadget: isGadget,
        dashboardId: dashboard,
        dashboardItemId: dashboardItem,
        issueKey: issueKey,
        issueId: issueId,
        license: license,
        hostBaseUrl: baseUrl,
        projectKey: projectKey
    };

    if (isIssueTimeBalance) {
        params.issueTimeBalance = true;
    }

    if (params.license == 'none') {
        var tenantName = baseUrl.match(/https:\/\/(.+?)\./)[1];
        if (['timereports', 'avelytchenko'].indexOf(tenantName) != -1) {
            params.license = 'active';
        }
    }
    if (v == 3 && params.license == 'active') {
        params.subscribe = true;
        window.isTimesheet = true;
    }

    if (v == 3) {
        params.appKey = 'jira-timesheet-plugin';
        params.appName = 'Timesheet Reports and Gadgets';
    } else {
        params.appKey = 'timereports';
        params.appName = 'Time Reports';
    }

    if (isWelcome) {
        window.mainController = 'WelcomeController';
        window.mainTemplate = '/templates/mainShort.html?_=' + b;
        params.templates = {
            welcome: '/templates/welcome.html?_=' + b
        };
        params.isWelcome = true;
    } else if (isConfiguration) {
        window.mainController = 'ConfigurationController';
        window.mainTemplate = '/templates/mainShort.html?_=' + b;
        params.templates = {
            menu: '/templates/configMenu.html?_=' + b,
            configure: '/templates/configure.html?_=' + b
        };
    } else if (dialog) {
        switch (dialog) {
            case 'timereports':
                window.mainController = 'TimesheetController';
                window.mainTemplate = '/templates/main.html?_=' + b;
                params.isGadget = true;
                params.reportDialog = true;
                params.templates = {
                    menu: '/templates/menu.html?_=' + b
                };
                break;
            case 'bitbucket':
                window.mainController = 'BitbucketController';
                window.mainTemplate = '/templates/dialogs/bitbucket.html?_=' + b;
                break;
            case 'createworklog':
                window.mainController = 'TimesheetController';
                window.mainTemplate = '/templates/main.html?_=' + b;
                params.createWorklog = true;
                params.isGadget = true;
                params.issueKey = issueKey;
                params.templates = {};
                break;
        }
    } else {
        window.mainController = 'TimesheetController';
        window.mainTemplate = '/templates/main.html?_=' + b;
        params.templates = {
            header: '/templates/header.html?_=' + b,
            menu: '/templates/menu.html?_=' + b,
            pivottable: '/templates/pivottable.html?_=' + b,
            piechart: '/templates/pieChart.html?_=' + b,
            timesheet: '/templates/timesheet.html?_=' + b,
            issuetimebalance: '/templates/issuetimebalance.html?_=' + b,
            issuetime: '/templates/issuetime.html?_=' + b,
            calendar: '/templates/calendar.html?_=' + b,
            editWorklog: '/templates/popups/editWorklog.html?_=' + b,
            subscribe: '/templates/popups/subscribe.html?_=' + b,
            gadgetConfig: '/templates/gadgetConfig.html?_=' + b,
            userpreferences: '/templates/dialogs/userpreferences.html?_=' + b
        }
    }
    window.i18nDefault = '/i18n/default.json?_=' + b;

    if (dialog) {
        AP.dialog.getButton('submit').disable();
    }
    $(function() {
        AP.requestBak = AP.request;
        var parentUrl;
        AP.getLocation(function (location) {
            parentUrl = location;
        });
        AP.request = function(options) {
            var successBak = options.success;
            var errorBak = options.error;
            options.success = function(responseText, error, xhr) {
                if (!responseText && xhr && xhr.status === 0) {
                    options.success = successBak;
                    options.error = errorBak;
                    setTimeout(function() {
                        AP.request(options);
                    }, 100);
                    if (window.loggerUrl) $.ajax({
                        type: "POST",
                        url: window.loggerUrl,
                        headers: {Authorization: "JWT " + window.atlToken},
                        contentType: "application/json",
                        data: angular.toJson({
                            url: parentUrl,
                            message: error,
                            type: "AP.request Error R14",
                            deployedDate: window.deployedDate
                        })
                    });
                } else {
                    successBak(responseText);
                }
            };
            options.error = function(xhr, type, error) {
                if (xhr && [429, 500, 502, 503, 504].indexOf(xhr.status) >= 0 && options.retryCount > 0) {
                    options.success = successBak;
                    options.error = errorBak;
                    options.retryCount--;
                    setTimeout(function() {
                        AP.request(options);
                    }, 100 * (10 - options.retryCount));
                } else if (errorBak) {
                    errorBak(xhr, type, error);
                }
            };
            if (!options.headers) {
                options.headers = {};
            }
            options.headers['x-atlassian-force-account-id'] = true;
            AP.requestBak(options);
        };
        $('body').css('background', '#fff');
        if (isGadget || dialog) {
            $('body').addClass('is-gadget');
        } else {
            $('body').addClass('is-not-gadget');
        }
        if (isWelcome) {
            $('body').addClass('is-welcome');
        }
        $('#content').html("<context items='" + JSON.stringify(params) + "' />");
        angular.bootstrap(document, ['timesheetApp']);
    });
});
