var TimesheetUtils = {};

TimesheetUtils.dateTimeFormat = 'YYYY-MM-DD HH:mm:ss';

TimesheetUtils.getLastMondayDate = function(reportingDay) {
    return TimesheetUtils.getPrevMonday(new Date(), reportingDay);
};

TimesheetUtils.getPrevMonday = function(date, reportingDay) {
    return TimesheetUtils.getNearestDayOfTheWeek(date, reportingDay, -1);
};

TimesheetUtils.getNextSunday = function(date, reportingDay) {
    return TimesheetUtils.getNearestDayOfTheWeek(date, (reportingDay + 6) % 7, 1);
};

TimesheetUtils.getNearestDayOfTheWeek = function(date, dayOfTheWeek, sign) {
    dayOfTheWeek = dayOfTheWeek || 0; // prevent occasional cycling
    while (date.getDay() != dayOfTheWeek) {
        date = TimesheetUtils.addDays(date, sign);
    }
    return TimesheetUtils.truncateTime(date);
};

TimesheetUtils.getFirstDayOfTheMonth = function(date) {
    if (date == null) {
        date = new Date();
    }
    return TimesheetUtils.truncateTime(moment(date).startOf('month').toDate());
};

TimesheetUtils.getLastDayOfTheMonth = function(date) {
    if (date == null) {
        date = new Date();
    }
    return TimesheetUtils.truncateTime(moment(date).endOf('month').toDate());
};

TimesheetUtils.truncateTime = function(date) {
    return new Date(date.getFullYear(), date.getMonth(), date.getDate());
};

TimesheetUtils.truncateMinutes = function(date) {
    return new Date(date.getFullYear(), date.getMonth(), date.getDate(), date.getHours());
};

// show work on the day it happened
// date is in server timezone, so convert it to worked user time zone
TimesheetUtils.convertDate = function(dateString, workTimeZone) {
    if (workTimeZone) {
        var workDate = moment.tz(dateString, workTimeZone).format().replace(/[+-]\d\d:?\d\d$/, '');
        var converted = moment(workDate).toDate();
        return converted;
    } else {
        return moment(dateString).toDate();
    }
};

// not used
TimesheetUtils.getTimezoneOffsetBak = function(dateString) {
    var date = moment(dateString).toDate();
    var time = date.getTime();
    if (TimesheetUtils.timezoneOffset == undefined) {
        TimesheetUtils.timezoneOffset = {};
    } else if (TimesheetUtils.timezoneOffset[time] !== undefined) {
        return TimesheetUtils.timezoneOffset[time];
    }
    // http://stackoverflow.com/a/5114625/922115
    function pad(number, length){
        var str = "" + number;
        while (str.length < length) {
            str = '0' + str;
        }
        return str
    }

    var offset = date.getTimezoneOffset();
    TimesheetUtils.timezoneOffset[time] = ((offset < 0 ? '+' : '-') + // Note the reversed sign!
              pad(parseInt(Math.abs(offset / 60)), 2) +
              pad(Math.abs(offset % 60), 2));
    return TimesheetUtils.timezoneOffset[time];
};

TimesheetUtils.addTime = function(date, diff, measure) {
    return moment(date).add(diff, measure).toDate();
};

TimesheetUtils.addDays = function(date, add) {
    return TimesheetUtils.addTime(date, add, 'days');
};

TimesheetUtils.addWeeks = function(date, add) {
    return TimesheetUtils.addTime(date, add, 'weeks');
};

TimesheetUtils.addMonths = function(date, add, isMonthView) {
    var m = moment(date);
    var endOfMonth = isMonthView && moment(m).endOf('month').isSame(m, 'day');
    m.add(add, 'months');
    if (endOfMonth) {
        m.endOf('month');
    }
    return m.toDate();
};

TimesheetUtils.addHours = function(date, add) {
    return TimesheetUtils.addTime(date, add, 'hours');
};

TimesheetUtils.sameDay = function(date1, date2) {
    return date1 && date2 && date1.getDate() == date2.getDate()
        && date1.getFullYear() == date2.getFullYear()
        && date1.getMonth() == date2.getMonth();
};

var isoDateRegex = /^\d{4}-[01]\d-[0-3]\dT[0-2]\d:[0-5]\d:[0-5]\d\.\d+(([+-][0-2]\d:?[0-5]\d)|Z)$/;

TimesheetUtils.isDate = function(value) {
    return typeof value == 'string' && value.match(isoDateRegex) != null;
};

TimesheetUtils.isAfterDate = function(date1, date2) {
    return date1.getTime()> date2.getTime();
};

var uuid = new RegExp(/[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i);
TimesheetUtils.isUuid = function(/*nullable*/ string) {
    return uuid.test(string);
};

TimesheetUtils.colspan = function(table) {
    if (table != null) {
        var rows = table.rows;
        var keys = Object.keys(rows);
        if (keys.length > 0) {
            var rowKey = rows[keys[0]].rowKey;
            if (rowKey.issue || rowKey.field) {
                return 5;
            }
        }
    }
    return 1;
};

TimesheetUtils.getWeekNumber = function(date) {
    return moment(date).week();
};

TimesheetUtils.getMonthKey = function(date) { // e.g.: 2015-12
    return moment(date).format('YYYY-MM');
};

TimesheetUtils.getNormalizedWeekNumber = function(date) {
    var week = TimesheetUtils.getWeekNumber(date);
    return TimesheetUtils.normalizeNumber(week);
};

TimesheetUtils.normalizeNumber = function(number){
    return (number < 10 ? '0' : '') + number;
};

TimesheetUtils.formatDate = function(date) {
    return moment(date).toJSON().replace("Z", "+0000");
};

TimesheetUtils.getHoursForDay = function(options) {
    var result = [];
    var startDate = options.startDate;
    var endDate = options.endDate;
    if (startDate.getTime() <= endDate.getTime()) {
        var now = new Date();
        while (startDate.getHours() != endDate.getHours()) {
            var isToday = startDate.getHours() == now.getHours();
            result.push({
                'date': startDate,
                'isToday': isToday
            });
            startDate = TimesheetUtils.addHours(startDate, 1);
        }
    }
    return result;
};

TimesheetUtils.isWeekend = function(date, configOptions) {
    var weekendType = configOptions && configOptions.weekendType ? configOptions.weekendType : '60'; // FIXME: for tests
    var isWeekend = weekendType.indexOf(date.getDay().toString()) != -1;
    return isWeekend;
};

TimesheetUtils.getWorkingDays = function(options, start, end) {
    var result = 0;
    var startDate = new Date(Math.max(moment(start), moment(options.startDate)));
    var endDate = new Date(Math.min(moment(end), moment(options.endDate)));
    if (startDate.getTime() <= endDate.getTime()) {
        endDate = TimesheetUtils.addDays(endDate, 1); // endDate should be in range
        while (!TimesheetUtils.sameDay(startDate, endDate)) {
            result += TimesheetUtils.isWeekend(startDate, options.configOptions) ? 0 : 1;
            startDate = TimesheetUtils.addDays(startDate, 1);
        }
    }
    return result;
};

TimesheetUtils.getDatesForWeek = function(options) {
    var result = [];
    var startDate = options.startDate;
    var endDate = options.endDate;
    if (startDate.getTime() <= endDate.getTime()) {
        endDate = TimesheetUtils.addDays(endDate, 1); // endDate should be in range
        while (!TimesheetUtils.sameDay(startDate, endDate)) {
            var isToday = TimesheetUtils.sameDay(startDate, TimesheetUtils.truncateTime(new Date()));
            var isWeekend = TimesheetUtils.isWeekend(startDate, options.configOptions);

            result.push({
                'date': startDate,
                'isToday': isToday,
                'isWeekend': isWeekend,
                'workingDays': isWeekend ? 0 : 1
            });
            startDate = TimesheetUtils.addDays(startDate, 1);
        }
    }
    return result;
};

TimesheetUtils.getWeeksForMonth = function(options) {
    var result = [];
    var startMonday = TimesheetUtils.getPrevMonday(options.startDate, options.reportingDay);
    var endSunday = TimesheetUtils.getNextSunday(options.endDate, options.reportingDay);
    var currentWeekNumber = TimesheetUtils.getNormalizedWeekNumber(new Date());
    var date = startMonday;
    while (date.getTime() <= endSunday.getTime()) {
        var nextDate = TimesheetUtils.addDays(date, 7);
        var wNum = TimesheetUtils.getNormalizedWeekNumber(date);
        result.push({
            'week': 'Week ' + wNum,
            'date': date,
            'isToday': wNum == currentWeekNumber,
            'workingDays': TimesheetUtils.getWorkingDays(options, date, TimesheetUtils.addDays(nextDate, -1))
        });
        date = nextDate;
    }
    if (result.length > 0) {
        result[0].firstDate = options.startDate;
        result[result.length - 1].lastDate = options.endDate;
    }
    return result;
};

TimesheetUtils.getMonthForYear = function(options) {
    var result = [];
    var firstDay = TimesheetUtils.getFirstDayOfTheMonth(options.startDate);
    var lastDay = TimesheetUtils.getLastDayOfTheMonth(options.endDate);
    var currentMonth = TimesheetUtils.getMonthKey(new Date());
    var date = firstDay;
    while (date.getTime() <= lastDay.getTime()) {
        var nextDate = TimesheetUtils.addMonths(date, 1);
        var monthNum = TimesheetUtils.getMonthKey(date);
        result.push({
            'month': moment.months()[monthNum],
            'date': date,
            'isToday': monthNum == currentMonth,
            'workingDays': TimesheetUtils.getWorkingDays(options, date, TimesheetUtils.addDays(nextDate, -1))
        });
        date = nextDate;
    }
    return result;
};

TimesheetUtils.changeDate = function(date, offset, nullable, isMonthView, reportingDay) {
    if (nullable && date == null) {
        return null;
    } else {
        if (date == null) {
            date = isMonthView ? TimesheetUtils.getFirstDayOfTheMonth() : TimesheetUtils.getLastMondayDate(reportingDay);
        }
        date = isMonthView ? TimesheetUtils.addMonths(date, offset, isMonthView) : TimesheetUtils.addDays(date, offset);
        return TimesheetUtils.formatDateForSearch(date);
    }
};

TimesheetUtils.formatDateForSearch = function(date) {
    return date.getFullYear() + "-" + TimesheetUtils.normalizeNumber(date.getMonth() + 1) + "-" + TimesheetUtils.normalizeNumber(date.getDate());
};

var schemaTypes = [
    'number', 'string', 'project', 'issuetype', 'securitylevel',
    'resolution', 'user', 'priority', 'status', 'datetime', 'date',
    'option', 'option-with-child', /* tempo */ 'account',
     'component', 'version', 'issuelinks', 'sd-customerorganization'
    /* TODO: 'watches', 'votes', 'timetracking', 'progress' */ ];

TimesheetUtils.filterFields = function(fields) {
    var result = [];
    for (var i = 0; i < fields.length; i++) {
        var field = fields[i];
        if (field.schema != null && (schemaTypes.indexOf(field.schema.type) >= 0
          || (field.schema.type == 'array'
            && schemaTypes.indexOf(field.schema.items) >= 0)
          || ['com.atlassian.jpo:jpo-custom-field-parent',
              'com.pyxis.greenhopper.jira:gh-epic-link',
              'com.pyxis.greenhopper.jira:gh-lexo-rank'].indexOf(field.schema.custom) >= 0)) {
            result.push(field);
        }
    }
    return result;
};

TimesheetUtils.sortByProperty = function(array, propertyName, desc, comparator) {
    var sign = !desc || -1;
    return array.sort(function(a, b) {
        if (comparator) {
            return sign * comparator(a[propertyName], b[propertyName]);
        } else if (typeof a[propertyName] == 'string') {
            return sign * a[propertyName].localeCompare(b[propertyName]);
        } else { // Number
            return sign * (a[propertyName] - b[propertyName]);
        }
    });
};

TimesheetUtils.getJson = function(data) {
    if (typeof data == 'string') {
        data = data != "" ? JSON.parse(data) : {};
    }
    return data;
};

TimesheetUtils.makeUISelectList = function(stringArray, addEmpty) {
    var makeSelectItemObject = function(name) {
        return {id: name, label: name}
    };

    var result = [];
    if (addEmpty) {
        result.push(makeSelectItemObject(''))
    }
    for (var i = 0; i < stringArray.length; i++) {
        result.push(makeSelectItemObject(stringArray[i]))
    }
    return result;
};

TimesheetUtils.convertHoursToFormat24 = function (hour, isPM) {
    if (isPM) {
        return (hour + 12) % 24;
    }
    return hour;
};

TimesheetUtils.convertHoursToFormat12 = function (hour) {
    if (hour > 12 || hour == 0) {
        return {hour: Math.abs(hour - 12), pm: true};
    }
    return {hour: hour, pm: false};
};

TimesheetUtils.getCurrentTimeMs = function() {
    var curDate = new Date();
    return (curDate.getHours()*60*60 + curDate.getMinutes()*60) * 1000;
};

// see also timespent directive in modules/directives.js
TimesheetUtils.getTimeSpentMs = function(timeSpent, config) {
    if (!timeSpent) {
        return 0;
    }
    var getMs = function(value) {
        var unit = config.defaultUnit;
        var quantity = 0;
        if (isNaN(value)) {
            var parsed = value.match(/(\d*\.?\d+)([a-zA-Z]*)/);
            if (parsed) {
                quantity = parseFloat(parsed[1]);
                unit = parsed[2];
            }
        } else {
            quantity = parseFloat(value);
        }
        var sec = 1000;
        var min = 60 * sec;
        var hour = 60 * min;
        var day = config.workingHoursPerDay * hour;
        var week = config.workingDaysPerWeek * day;
        switch (unit) {
            case 'w':
            case 'week':
                return quantity * week;
            case 'd':
            case 'day':
                return quantity * day;
            case 'h':
            case 'hour':
                return quantity * hour;
            case 'm':
            case 'minute':
                return quantity * min;
            case 's':
                return quantity * sec;
        }
    };
    var blocks = timeSpent.split(" ");
    var result = 0;
    for (var i = 0; i < blocks.length; i++) {
        result += getMs(blocks[i]);
    }
    return result;
};

TimesheetUtils.isLongerOneDay = function(timeSpent, config) {
    if (!timeSpent || timeSpent.match(/[wd]/)) {
        return false;
    }
    var getSec = function(value) {
        var unit = config.defaultUnit;
        var quantity = 0;
        if (isNaN(value)) {
            var parsed = value.match(/(\d*\.?\d+)([a-zA-Z]*)/);
            if (parsed) {
                quantity = parseFloat(parsed[1]);
                unit = parsed[2];
            }
        } else {
            quantity = parseFloat(value);
        }
        switch (unit) {
            case 'h':
            case 'hour':
                return quantity * 60;
            case 'm':
            case 'minute':
                return quantity;
        }
    };
    var blocks = timeSpent.split(" ");
    var result = 0;
    for (var i = 0; i < blocks.length; i++) {
        result += getSec(blocks[i]);
    }
    return result > config.workingHoursPerDay * 60;
};

TimesheetUtils.getTimeSpents = function(timeSpent, config) {
    if (!timeSpent) {
        return [];
    }
    var getDays = function(value, timespents) {
        var unit = config.defaultUnit;
        var quantity = 0;
        if (isNaN(value)) {
            var parsed = value.match(/(\d*\.?\d+)([a-zA-Z]*)/);
            if (parsed) {
                quantity = parseFloat(parsed[1]);
                unit = parsed[2];
            }
        } else {
            quantity = parseFloat(value);
        }
        var hour = 60;
        var week = config.workingDaysPerWeek;
        switch (unit) {
            case 'w':
            case 'week':
                timespents.days += quantity * week;
                return;
            case 'd':
            case 'day':
                timespents.days += quantity;
                return;
            case 'h':
            case 'hour':
                timespents.mins += quantity * hour;
                return;
            case 'm':
            case 'minute':
                timespents.mins += quantity;
                return;
        }
    };
    var blocks = timeSpent.split(" ");
    var timespents = {days: 0, mins: 0};
    for (var i = 0; i < blocks.length; i++) {
        getDays(blocks[i], timespents);
    }
    var result = [];
    while (timespents.days > 0) {
        var min = Math.min(timespents.days, 1);
        result.unshift(min + 'd');
        timespents.days -= min;
    }
    if (timespents.mins) {
        result.unshift(timespents.mins + 'm');
    }
    return result;
};

TimesheetUtils.getPredefinedJql = function (externalFilterId) {
    switch (externalFilterId) {
        case 0: // Active sprints issues
            return 'sprint in openSprints()'; // includes subtasks
        case -1: // My open issues
            return 'assignee = currentUser() and resolution = Unresolved';
        case -2: // Reported by me
            return 'reporter = currentUser()';
        case -3: // Viewed recently
            return 'issuekey in issueHistory()';
        case -4: // All issues
            return '';
        case -5: // Open issues
            return 'resolution = Unresolved';
        case -6: // Created recently
            return 'created >= -1w';
        case -7: // Resolved recently
            return 'resolutiondate >= -1w';
        case -8: // Updated recently
            return 'updated >= -1w';
        case -9: // Done issues
            return 'statusCategory = Done';
        default:
            AP.flag.create({
                title: 'Unsupported predefined filter',
                body: 'Filter ' + externalFilterId + ' is not known',
                type: 'error'
            });
            return;
    }
};

TimesheetUtils.getSearchQueryBuilder = function(options) {

    var getFilterOrProjectValue = function(options, startString) {
        var result = null;
        if (options.filterOrProjectId) {
            var items = Array.isArray(options.filterOrProjectId)
                ? options.filterOrProjectId
                : options.filterOrProjectId.split(',');
            result = [];
            for (var i = 0; i < items.length; i++) {
                if (items[i].indexOf(startString) == 0) {
                    result.push(items[i].split(startString)[1]);
                }
            }
        }
        return result;
    };
    var getOrQueryBuilder = function(paramName, items) {
        var qb = new TimesheetUtils.queryBuilder(" or ");
        for (var i = 0; i < items.length; i++) {
            qb.addQuery(paramName, '"' + items[i] + '"');
        }
        return qb;
    };
    var filterIds = getFilterOrProjectValue(options, 'filter_');
    var projectKeys = getFilterOrProjectValue(options, 'project_');
    var hasJQL = typeof options.jql == 'string'; // empty JQL for all issues
    var jqlIds = getFilterOrProjectValue(options, 'jql_');
    if (!filterIds && !projectKeys && !hasJQL && !jqlIds && options.projectKey) {
        projectKeys = [options.projectKey];
    }

    // performance optimization hack, see pivottableService.checkIfMatches()
    options.projectKeys = projectKeys;
    options.filterIds = filterIds || jqlIds;

    var jqlQueryBuilder = new TimesheetUtils.queryBuilder(" and ");
    if (options.jql) {
        jqlQueryBuilder.addStatement('(' + options.jql + ')');
    }
    if (filterIds && filterIds.length > 0) {
        // https://answers.atlassian.com/questions/22264/can-filters-be-used-with-the-rest-api
        if (filterIds.length == 1) {
            jqlQueryBuilder.addQuery('filter', filterIds[0]);
        } else {
            jqlQueryBuilder.addQueryBuilder(getOrQueryBuilder('filter', filterIds));
        }
    }
    if (projectKeys && projectKeys.length > 0) {
        // https://answers.atlassian.com/questions/155180/filter-issue-by-project-key
        if (projectKeys.length == 1) {
            jqlQueryBuilder.addQuery('project', '"' + projectKeys[0] + '"');
        } else {
            jqlQueryBuilder.addQueryBuilder(getOrQueryBuilder('project', projectKeys));
        }
    }
    if (jqlIds && jqlIds.length > 0) {
        var qb = new TimesheetUtils.queryBuilder(" or ");
        jqlIds.map(function (jqlId) {
            return TimesheetUtils.getPredefinedJql(-jqlId);
        }).filter(Boolean).forEach(function (jql) {
            qb.addStatement('(' + jql + ')');
        });
        if (!qb.isEmpty()) {
            jqlQueryBuilder.addQueryBuilder(qb);
        }
    }
    return jqlQueryBuilder;
};

TimesheetUtils.queryBuilder = function(delimiter) {
    var items = [];
    delimiter = delimiter || "&";

    this.addStatement = function(statement) {
        items.push(statement);
        return this;
    };

    this.addQuery = function(param, value, delimiter) {
        delimiter = delimiter || "=";
        if (value != null && value != "") {
            this.addStatement(param + delimiter + value);
        }
        return this;
    };

    this.addQueryWithFunc = function(param, value, delimiter, queryFunc) {
        this.addQuery(param, queryFunc + "(" + value + ")", delimiter);
    };

    this.addQueryBuilder = function(queryBuilder) {
        this.addStatement("(" + queryBuilder.build() + ")");
    };

    this.build = function() {
        return this.isEmpty()
            ? ""
            : items.join(delimiter);
    };

    this.isEmpty = function() {
        return items == null || items.length == 0;
    };
};

TimesheetUtils.closeDialogFunctions = {
    submit: function() {
        AP.Dialog.close('submit');
    },
    cancel: function() {
        AP.Dialog.close('cancel');
    }
};

TimesheetUtils.normalizeObject = function(obj, removeEmpty) {
    return TimesheetUtils.doWithObject(obj, function(key, value) {
        if (value == null || (removeEmpty && (value === "" || value === [] || value === false))) { // preserve reportingDay=0
            delete obj[key];
        }
    });
};

TimesheetUtils.unwrapBoolObjects = function(obj) {
    return TimesheetUtils.doWithObject(obj, function(key, value) {
        if (angular.isObject(value) && Object.keys(value).length == 1 && value.hasOwnProperty("enabled")) {
            obj[key] = value.enabled;
        }
    });
};

TimesheetUtils.doWithObject = function(obj, callback) {
    for (var key in obj) {
        callback(key, obj[key]);
    }
    return obj;
};

TimesheetUtils.getPerformance = function() {
    var performance = typeof window != 'undefined' && window.performance || Date;
    if (!performance.now) {
      performance.now = Date.now;
    }
    return performance;
};

TimesheetUtils.parseParams = function(paramsString) {
    var _convertParamValue = function(value) {
        if (["true", "false"].indexOf(value) != -1) {
            return value == "true";
        } else if ($.isNumeric(value)) {
            return parseFloat(value);
        }
        return decodeURIComponent(value.replace(/\+/g, ' '));
    };
    var result = {};
    var paramParts = paramsString.split("&");
    if (paramParts) {
        for (var i = 0; i < paramParts.length; i++) {
            if (paramParts[i]) {
                var parts = paramParts[i].split("=");
                if (parts.length == 2) {
                    var paramKey = parts[0];
                    var paramValue = _convertParamValue(parts[1]);
                    if (Object.keys(result).indexOf(paramKey) != -1) {
                        if (Array.isArray(result[paramKey])) {
                            result[paramKey].push(paramValue);
                        } else {
                            var tmp = result[paramKey];
                            result[paramKey] = [tmp, paramValue];
                        }
                    } else {
                        result[paramKey] = paramValue;
                    }
                }
            }
        }
    }
    return result;
};

TimesheetUtils.wrapIntoArray = function(value) {
    return value && !Array.isArray(value)
        ? [value] : value;
};

/**
 * @description determine if an array contains one or more items from another array.
 * @param {array} haystack the array to search.
 * @param {array} arr the array providing items to check for in the haystack.
 * @return {boolean} true|false if haystack contains at least one item from arr.
 * http://stackoverflow.com/a/25926600/922115
 */
TimesheetUtils.findOne = function(haystack, arr) {
    return arr.some(function (v) {
        return haystack.indexOf(v) >= 0;
    });
};

TimesheetUtils.indexByProperty = function(property, value, array) {
    for (var i = 0; i < array.length; i++) {
        if (value == array[i][property]) {
            return i;
        }
    }
    return -1;
};

TimesheetUtils.findByProperty = function(property, value, array) {
  for (var i = 0; i < array.length; i++) {
      if (value == array[i][property]) {
          return array[i];
      }
  }
  return null;
};

TimesheetUtils.findByCondition = function(conditionCallback, array) {
  for (var i = 0; i < array.length; i++) {
      if (conditionCallback(array[i])) {
          return array[i];
      }
  }
  return null;
};

TimesheetUtils.replaceAll = function(string, toBeReplaced, replacement) {
    return string.split(toBeReplaced).join(replacement);
};

TimesheetUtils.repeat = function(str, count) {
    if (String.prototype.repeat) {
        return str.repeat(count);
    } else {
        var rpt = '';
        for (var i = 0; i < count; i++) {
          rpt += str;
        }
        return rpt;
    }
};

// no Object.values in IE
TimesheetUtils.objectValues = function(obj) {
    return Object.keys(obj).map(function (key) {
        return obj[key]; // has? isEnumerable?
    });
};

TimesheetUtils.uniqueArray = function(array) {
    var a = array.concat();
    for(var i = 0; i < a.length; i++) {
        for(var j = i + 1; j < a.length; j++) {
            if(a[i] === a[j]) {
                a.splice(j--, 1);
            }
        }
    }
    return a;
};

TimesheetUtils.getTags = function(value) {
    var result = [];
    if (value) {
        var items = (" " + value).match(/\s#\S*/g);
        if (items && items.length > 0) {
            items.forEach(function (object) {
                result.push(object.substring(2));
            });
        }
    }
    return result;
};

TimesheetUtils.screenshot = function (cb) {
    try {
        html2canvas(document.body, {
            onrendered: function(canvas) {
                try {
                    if (canvas.msToBlob) {// IE
                        cb(canvas.msToBlob());
                    } else {
                        canvas.toBlob(cb, 'image/png');
                    }
                } catch (e) {
                    cb(null, e);
                }
            }
        });
    } catch (e) {
        cb(null, e);
    }
};

TimesheetUtils.isCurrentWeek = function (startDate, endDate) {
    if (!startDate || !endDate) {
        return false;
    }
    var now = moment();
    var start = moment(startDate);
    var end = moment(endDate);
    var add8Days = moment(startDate).add(8, 'days');
    return (now.isBetween(start, end, 'days') || now.isSame(start, 'days') || now.isSame(end, 'days')) && add8Days.isAfter(end, 'days');
};

TimesheetUtils.getOrCreate = function(dictionary, key, recordClass, init) {
    var record = dictionary[key.keyValue];
    if (record == undefined) {
        record = new recordClass(key);
        if (init) init.apply(record);
        dictionary[key.keyValue] = record;
    }
    return record;
};

TimesheetUtils.clone = function(obj) {
    if (Array.isArray(obj)) {
        return TimesheetUtils.cloneArray(obj);
    } else if (obj != null && typeof obj === "object") {
        return TimesheetUtils.cloneObject(obj);
    } else {
        return obj;
    }
};

TimesheetUtils.cloneObject = function cloneObject(obj) {
    if ([Boolean, Number, String, Date/*, Symbol*/].indexOf(obj.constructor) < 0) {
        var clone = Object.create(Object.getPrototypeOf(obj)); // instead of deprecated __proto__
        Object.keys(obj).forEach(function (i) {
            clone[i] = TimesheetUtils.clone(obj[i]);
        });
        return clone;
    } else {
        return new obj.constructor(obj.valueOf());
    }
};

TimesheetUtils.cloneArray = function(arr) {
    var clone = [];
    for (var i = 0; i < arr.length; i++) {
        clone.push(TimesheetUtils.clone(arr[i]));
    }
    return clone;
};

