TimesheetUtils.validateParams = function (params) {
    var errParams = [];
    if (params.pivotTableType && Object.keys(PivotTableType).indexOf(params.pivotTableType) == -1) {
        errParams.push('Unknown PivotTableType: ' + params.pivotTableType);
    }
    if (params.filterOrProjectId) {
        var filterOrProjects = TimesheetUtils.wrapIntoArray(params.filterOrProjectId);
        for (var i = 0; i < filterOrProjects.length; i++) {
            if (filterOrProjects[i].indexOf('project_') != 0 && filterOrProjects[i].indexOf('filter_') != 0) {
                errParams.push('Unknown filterOrProjectId: ' + filterOrProjects[i]);
            }
        }
    }
    if (params.numOfWeeks && parseInt(Number(params.numOfWeeks)) != params.numOfWeeks) {
        errParams.push('numOfWeeks should be Integer');
    }
    if (params.reportingDay && (parseInt(Number(params.reportingDay)) != params.reportingDay || params.reportingDay > 6 || params.reportingDay < -1)) {
        errParams.push('reportingDay should be -1..6');
    }
    if (params.offset && parseInt(Number(params.offset)) != params.offset) {
        errParams.push('offset should be Integer');
    }
    var startDate = params.startDate || params.start;
    if (startDate && (!/^\d{4}-\d{2}-\d{2}$/.test(startDate) || isNaN(Date.parse(startDate)))) {
        errParams.push('startDate should be date in YYYY-MM-DD format');
    }
    var endDate = params.endDate || params.end;
    if (endDate && (!/^\d{4}-\d{2}-\d{2}$/.test(endDate) || isNaN(Date.parse(endDate)))) {
        errParams.push('endDate should be date in YYYY-MM-DD format');
    }
    if (params.sum && ['day', 'week', 'month'].indexOf(params.sum) == -1) {
        errParams.push('sum should be in ["day", "week", "month"]');
    }
    if (params.view && ['day', 'week', 'month'].indexOf(params.view) == -1) {
        errParams.push('view should be in ["day", "week", "month"]');
    }
    return errParams.length ? errParams : null;
};

TimesheetUtils.clearObject = function (obj) {
    var keys = Object.keys(obj);
    for (var i = 0; i < keys.length; i++) {
        delete obj[keys[i]];
    }
};

